import MainPage from './MainPage'
import Blank from './Blank'
import profileRoutes from '@/modules/profile/profile-routes'
import reports from './content/reports/reports.routes'
import ordersRoutes from '@/modules/orders/orders-routes'
import stockRoutes from '@/modules/stock/stock-routes'
import promotionsRoutes from '@/modules/promotions/promotions-routes'
import benchmarkingRoutes from '@/modules/benchmarking/benchmarking-routes'
import Store from '@/modules/store'
import { IS_AUTHENTICATED_GETTER } from '@/modules/authentication'

const blank = {
  path: '',
  name: 'blank',
  component: Blank
}

export default {
  path: '',
  component: MainPage,
  children: [
    ...ordersRoutes,
    ...stockRoutes,
    ...promotionsRoutes,
    ...profileRoutes,
    reports,
    benchmarkingRoutes,
    blank
  ],
  beforeEnter: (to, from, next) => {
    const isAuthenticated =
      Store.store.getters[`authentication/${IS_AUTHENTICATED_GETTER}`]
    if (isAuthenticated) {
      next()
    } else {
      next({ name: 'hello' })
    }
  }
}
