import { AbstractFetchService } from '@/modules/http'
import { StoreId } from '@/modules/store/data/resources'
import { host } from '@/api/localConfig'
import { StoreProductsListDTO } from './dtos'

export interface GetProductsPayload {
  storeId: StoreId
}

export const storesApiFactory = (fetch: AbstractFetchService) => {
  return {
    getStoreProducts: ({ storeId }: GetProductsPayload) => {
      return fetch.get<StoreProductsListDTO>(
        `${host}/stores/${storeId}/products`
      )
    }
  }
}
