<template>
  <div class="quantity-selector">
    <div
      v-show="edit"
      class="quantity-selector_action"
      data-qa="quantity-selector-subtract"
      :class="{ 'quantity-selector_action_disabled': quantity < 1 }"
      @click="$emit('subtract')"
    >
      <icon name="minus"></icon>
    </div>
    <div class="quantity-selector_content">
      <slot name="counter" :quantity="quantity" :initial="initial">
        <div class="quantity-selector_quantity">
          {{ $tc('order.products.count', quantity, { size: quantity }) }}
        </div>
        <div v-if="initial !== quantity" class="quantity-selector_initial">
          {{ $tc('order.products.count', initial, { size: initial }) }}
        </div>
      </slot>
    </div>
    <div
      v-show="edit"
      data-qa="quantity-selector-add"
      class="quantity-selector_action"
      @click="$emit('add')"
    >
      <icon name="plus"></icon>
    </div>
  </div>
</template>
<script>
import Icon from 'common/Icon'
export default {
  name: 'QuantitySelector',
  components: {
    Icon
  },
  props: {
    quantity: {
      type: Number,
      required: true
    },
    initial: {
      type: Number,
      required: true
    },
    edit: {
      type: Boolean
    }
  },
  data() {
    return {}
  }
}
</script>
