import Component from 'vue-class-component'
import Vue from 'vue'
import { Action } from 'vuex-class'
import { Prop } from 'vue-property-decorator'
import { ToBeMigrated } from 'types/any'
import { OrderId } from '@/modules/store'
import { StoreAddressId } from '@/modules/store/data/resources'
import GlovoBtn from 'common/GlovoBtn.vue'

@Component({
  name: 'ReportCourierPopup',
  components: {
    GlovoBtn
  }
})
export default class ReportCourierPopup extends Vue {
  @Prop() orderId!: OrderId
  @Prop() storeAddressId!: StoreAddressId

  overlayVisible = false
  courierReportIssues: Array<string> = []
  courierReportIssueOther = ''
  isReportIssueSubmitDisabled = true

  @Action('reportCourier') reportCourier: ToBeMigrated

  get courierReportingIssueList() {
    return [
      {
        issue: 'IMPOLITE_GLOVER',
        message: this.$t('order.courier.reporting.issue1')
      },
      {
        issue: 'GLOVER_DIDNT_LEAVE_AT_TIME',
        message: this.$t('order.courier.reporting.issue2')
      },
      {
        issue: 'GLOVER_DIDNT_TAKE_PICTURE_BEFORE_PICKUP',
        message: this.$t('order.courier.reporting.issue3')
      },
      {
        issue: 'MISSING_GLOVER_MATERIAL',
        message: this.$t('order.courier.reporting.issue4')
      }
    ]
  }

  get isOtherCheckboxChecked() {
    return this.courierReportIssues.includes('OTHER')
  }

  get hasNoOptionChecked() {
    return this.courierReportIssues.length === 0
  }

  handleIssueReportChange() {
    if (this.courierReportIssues.length === 0) {
      this.isReportIssueSubmitDisabled = true
      return
    }
    this.isReportIssueSubmitDisabled = false
  }

  submitCourierReport() {
    if (this.courierReportIssues.length < 1) {
      return
    }
    const data = {
      issues: this.courierReportIssues,
      comment: this.courierReportIssues.includes('OTHER')
        ? this.courierReportIssueOther
        : null
    }
    const orderId = this.orderId
    const storeAddressId = this.storeAddressId
    this.reportCourier({ storeAddressId, orderId, data }).then(() => {
      this.$emit('close')
    })
  }

  handleOtherIssueUpdate() {
    if (
      this.courierReportIssueOther !== '' &&
      !this.courierReportIssues.includes('OTHER')
    ) {
      this.courierReportIssues.push('OTHER')
    } else if (
      this.courierReportIssueOther === '' &&
      this.courierReportIssues.includes('OTHER')
    ) {
      this.courierReportIssues.splice(
        this.courierReportIssues.indexOf('OTHER'),
        1
      )
    }
  }
}
