
import Component from 'vue-class-component'
import Vue from 'vue'
import { Prop } from 'vue-property-decorator'

@Component({ name: 'OrderDetailsCutlery' })
export default class OrderDetailsCutlery extends Vue {
  @Prop() isCutleryRequested!: boolean

  get cutleryMessage(): string {
    return this.isCutleryRequested
      ? 'cutlery.request.yes'
      : 'cutlery.request.no'
  }

  get setCutleryIcon(): string {
    return this.isCutleryRequested
      ? require('@/assets/images/cutlery-yes.svg')
      : require('@/assets/images/cutlery-no.svg')
  }
}
