
import Vue from 'vue'
import ModalWrapper from 'common/ModalWrapper.vue'
import CollectionsList from 'common/CollectionsList.vue'
import Icon from 'common/Icon.vue'
import cloneDeep from 'lodash/cloneDeep'
import Component from 'vue-class-component'
import { Prop } from 'vue-property-decorator'
import { Loading } from 'element-ui'
import {
  OrderProductItem,
  PersoProduct,
  productHelper
} from '@/modules/orders/components/details/product'
import { OrderDetails } from '@/modules/store/data/resources/order-details'
import {
  MenuCollection,
  MenuSection,
  Product
} from '@/modules/store/data/resources'
import { Action, Getter } from 'vuex-class'
import OrderProduct from '@/models/OrderProduct'
import { REPLACE_PRODUCT, ReplaceProductPayload } from '@/modules/orders/store'
import { StoreAction } from '@/modules/store/types/store'
import { orderManagement } from '@/modules/orders/store/namespace'

@Component({
  name: 'replace-product',
  components: {
    Icon,
    ModalWrapper,
    CollectionsList,
    PersoProduct,
    OrderProductItem
  }
})
export default class ReplaceProduct extends Vue {
  @Getter content!: object
  @Getter isSingleCollection!: boolean
  @Getter storeAddressCollections!: Record<number, any>

  @orderManagement.Action(REPLACE_PRODUCT) replaceProduct!: StoreAction<
    ReplaceProductPayload
  >
  @Action getContent!: (id: number) => void

  screenType = 'default'
  currentCollectionId = 0
  persoProduct: Record<string, any> | boolean = false
  SCREENS = {
    DEFAULT: 'default',
    PERSO: 'perso',
    COLLECTIONS: 'collections'
  }

  @Prop() productToReplace!: OrderProduct
  @Prop() order!: OrderDetails

  async mounted() {
    this.getStoreAddressMenuContent()
  }

  get storeAddressId(): number {
    return this.order.storeDetails.storeAddressId
  }

  get hasMultipleSections(): boolean {
    return this.replacementCollection.sections!.length > 1
  }

  get hasValidReplacementSections(): boolean {
    return (
      !!this.storeAddressCollections[this.currentCollectionId] &&
      !!this.storeAddressCollections[this.currentCollectionId].sections
    )
  }

  get replacementCollection(): MenuCollection {
    if (!this.hasValidReplacementSections) {
      return {
        id: -1,
        name: '',
        sections: []
      }
    }

    const myReplacements = this.storeAddressCollections[
      this.currentCollectionId
    ]
    if (myReplacements.sections.length === 0) {
      return myReplacements
    }
    return this.sortSections(myReplacements, this.productToReplace.productId)
  }

  selectCollection(collectionId: number): void {
    this.currentCollectionId = collectionId
    this.screenType = this.SCREENS.DEFAULT
  }

  enabledProducts(products: Product[]) {
    return products.filter(product => product.enabled)
  }

  getCollectionIdByProductId(productId: number): number {
    const collection = Object.values(
      this.storeAddressCollections
    ).find(collection =>
      collection.sections.some((section: MenuSection) =>
        section.products.some(product => product.id === productId)
      )
    )
    if (collection && collection.id) {
      return collection.id
    } else {
      return +Object.keys(this.storeAddressCollections)[0]
    }
  }

  sortSections(
    replacements: MenuCollection,
    productIdToPrioritize: number
  ): MenuCollection {
    // Cycle through sections and find the section where the replacement product belongs to
    const index = replacements.sections.findIndex(section =>
      section.products.some(product => product.id === productIdToPrioritize)
    )

    if (index < 1) {
      return replacements
    }

    // Move section of the product replacement to the beginning of collection
    const sorted = cloneDeep(replacements)
    const section = sorted.sections.splice(index, 1)
    sorted.sections.unshift(section[0])
    return sorted
  }

  async getStoreAddressMenuContent(): Promise<void> {
    const loadingInstance = Loading.service({ fullscreen: true })
    await this.getContent(this.storeAddressId)
    this.currentCollectionId = this.getCollectionIdByProductId(
      this.productToReplace.productId
    )
    loadingInstance.close()
  }

  back(): void {
    if (
      this.screenType === this.SCREENS.COLLECTIONS ||
      this.screenType === this.SCREENS.PERSO
    ) {
      this.screenType = this.SCREENS.DEFAULT
    } else {
      this.$emit('close')
    }
  }

  async checkForCustomizations(prod: OrderProduct): Promise<void> {
    if (prod.attributeGroups && prod.attributeGroups.length > 0) {
      this.persoProduct = productHelper.parseAndCloneProduct(
        prod,
        this.productToReplace
      )
      this.screenType = this.SCREENS.PERSO
    } else {
      await this.picked(prod)
    }
  }

  async picked(replacement: OrderProduct): Promise<void> {
    this.screenType = this.SCREENS.DEFAULT
    replacement.quantity = this.productToReplace.quantity
    const data = {
      order: this.order,
      replacedProductId: this.productToReplace.initialId(),
      productToReplaceWith: replacement
    }
    try {
      await this.replaceProduct(data)
    } catch (error) {
      this.$emit('error', error.response)
    } finally {
      this.$emit('close')
    }
  }
}
