import { Store } from 'vuex'
import { State } from '@/modules/store'
import { RequestInterceptor } from '@/modules/http'
import { JWTHelper } from '@/modules/authentication'
import {
  getExpirationTime,
  isRefreshCall,
  isRefreshInProgress,
  refreshToken
} from '@/modules/authentication/interceptors/helpers'

export function newTokenRefreshInterceptor(
  store: Store<State>
): RequestInterceptor {
  return requestConfig => {
    const isNotAlreadyRefreshingToken =
      !isRefreshCall(requestConfig.url) && !isRefreshInProgress(store)

    if (hasTokenExpired(store) && isNotAlreadyRefreshingToken) {
      if (requestConfig.cancelToken) {
        requestConfig.cancelToken.cancel(
          'Request cancelled as token has expired'
        )
      }
      refreshToken(store) // no need to await the response, we don't want to queue requests
    } else if (isTokenAboutToExpire(store) && isNotAlreadyRefreshingToken) {
      refreshToken(store) // no need to await the response, we don't want to queue requests
    }
    return requestConfig
  }
}

function isTokenAboutToExpire(store: Store<State>) {
  const expirationTime = getExpirationTime(store)
  return (
    expirationTime !== null && JWTHelper.tokenIsGoingToExpire(expirationTime)
  )
}

function hasTokenExpired(store: Store<State>) {
  const expirationTime = getExpirationTime(store)
  return expirationTime !== null && JWTHelper.hasTokenExpired(expirationTime)
}
