
import Vue from 'vue'
import Component from 'vue-class-component'
import { Action, Getter } from 'vuex-class'
import { Prop, Watch } from 'vue-property-decorator'
import OrderProductEditModal from './product/edit/OrderProductEditModal.vue'
import OrderProductQuantityModal from './product/quantity/OrderProductQuantityModal.vue'
import OrderProductAttributesModal from './product/attributes/OrderProductAttributesModal.vue'
import ReplaceProduct from '@/components/main/popups/ReplaceProduct.vue'
import ReplaceProductNew from './product/replace/ReplaceProduct.vue'
import AddNewProduct from './product/add-new/AddNewProduct.vue'
import RemoveProduct from './product/remove/RemoveProduct.vue'
import OrderProductItem from './product/OrderProductItem.vue'
import ReportCourierPopup from '@/components/common/ReportCourierPopup.vue'
import GlovoBtn from '@/components/common/GlovoBtn.vue'
import Order from 'models/Order'
import Icon from 'common/Icon.vue'
import Placeholder from 'common/Placeholder.vue'
import LoadingOrderDetails from './LoadingOrderDetails.vue'
import ErrorOrderDetails from './ErrorOrderDetails.vue'
import OrderDetailsMap from './OrderDetailsMap.vue'
import OrderDetailsPrinter from './OrderDetailsPrinter.vue'
import OrderDetailsCourier from './OrderDetailsCourier.vue'
import MarketplaceOrderBreakdownSummary from './breakdown-summary/MarketplaceOrderBreakdownSummary.vue'
import NormalOrderBreakdownSummary from './breakdown-summary/NormalOrderBreakdownSummary.vue'
import OrderDetailsDescription from './description/new/OrderDetailsDescription.vue'
import NewOrderProductItem from './product/NewOrderProductItem.vue'
import { DISPLAY_ORDER_DETAILS } from '@/modules/store/services/display-types'
import { OrderId } from '@/modules/store'
import {
  OrderDetails as OrderDetailsModel,
  ProductDetails
} from '@/modules/store/data/resources/order-details'
import {
  ProductId,
  StoreAddressId,
  StoreAdmin
} from '@/modules/store/data/resources'
import { GET_CURRENT_STORE_ADMIN_GETTER } from '@/modules/authentication'
import { NetworkError } from '@/modules/store/data/resources/network-error'
import User from 'models/User'
import OrderProduct from 'models/OrderProduct'
import {
  GET_ORDER_DETAILS,
  GetOrderDetailsPayload,
  ACCEPT_ORDER,
  AcceptOrderPayload,
  EDIT_PRODUCT,
  EditProductPayload,
  ASSIGN_KITCHEN_CODE,
  AssignKitchenCodePayload,
  REVERT_PRODUCT,
  RevertProductPayload,
  CLEAR_ORDER_DETAILS_UI_STATE,
  GET_ORDER_DETAILS_SUCCESS,
  GetOrderDetailsSuccessPayload,
  GET_ORDER_DETAILS_FAILURE,
  GetOrderDetailsFailurePayload,
  UPDATE_ORDER_DETAILS_LOADING,
  UpdateOrderDetailsLoadingPayload,
  OPEN_ORDER_PRODUCT_QUANTITY_MODAL,
  OpenOrderProductQuantityModalPayload,
  OPEN_ORDER_PRODUCT_ATTRIBUTES_MODAL,
  OpenOrderProductAttributesModalPayload
} from '@/modules/orders/store'
import { authentication } from '@/modules/authentication/store/namespace'
import { StoreAction } from '@/modules/store/types/store'
import GlovoTicket from '@/services/ticket/GlovoTicket'
import {
  GET_IS_ORDER_DETAILS_LOADING_GETTER,
  GET_ORDER_DETAILS_GETTER,
  GET_IS_GROCERIES_STORE_ADDRESS_GETTER,
  GET_IS_KITCHEN_CODE_STORE_ADDRESS_GETTER,
  GET_IS_ORDER_DETAILS_LOADING_FAILURE_GETTER
} from '@/modules/orders/store/getters'
import { orderManagement } from '@/modules/orders/store/namespace'
import { ToBeMigrated } from 'types/any'
import OrderDetailsInformation from './information/OrderDetailsInformation.vue'
import { GET_TOGGLES_GETTER } from '@/modules/feature-toggles'
import { FeatureToggle } from '@/modules/feature-toggles/store/state'
import telemetry from '@/modules/telemetry/telemetry'
import { PollingService } from '@/modules/shared'
import { FIFTEEN_SECONDS_IN_MILLIS } from '@/modules/core/helpers'
import { HttpModule } from '@/modules/http'
import ordersApiFactory from '@/api/orders'
import { Subscription } from 'rxjs'
import Timeout = NodeJS.Timeout
import { GET_ORDERS_GETTER } from '@/modules/store/data/getters'
import { OrderTypes } from '@/modules/store/data/state'

const POLLING_INTERVAL = FIFTEEN_SECONDS_IN_MILLIS

@Component({
  name: 'OrderDetails',
  components: {
    OrderDetailsInformation,
    OrderDetailsMap,
    OrderDetailsDescription,
    OrderDetailsPrinter,
    OrderDetailsCourier,
    MarketplaceOrderBreakdownSummary,
    NormalOrderBreakdownSummary,
    OrderProductEditModal,
    OrderProductQuantityModal,
    ReplaceProductNew,
    AddNewProduct,
    OrderProductAttributesModal,
    ReplaceProduct,
    RemoveProduct,
    OrderProductItem,
    NewOrderProductItem,
    ReportCourierPopup,
    Icon,
    Placeholder,
    LoadingOrderDetails,
    ErrorOrderDetails,
    GlovoBtn
  }
})
export default class OrderDetails extends Vue {
  @Prop()
  modalMode!: boolean

  @Getter replaceModalDisplayed?: boolean
  @Getter addNewProductModalDisplayed?: boolean
  @Getter ticketCopies!: number
  @Getter(GET_TOGGLES_GETTER) toggles!: Record<FeatureToggle, boolean>

  @authentication.Getter(GET_CURRENT_STORE_ADMIN_GETTER)
  storeAdmin!: StoreAdmin | null

  @orderManagement.Getter(GET_ORDER_DETAILS_GETTER)
  orderDetails!: OrderDetailsModel

  @Getter(GET_ORDERS_GETTER)
  allOrders!: Record<OrderId, OrderTypes>

  @orderManagement.Getter(GET_IS_ORDER_DETAILS_LOADING_GETTER)
  isLoading!: boolean

  @orderManagement.Getter(GET_IS_ORDER_DETAILS_LOADING_FAILURE_GETTER)
  hasLoadingFailed!: boolean

  @orderManagement.Getter(GET_IS_KITCHEN_CODE_STORE_ADDRESS_GETTER)
  isKitchenCodeStoreAddress!: (id: StoreAddressId) => boolean

  @orderManagement.Getter(GET_IS_GROCERIES_STORE_ADDRESS_GETTER)
  isGroceriesStoreAddress!: (id: StoreAddressId) => boolean

  @Action('displayState') displayState: ToBeMigrated
  @Action('changeModalDisplay') changeModalDisplay: any

  @orderManagement.Action(UPDATE_ORDER_DETAILS_LOADING)
  updateIsOrderDetailsLoading!: StoreAction<UpdateOrderDetailsLoadingPayload>

  @orderManagement.Action(GET_ORDER_DETAILS)
  getOrderDetails!: StoreAction<GetOrderDetailsPayload>

  @orderManagement.Action(GET_ORDER_DETAILS_SUCCESS)
  getOrderDetailsSuccess!: StoreAction<GetOrderDetailsSuccessPayload>

  @orderManagement.Action(GET_ORDER_DETAILS_FAILURE)
  getOrderDetailsFailure!: StoreAction<GetOrderDetailsFailurePayload>

  @orderManagement.Action(ACCEPT_ORDER)
  acceptOrder!: StoreAction<AcceptOrderPayload>

  @orderManagement.Action(ASSIGN_KITCHEN_CODE)
  assignTracking!: StoreAction<AssignKitchenCodePayload>

  @orderManagement.Action(REVERT_PRODUCT)
  revertProduct!: StoreAction<RevertProductPayload>

  @orderManagement.Action(EDIT_PRODUCT)
  editProduct?: StoreAction<EditProductPayload>

  @orderManagement.Action(CLEAR_ORDER_DETAILS_UI_STATE)
  clearUiStateForOrderDetails!: StoreAction<void>

  @orderManagement.Action(OPEN_ORDER_PRODUCT_QUANTITY_MODAL)
  openOrderProductQuantityModal!: StoreAction<
    OpenOrderProductQuantityModalPayload
  >

  @orderManagement.Action(OPEN_ORDER_PRODUCT_ATTRIBUTES_MODAL)
  openOrderProductAttributesModal!: StoreAction<
    OpenOrderProductAttributesModalPayload
  >

  acceptMode = false
  kitchenCodeValue = ''
  productToReplace: OrderProduct | {} = {}
  productToBeRemoved: ProductDetails | null = null
  acceptInProgress = false
  courierReportOverlayVisible = false
  errorResponse: string | null = null

  subscription: Subscription | null = null
  subscriptionTimeout: Timeout | null = null
  delayToSetupOrderDetailsPolling: number = POLLING_INTERVAL
  orderDetailsPollingInterval: number = POLLING_INTERVAL

  get user(): User {
    return User.parse(this.storeAdmin || {})
  }

  mounted() {
    this.retrieveOrderDetails()
    if (!this.modalMode) {
      this.displayState(DISPLAY_ORDER_DETAILS)
    }
    this.setupPollingAfterDelay()
  }

  beforeDestroy() {
    this.tearDownPolling()
  }

  retrieveOrderDetails() {
    if (this.storeAddressId) {
      this.getOrderDetails({
        storeAddressId: this.storeAddressId,
        orderId: this.orderId
      })
    } else {
      this.updateIsOrderDetailsLoading(true)
    }
  }

  @Watch('storeAddressId')
  onStoreAddressIdChange() {
    if (this.storeAddressId) {
      this.retrieveOrderDetails()
    }
  }

  get storeAddressId(): StoreAddressId | null {
    const storeAddressIdFromParams = parseInt(this.$route.params.storeAddressId)
    const orderInState = this.allOrders[this.orderId]
    const storeAddressIdFromState = orderInState
      ? orderInState.storeAddressId
      : null
    if (storeAddressIdFromParams) {
      return storeAddressIdFromParams
    } else if (storeAddressIdFromState) {
      return storeAddressIdFromState
    } else {
      return null // while waiting for the order polling to respond
    }
  }

  get orderId(): OrderId {
    return parseInt(this.$route.params.id)
  }

  setupPollingAfterDelay() {
    this.subscriptionTimeout = setTimeout(() => {
      this.setupOrderDetailsPolling()
    }, this.delayToSetupOrderDetailsPolling)
  }

  setupOrderDetailsPolling() {
    // Refactor into a service: https://glovoapp.atlassian.net/browse/PAR-2157
    const { orderDetails } = ordersApiFactory(HttpModule.fetchService)
    this.subscription = new PollingService({
      interval: this.orderDetailsPollingInterval,
      attempts: Number.POSITIVE_INFINITY
    })
      .createPolling(() => {
        return orderDetails(this.storeAddressId!, this.orderId)
      })
      .subscribe(
        response => this.getOrderDetailsSuccess(response),
        error => this.getOrderDetailsFailure(error)
      )
  }

  tearDownPolling() {
    if (this.subscription) {
      this.subscription.unsubscribe()
    }
    if (this.subscriptionTimeout) {
      clearTimeout(this.subscriptionTimeout)
    }
  }

  showErrorResponse(error: NetworkError) {
    if (error.data && error.data.error && error.data.error.message) {
      this.errorResponse = error.data.error.message
    }
  }

  hasOriginalDescription() {
    return (
      !!this.orderDetails && Order.hasOriginalDescription(this.orderDetails)
    )
  }

  get isAndroidInterfaceEnabled() {
    return window.Android
  }

  printTicket() {
    if (this.orderDetails == null) {
      return
    }
    const ticket = new GlovoTicket(this.orderDetails, {
      currency: this.user.city.currency,
      usesKitchenCode: this.doesOrderUseKitchenCode
    })

    ticket.printTicket(this.ticketCopies)
  }

  changeReplaceModalDisplay(display: boolean) {
    this.changeModalDisplay({ key: 'REPLACE', display })
  }

  changeAddNewProductModalDisplay(display: boolean) {
    this.changeModalDisplay({ key: 'ADD_NEW_PRODUCT', display })
  }

  replace(product: OrderProduct) {
    this.productToReplace = product
    this.changeReplaceModalDisplay(true)
  }

  addNewProduct() {
    this.changeAddNewProductModalDisplay(true)
  }

  async save(product: OrderProduct) {
    this.errorResponse = null
    if (!product.hasModifications()) {
      await this.revert(product)
    } else {
      try {
        await this.editProduct!({
          order: this.orderDetails!,
          product: product
        })
      } catch (error) {
        this.showErrorResponse(error.response)
      }
    }
  }

  async revert(product: OrderProduct) {
    await this.revertProduct!({
      order: this.orderDetails!,
      productId: product.id
    })
  }

  preaccept() {
    if (this.doesOrderUseKitchenCode) {
      this.acceptMode = true
    } else {
      this.accept()
    }
  }

  acceptAndPrint() {
    this.accept()
  }

  async accept() {
    const { id, storeDetails } = this.orderDetails
    // @ts-ignore
    this.$analytics.onAcceptOrderClicked(
      id,
      storeDetails.storeAddressId,
      this.storeAdmin?.id
    )
    if (this.doesOrderUseKitchenCode && !this.kitchenCode) {
      return
    }
    this.acceptInProgress = true
    try {
      if (this.shouldPrintTicketOnAccept) {
        this.printTicket()
      }
      if (this.doesOrderUseKitchenCode && this.kitchenCode) {
        await this.handleKitchenCode()
      }
      await this.handleAcceptOrder(
        this.orderDetails!.storeDetails.storeAddressId,
        this.orderDetails!.id
      )
    } catch (error) {
      telemetry.Log.error('ORDER_ACCEPTANCE_ERROR', error.toString())
    } finally {
      this.acceptInProgress = false
      this.acceptMode = false
    }
  }

  handleKitchenCode() {
    return this.assignTracking!({
      order: this.orderDetails!,
      storeTrackingCode: this.kitchenCode
    })
  }

  handleAcceptOrder(storeAddressId: number, orderId: number) {
    if (this.isPending) {
      return this.acceptOrder({
        storeAddressId,
        orderId
      })
    }
  }

  hasABackRoute() {
    if (!this.$route.meta) {
      return false
    }
    return this.$route.meta.referrer === 'orders'
  }

  goBack() {
    this.clearUiStateForOrderDetails()
    if (this.hasABackRoute()) {
      this.$router.go(-1)
    } else {
      this.$router.replace({ name: 'orders' })
    }
  }

  get shouldShowAcceptButton() {
    return this.user.isAllowedToManageOrders() && this.isPending
  }

  get shouldPrintTicketOnAccept() {
    return this.isAndroidInterfaceEnabled
  }

  showCourierReportOverlay() {
    this.courierReportOverlayVisible = true
  }

  hideCourierReportOverlay() {
    this.courierReportOverlayVisible = false
  }

  shouldShowGroceriesCheckbox() {
    return !!(
      this.orderDetails &&
      this.isGroceriesStoreAddress(
        this.orderDetails.storeDetails.storeAddressId
      )
    )
  }

  @Watch('idle')
  onIdleChange() {
    if (this.idle) {
      this.$router.push('orders')
    }
  }

  get kitchenCode(): string {
    if (this.kitchenCodeValue) {
      return this.kitchenCodeValue
    }
    if (
      this.orderDetails &&
      this.orderDetails.orderInformation.storeTrackingCode
    ) {
      return this.orderDetails.orderInformation.storeTrackingCode
    }
    return ''
  }

  set kitchenCode(code: string) {
    this.kitchenCodeValue = code
  }

  get isOrderReadOnly() {
    return !this.orderDetails || !Order.isEditable(this.orderDetails)
  }

  get idle() {
    return this.$store.state.idleVue.isIdle
  }

  get isMarketplace() {
    return this.orderDetails && this.orderDetails.orderInformation.isMarketplace
  }

  get isPending() {
    if (!this.orderDetails) {
      return false
    }

    return Order.isPending(this.orderDetails)
  }

  get isAccepted() {
    if (!this.orderDetails) {
      return false
    }

    return Order.isAccepted(this.orderDetails)
  }

  get isAcceptDisabled() {
    return (
      this.acceptInProgress ||
      !(!this.doesOrderUseKitchenCode || !!this.kitchenCode)
    )
  }

  get doesOrderUseKitchenCode() {
    if (!this.orderDetails) {
      return false
    }
    return this.isKitchenCodeStoreAddress(
      this.orderDetails.storeDetails.storeAddressId
    )
  }

  get areOrderModificationChangesEnabled() {
    return this.toggles.FE_PARTNERS_ORDER_MODIFICATION_CHANGES_ENABLED
  }

  removeProductFromAnOrder(product: ProductDetails) {
    this.productToBeRemoved = product
  }

  editProductQuantity(productId: ProductId) {
    this.openOrderProductQuantityModal(productId)
  }

  editProductAttributes(productId: ProductId) {
    this.openOrderProductAttributesModal(productId)
  }

  clearProductToBeRemoved() {
    this.productToBeRemoved = null
  }
}
