import { API_ENDPOINTS } from '@/modules/authentication/constants/api-endpoints'
import { Store } from 'vuex'
import { State } from '@/modules/store'
import { REFRESH_TOKEN } from '@/modules/authentication/store/action-types'
import {
  GET_TOKEN_EXPIRATION_TIME_GETTER,
  IS_REFRESH_IN_PROGRESS_GETTER
} from '@/modules/authentication/store/getters'

export function isRefreshCall(url: string): boolean {
  return url.includes(API_ENDPOINTS.REFRESH_TOKEN)
}

export async function refreshToken(store: Store<State>) {
  await store.dispatch(`authentication/${REFRESH_TOKEN}`)
}

export function getExpirationTime(store: Store<State>): number | null {
  return store.getters[`authentication/${GET_TOKEN_EXPIRATION_TIME_GETTER}`]
}

export function isRefreshInProgress(store: Store<State>): boolean {
  return store.getters[`authentication/${IS_REFRESH_IN_PROGRESS_GETTER}`]
}
