import { Module } from 'vuex'
import { initialState, State } from '@/modules/core/store/state'
import { State as RootState } from '@/modules/store'
import { actionsFactory } from '@/modules/core/store/actions'
import { mutations } from '@/modules/core/store/mutations'
import { getters } from '@/modules/core/store/getters'

export function storeFactory(): Module<State, RootState> {
  return {
    namespaced: true,
    state: initialState,
    actions: actionsFactory(),
    mutations,
    getters
  }
}
