
import Vue from 'vue'
import Component from 'vue-class-component'
import { Prop } from 'vue-property-decorator'
import Order from 'models/Order'
import { OrderDetails } from '@/modules/store/data/resources/order-details'
import {
  AcceptedOrder,
  PendingOrder
} from '@/modules/store/data/resources/orders-list/order-types'

@Component({
  name: 'BundleInfo'
})
export default class BundleInfo extends Vue {
  @Prop() order!: OrderDetails | AcceptedOrder | PendingOrder

  get orderStatus() {
    return Order.isAccepted(this.order)
      ? this.$t('orders.accepted.label')
      : this.$t('orders.pending.label')
  }

  private onCardClick() {
    this.$emit('click')
  }

  get bundledOrderCode() {
    if (!this.order) return null
    const { code, pickupCode } = this.order.orderInformation
    if (pickupCode) {
      return pickupCode
    }
    return code
  }
}
