import {
  photosHost as _photosHost,
  picturesCDN as _picturesCDN
} from '../api/localConfig'

export const CLOUDINARY_THUMBNAIL_SETTINGS = 'w_80,h_80,c_fill'

export enum ImageUrlSources {
  external = 'external',
  cloudinary = 'cloudinary',
  local = 'local'
}

export const photosHost = _photosHost
export const picturesCDN = _picturesCDN
export const buildSmartUrl = (
  urlId: string | null,
  origin: string | null,
  params: string | null
) => {
  if (!urlId) {
    return ''
  }

  if (origin === ImageUrlSources.cloudinary) {
    return `${_photosHost}${params}/${urlId}`
  } else if (origin === ImageUrlSources.external) {
    return urlId
  } else if (origin === ImageUrlSources.local) {
    return require(`../assets/images/${urlId}`)
  } else {
    return `${_picturesCDN}${urlId}`
  }
}
