import {
  OrderDetails,
  ProductDetails
} from '@/modules/store/data/resources/order-details'
import {
  AcceptedOrder,
  PendingOrder
} from '@/modules/store/data/resources/orders-list/order-types'
import { EditProductQuantity } from '@/modules/store/data/resources/edit-product-quantity'

export const INIT_STORE = 'INIT_STORE'

// Live orders
export const START_LIVE_ORDERS = 'START_LIVE_ORDERS'
export const STOP_LIVE_ORDERS = 'STOP_LIVE_ORDERS'
export const RESUME_LIVE_ORDERS = 'RESUME_LIVE_ORDERS'
export const PAUSE_LIVE_ORDERS = 'PAUSE_LIVE_ORDERS'
export const SUBSCRIBE_TO_LIVE_ORDERS = 'SUBSCRIBE_TO_LIVE_ORDERS'

export const GET_ORDERS = 'GET_ORDERS'

export const GET_ORDER_DETAILS = 'GET_ORDER_DETAILS'
export type GetOrderDetailsPayload = {
  storeAddressId: StoreAddressId
  orderId: OrderId
}

export const UPDATE_ORDER_DETAILS_LOADING = 'UPDATE_ORDER_DETAILS_LOADING'
export type UpdateOrderDetailsLoadingPayload = boolean

export const UPDATE_ORDER_DETAILS_FAILURE = 'UPDATE_ORDER_DETAILS_FAILURE'
export type UpdateOrderDetailsFailurePayload = boolean

export const CLEAR_ORDER_DETAILS_UI_STATE = 'CLEAR_ORDER_DETAILS_UI_STATE'

export const GET_ORDER_DETAILS_SUCCESS = 'GET_ORDER_DETAILS_SUCCESS'
export type GetOrderDetailsSuccessPayload = OrderDetails

export const GET_ORDER_DETAILS_FAILURE = 'GET_ORDER_DETAILS_FAILURE'
export type GetOrderDetailsFailurePayload = Error

// Store orders
export const ADD_ACCEPTED_ORDERS = 'ADD_ACCEPTED_ORDERS'
export type AddActiveOrdersPayload = { id: OrderId }[]
export const ADD_PENDING_ORDERS = 'ADD_PENDING_ORDERS'
export type AddPendingOrdersPayload = { id: OrderId }[]
export const ADD_CANCELLED_ORDERS = 'ADD_CANCELLED_ORDERS'
export type AddCancelledOrdersPayload = { id: OrderId }[]
export const ADD_SERVED_ORDERS = 'ADD_SERVED_ORDERS'
export type AddServedOrdersPayload = { id: OrderId }[]

// Acknowledge active order
export const MARK_ACTIVE_ORDER_AS_SHOWN = 'MARK_ACTIVE_ORDER_AS_SHOWN'
export type MarkActiveOrderAsShownPayload = OrderId

// Acknowledge cancelled order
export const MARK_CANCELLED_ORDER_AS_SHOWN = 'MARK_CANCELLED_ORDER_AS_SHOWN'
export type MarkCancelledOrderAsShownPayload = OrderId
export const MARK_CANCELLED_ORDER_AS_SHOWN_FAILURE =
  'MARK_CANCELLED_ORDER_AS_SHOWN_FAILURE'
export type MarkCancelledOrderAsShownFailurePayload = UndefinableError

export const MARK_ACTIVE_ORDER_AS_VIEWED = 'MARK_ACTIVE_ORDER_AS_VIEWED'
export interface MarkActiveOrderAsViewedPayload {
  orderId: OrderId
  storeAdminId: StoreAdminId
}

export const MARK_ACTIVE_ORDER_AS_LOADED = 'MARK_ACTIVE_ORDER_AS_LOADED'
export interface MarkActiveOrderAsLoadedPayload {
  orderId: OrderId
  storeAdminId: StoreAdminId
}

// Order acceptance
export const ACCEPT_ORDER = 'ACCEPT_ORDER'
export type AcceptOrderPayload = {
  storeAddressId: StoreAddressId
  orderId: OrderId
}

export const ACCEPT_ORDER_SUCCESS = 'ACCEPT_ORDER_SUCCESS'

export const ACCEPT_ORDER_FAILURE = 'ACCEPT_ORDER_FAILURE'
export type AcceptOrderFailurePayload = UndefinableError

export const ASSIGN_KITCHEN_CODE = 'ASSIGN_KITCHEN_CODE'
export type AssignKitchenCodePayload = {
  order: OrderDetails
  storeTrackingCode: string
}

export const ASSIGN_KITCHEN_CODE_FAILURE = 'ASSIGN_KITCHEN_CODE_FAILURE'
export type AssignKitchenCodeFailurePayload = UndefinableError

export const SET_ORDER_DETAILS_STORE_TRACKING_CODE =
  'SET_ORDER_DETAILS_STORE_TRACKING_CODE'
export type SetStoreTrackingCodePayload = string

// Order products
export const UPDATE_ORDER_PRODUCT_EDIT_MODAL = 'UPDATE_ORDER_PRODUCT_EDIT_MODAL'
export type UpdateOrderProductEditModalPayload = {
  isDisplayed: boolean
  productId: ProductId | null
}
export const OPEN_ORDER_PRODUCT_EDIT_MODAL = 'OPEN_ORDER_PRODUCT_EDIT_MODAL'
export type OpenOrderProductEditModalPayload = {
  productId: ProductId
}
export const CLOSE_ORDER_PRODUCT_EDIT_MODAL = 'CLOSE_ORDER_PRODUCT_EDIT_MODAL'

export const UPDATE_ORDER_PRODUCT_QUANTITY_MODAL =
  'UPDATE_ORDER_PRODUCT_QUANTITY_MODAL'
export type UpdateOrderProductQuantityModalPayload = {
  isDisplayed: boolean
  productId: ProductId | null
}

export const OPEN_ORDER_PRODUCT_QUANTITY_MODAL =
  'OPEN_ORDER_PRODUCT_QUANTITY_MODAL'
export type OpenOrderProductQuantityModalPayload = ProductId

export const CLOSE_ORDER_PRODUCT_QUANTITY_MODAL =
  'CLOSE_ORDER_PRODUCT_QUANTITY_MODAL'

export const UPDATE_ORDER_PRODUCT_ATTRIBUTES_MODAL =
  'UPDATE_ORDER_PRODUCT_ATTRIBUTES_MODAL'
export type UpdateOrderProductAttributesModalPayload = {
  isDisplayed: boolean
  productId: ProductId | null
}

export const OPEN_ORDER_PRODUCT_ATTRIBUTES_MODAL =
  'OPEN_ORDER_PRODUCT_ATTRIBUTES_MODAL'
export type OpenOrderProductAttributesModalPayload = ProductId

export const CLOSE_ORDER_PRODUCT_ATTRIBUTES_MODAL =
  'CLOSE_ORDER_PRODUCT_ATTRIBUTES_MODAL'

export const REVERT_PRODUCT = 'REVERT_PRODUCT'
export type RevertProductPayload = {
  productId: number
  order: OrderDetails
}

export const REVERT_PRODUCT_FAILURE = 'REVERT_PRODUCT_FAILURE'
export type RevertProductFailurePayload = UndefinableError

export const SET_ORDER_DETAILS_PRODUCTS = 'SET_ORDER_DETAILS_PRODUCTS'
export type SetProductsPayload = ProductDetails[]

export const SET_ORDER_DETAILS_TOTAL_NUMBER_OF_PRODUCTS =
  'SET_ORDER_DETAILS_TOTAL_NUMBER_OF_PRODUCTS'
export type SetOrderDetailsTotalNumberOfProductsPayload = ProductDetails[]

export const EDIT_PRODUCT = 'EDIT_PRODUCT'
export type EditProductPayload = {
  order: OrderDetails
  product: OrderProduct
}

export const EDIT_PRODUCT_FAILURE = 'EDIT_PRODUCT_FAILURE'
export type EditProductFailurePayload = UndefinableError

export const REPLACE_PRODUCT = 'REPLACE_PRODUCT'
export type ReplaceProductPayload = {
  order: OrderDetails
  replacedProductId: number
  productToReplaceWith: OrderProduct
}

export const EDIT_PRODUCT_QUANTITY = 'EDIT_PRODUCT_QUANTITY'
export type EditProductQuantityPayload = EditProductQuantity

export const REMOVE_PRODUCT = 'REMOVE_PRODUCT'
export type RemoveProductPayload = {
  orderId: OrderId
  storeAddressId: StoreAddressId
  product: ProductDetails
  hasAlsoBeenDisabledInMenu?: boolean
}

export const RESTORE_EDIT_PRODUCT_QUANTITY = 'RESTORE_EDIT_PRODUCT_QUANTITY'

export const ADD_TEMPORARY_EDIT_PRODUCT_QUANTITY =
  'ADD_TEMPORARY_EDIT_PRODUCT_QUANTITY'
export type AddTemporaryEditProductQuantityPayload = EditProductQuantity

export const CLEAR_TEMPORARY_EDIT_PRODUCT_QUANTITY =
  'CLEAR_TEMPORARY_EDIT_PRODUCT_QUANTITY'

export const REPLACE_PRODUCT_FAILURE = 'REPLACE_PRODUCT_FAILURE'
export type ReplaceProductFailurePayload = UndefinableError

export const SET_IS_GROCERIES_CHECKBOX_CHECKED =
  'SET_IS_GROCERIES_CHECKBOX_CHECKED'
export type SetIsGroceriesCheckboxCheckedPayload = {
  isChecked: boolean
  id: ProductId
}

export const SET_ORDER_DETAILS_DESCRIPTION = 'SET_ORDER_DETAILS_DESCRIPTION'
export type SetDescriptionPayload = string

export const SET_ORDER_DETAILS_AS_ACCEPTED = 'SET_ORDER_DETAILS_AS_ACCEPTED'

export const MARK_ORDER_AS_READY = 'MARK_ORDER_AS_READY'
export type MarkOrderAsReadyPayload = {
  storeAddressId: StoreAddressId
  orderId: OrderId
  buttonTextKey?: string
  storeAdminId?: StoreAdminId
  view?: string | null
}

export const MARK_ORDER_AS_READY_SUCCESS = 'MARK_ORDER_AS_READY_SUCCESS'
export type MarkOrderAsReadySuccessPayload = {
  storeAddressId: StoreAddressId
  orderId: OrderId
}

export const MARK_ORDER_AS_READY_FAILURE = 'MARK_ORDER_AS_READY_FAILURE'
export type MarkOrderAsReadyFailurePayload = Error

export const MARK_ORDER_IS_READY_BUTTON_VIEWED =
  'MARK_ORDER_IS_READY_BUTTON_VIEWED'
export type MarkOrderIsReadyButtonViewedPayload = {
  orderId: OrderId
  buttonTextKey: string
  storeAdminId: StoreAdminId
}

export const DISPATCH_ORDER = 'DISPATCH_ORDER'
export type DispatchOrderPayload = AcceptedOrder | PendingOrder

export const DISPATCH_ORDER_SUCCESS = 'DISPATCH_ORDER_SUCCESS'
export type DispatchOrderSuccessPayload = AcceptedOrder | PendingOrder

export const DISPATCH_ORDER_FAILURE = 'DISPATCH_ORDER_FAILURE'
export type DispatchOrderFailurePayload = any

// Namespaced external Action
export const AUTHENTICATION__SET_SELECTED_STORE_ADDRESS_IDS =
  'authentication/SET_SELECTED_STORE_ADDRESS_IDS'

export type Action =
  | { type: typeof INIT_STORE }
  | { type: typeof START_LIVE_ORDERS }
  | { type: typeof STOP_LIVE_ORDERS }
  | { type: typeof RESUME_LIVE_ORDERS }
  | { type: typeof PAUSE_LIVE_ORDERS }
  | { type: typeof SUBSCRIBE_TO_LIVE_ORDERS }
  | { type: typeof GET_ORDERS }
  | { type: typeof CLEAR_ORDER_DETAILS_UI_STATE }
  | {
      type: typeof ADD_ACCEPTED_ORDERS
      payload: AddActiveOrdersPayload
    }
  | {
      type: typeof ADD_PENDING_ORDERS
      payload: AddActiveOrdersPayload
    }
  | {
      type: typeof ADD_CANCELLED_ORDERS
      payload: AddCancelledOrdersPayload
    }
  | {
      type: typeof ADD_SERVED_ORDERS
      payload: AddServedOrdersPayload
    }
  | {
      type: typeof MARK_ACTIVE_ORDER_AS_SHOWN
      payload: MarkActiveOrderAsShownPayload
    }
  | {
      type: typeof MARK_CANCELLED_ORDER_AS_SHOWN
      payload: MarkCancelledOrderAsShownPayload
    }
  | {
      type: typeof MARK_ACTIVE_ORDER_AS_VIEWED
      payload: MarkActiveOrderAsViewedPayload
    }
  | {
      type: typeof MARK_ACTIVE_ORDER_AS_LOADED
      payload: MarkActiveOrderAsLoadedPayload
    }
  | {
      type: typeof MARK_CANCELLED_ORDER_AS_SHOWN_FAILURE
      payload: MarkCancelledOrderAsShownFailurePayload
    }
  | {
      type: typeof ACCEPT_ORDER
      payload: AcceptOrderPayload
    }
  | {
      type: typeof ACCEPT_ORDER_SUCCESS
    }
  | {
      type: typeof ACCEPT_ORDER_FAILURE
      payload: AcceptOrderFailurePayload
    }
  | {
      type: typeof SET_IS_GROCERIES_CHECKBOX_CHECKED
      payload: SetIsGroceriesCheckboxCheckedPayload
    }
  | {
      type: typeof DISPATCH_ORDER
      payload: DispatchOrderPayload
    }
  | {
      type: typeof DISPATCH_ORDER_SUCCESS
      payload: DispatchOrderSuccessPayload
    }
  | {
      type: typeof DISPATCH_ORDER_FAILURE
      payload: DispatchOrderFailurePayload
    }
  | {
      type: typeof AUTHENTICATION__SET_SELECTED_STORE_ADDRESS_IDS
      payload: SetSelectedStoreAddressIdsPayload
    }
  | {
      type: typeof GET_ORDER_DETAILS
      payload: GetOrderDetailsPayload
    }
  | {
      type: typeof GET_ORDER_DETAILS_SUCCESS
      payload: GetOrderDetailsSuccessPayload
    }
  | {
      type: typeof GET_ORDER_DETAILS_FAILURE
      payload: GetOrderDetailsFailurePayload
    }
  | {
      type: typeof OPEN_ORDER_PRODUCT_EDIT_MODAL
      payload: OpenOrderProductEditModalPayload
    }
  | {
      type: typeof CLOSE_ORDER_PRODUCT_EDIT_MODAL
    }
  | {
      type: typeof UPDATE_ORDER_PRODUCT_EDIT_MODAL
      payload: UpdateOrderProductEditModalPayload
    }
  | {
      type: typeof OPEN_ORDER_PRODUCT_QUANTITY_MODAL
      payload: OpenOrderProductQuantityModalPayload
    }
  | {
      type: typeof CLOSE_ORDER_PRODUCT_QUANTITY_MODAL
    }
  | {
      type: typeof UPDATE_ORDER_PRODUCT_QUANTITY_MODAL
      payload: UpdateOrderProductQuantityModalPayload
    }
  | {
      type: typeof OPEN_ORDER_PRODUCT_ATTRIBUTES_MODAL
      payload: OpenOrderProductAttributesModalPayload
    }
  | {
      type: typeof CLOSE_ORDER_PRODUCT_ATTRIBUTES_MODAL
    }
  | {
      type: typeof UPDATE_ORDER_PRODUCT_ATTRIBUTES_MODAL
      payload: UpdateOrderProductQuantityModalPayload
    }
  | {
      type: typeof UPDATE_ORDER_DETAILS_LOADING
      payload: UpdateOrderDetailsLoadingPayload
    }
  | {
      type: typeof SET_ORDER_DETAILS_STORE_TRACKING_CODE
      payload: SetStoreTrackingCodePayload
    }
  | {
      type: typeof SET_ORDER_DETAILS_PRODUCTS
      payload: SetProductsPayload
    }
  | {
      type: typeof SET_ORDER_DETAILS_TOTAL_NUMBER_OF_PRODUCTS
      payload: SetOrderDetailsTotalNumberOfProductsPayload
    }
  | {
      type: typeof SET_ORDER_DETAILS_DESCRIPTION
      payload: SetDescriptionPayload
    }
  | {
      type: typeof SET_ORDER_DETAILS_AS_ACCEPTED
    }
  | {
      type: typeof ASSIGN_KITCHEN_CODE
      payload: AssignKitchenCodePayload
    }
  | {
      type: typeof ASSIGN_KITCHEN_CODE_FAILURE
      payload: AssignKitchenCodeFailurePayload
    }
  | {
      type: typeof REVERT_PRODUCT
      payload: RevertProductPayload
    }
  | {
      type: typeof REVERT_PRODUCT_FAILURE
      payload: RevertProductFailurePayload
    }
  | {
      type: typeof EDIT_PRODUCT
      payload: EditProductPayload
    }
  | {
      type: typeof EDIT_PRODUCT_FAILURE
      payload: EditProductFailurePayload
    }
  | {
      type: typeof MARK_ORDER_AS_READY
      payload: MarkOrderAsReadyPayload
    }
  | {
      type: typeof MARK_ORDER_AS_READY_SUCCESS
      payload: MarkOrderAsReadySuccessPayload
    }
  | {
      type: typeof MARK_ORDER_AS_READY_FAILURE
      payload: MarkOrderAsReadyFailurePayload
    }
  | {
      type: typeof MARK_ORDER_IS_READY_BUTTON_VIEWED
      payload: MarkOrderIsReadyButtonViewedPayload
    }
  | {
      type: typeof UPDATE_ORDER_DETAILS_FAILURE
      payload: UpdateOrderDetailsFailurePayload
    }
  | {
      type: typeof REPLACE_PRODUCT
      payload: ReplaceProductPayload
    }
  | {
      type: typeof REMOVE_PRODUCT
      payload: RemoveProductPayload
    }
  | {
      type: typeof EDIT_PRODUCT_QUANTITY
      payload: EditProductQuantityPayload
    }
  | {
      type: typeof RESTORE_EDIT_PRODUCT_QUANTITY
    }
  | {
      type: typeof ADD_TEMPORARY_EDIT_PRODUCT_QUANTITY
      payload: AddTemporaryEditProductQuantityPayload
    }
  | {
      type: typeof CLEAR_TEMPORARY_EDIT_PRODUCT_QUANTITY
    }
  | {
      type: typeof REPLACE_PRODUCT_FAILURE
      payload: ReplaceProductFailurePayload
    }
