import { State } from './state'
import {
  LOG_OUT,
  SET_CURRENT_STORE_ADMIN,
  SetCurrentStoreAdminPayload,
  SET_ADMIN_MODE,
  SetAdminModePayload,
  SET_SELECTED_STORE_ADDRESS_IDS,
  SetSelectedStoreAddressIdsPayload,
  REFRESH_TOKEN_BEFORE,
  REFRESH_TOKEN_AFTER,
  SET_TOKEN_SUCCESS,
  SetTokenSuccessPayload,
  RestoreSessionSuccessPayload,
  RESTORE_SESSION_SUCCESS,
  STORE_ADMIN_LOADED_SUCCESS,
  STORE_ADMIN_LOADED_FAILURE
} from './action-types'

export const mutations = {
  [SET_TOKEN_SUCCESS](state: State, payload: SetTokenSuccessPayload) {
    const { tokenInformation, tokenExpirationTime } = payload
    state.tokenInformation = { ...tokenInformation }
    state.tokenExpirationTime = tokenExpirationTime
  },
  [LOG_OUT](state: State) {
    state.storeAdminId = null
    state.tokenInformation = null
    state.tokenExpirationTime = null
    state.selectedStoreAddressIds = []
  },
  [SET_CURRENT_STORE_ADMIN](
    state: State,
    payload: SetCurrentStoreAdminPayload
  ) {
    state.storeAdminId = payload.id
  },
  [SET_ADMIN_MODE](state: State, payload: SetAdminModePayload) {
    state.isAdminMode = payload
  },
  [SET_SELECTED_STORE_ADDRESS_IDS](
    state: State,
    payload: SetSelectedStoreAddressIdsPayload
  ) {
    state.selectedStoreAddressIds = payload
  },
  [RESTORE_SESSION_SUCCESS](
    state: State,
    payload: RestoreSessionSuccessPayload
  ) {
    state.tokenInformation = payload.tokenInformation
    state.tokenExpirationTime = payload.tokenExpirationTime
  },
  [REFRESH_TOKEN_BEFORE](state: State) {
    state.refreshInProgress = true
  },
  [REFRESH_TOKEN_AFTER](state: State) {
    state.refreshInProgress = false
  },
  [STORE_ADMIN_LOADED_SUCCESS](state: State) {
    state.storeAdminLoaded = true
  },
  [STORE_ADMIN_LOADED_FAILURE](state: State) {
    state.storeAdminLoaded = false
  }
}
