import { AbstractFetchService } from '@/modules/http'
import { Module } from 'vuex'
import { initialState, State } from '@/modules/invoices/store/state'
import { State as RootState } from '@/modules/store'
import { actionsFactory } from '@/modules/invoices/store/actions'
import { mutations } from '@/modules/invoices/store/mutations'
import invoiceApiFactory from '@/api/invoices'
import { getters } from '@/modules/invoices/store/getters'

export function storeFactory(
  fetchService: AbstractFetchService
): Module<State, RootState> {
  const invoiceApi = invoiceApiFactory(fetchService)
  return {
    namespaced: true,
    state: initialState,
    actions: actionsFactory(invoiceApi),
    mutations,
    getters
  }
}
