
import Component from 'vue-class-component'
import Vue from 'vue'
import { Prop } from 'vue-property-decorator'
import GlovoBtn from '../../../../components/common/GlovoBtn.vue'
import { OrderId } from '../../../store'
import {
  MARK_ORDER_AS_READY,
  MarkOrderAsReadyPayload,
  MARK_ORDER_IS_READY_BUTTON_VIEWED,
  MarkOrderIsReadyButtonViewedPayload
} from '../../store'
import { orderManagement } from '../../store/namespace'
import { StoreAction } from '../../../store/types/store'
import { StoreAddressId } from '../../../store/data/resources'
import { GET_TOGGLES_GETTER } from '@/modules/feature-toggles'
import { FeatureToggle } from '@/modules/feature-toggles/store/state'
import { Getter } from 'vuex-class'
import { authentication } from '@/modules/authentication/store/namespace'
import { GET_CURRENT_STORE_ADMIN_GETTER } from '@/modules/authentication'
import { StoreAdmin } from '@/modules/store/data/resources'

@Component({ name: 'OrderReadyButton', components: { GlovoBtn } })
export default class OrderReadyButton extends Vue {
  @Prop() orderId!: OrderId
  @Prop() storeAddressId!: StoreAddressId
  @Prop() isReady!: boolean
  @Prop({ default: false }) hasBeenViewed!: boolean

  @orderManagement.Action(MARK_ORDER_AS_READY) markAsReady!: StoreAction<
    MarkOrderAsReadyPayload
  >

  @orderManagement.Action(MARK_ORDER_IS_READY_BUTTON_VIEWED)
  markOrderIsReadyButtonAsViewed!: StoreAction<
    MarkOrderIsReadyButtonViewedPayload
  >

  @authentication.Getter(GET_CURRENT_STORE_ADMIN_GETTER)
  storeAdmin!: StoreAdmin

  @Getter(GET_TOGGLES_GETTER) toggles!: Record<FeatureToggle, string>

  markOrderAsReady() {
    this.markAsReady({
      storeAddressId: this.storeAddressId,
      orderId: this.orderId,
      storeAdminId: (this.storeAdmin || {}).id || -1,
      buttonTextKey: this.translationKey,
      view: this.$route.name
    })
  }

  markButtonAsViewed(isVisible: boolean) {
    if (!this.hasBeenViewed && isVisible) {
      this.markOrderIsReadyButtonAsViewed({
        orderId: this.orderId,
        storeAdminId: (this.storeAdmin || {}).id || -1,
        buttonTextKey: this.translationKey
      })
    }
  }

  get translationKey() {
    return this.toggles.FE_PARTNERS_ORDER_SWAP_BUTTON_TEXT
  }

  get buttonText() {
    return this.$t(this.translationKey)
  }
}
