import { AbstractFetchService } from '@/modules/http'
import { Module } from 'vuex'
import { State as RootState } from '@/modules/store'

import { storesApiFactory } from '../api/stores-api'
import { initialState, State } from './state'
import { actionsFactory } from './actions'
import { mutations } from './mutations'
import { getters } from './getters'

export {
  GET_STORE_PRODUCTS_BY_STORE_ID,
  GET_STORE_PRODUCTS_ERROR_GETTER,
  GET_STORE_PRODUCTS_LOADING_GETTER
} from './getters'

export { GET_STORE_PRODUCTS } from './action-types'

export function storeFactory(
  fetchService: AbstractFetchService
): Module<State, RootState> {
  const storesApi = storesApiFactory(fetchService)
  return {
    namespaced: true,
    state: initialState,
    actions: actionsFactory(storesApi),
    mutations,
    getters
  }
}
