  ASSIGN_KITCHEN_CODE,
  AssignKitchenCodePayload,
  REPLACE_PRODUCT,
  ReplaceProductPayload,
  Action,
  DispatchOrderFailurePayload,
  ADD_SERVED_ORDERS,
  AddServedOrdersPayload,
  SET_IS_GROCERIES_CHECKBOX_CHECKED,
  SetIsGroceriesCheckboxCheckedPayload,
  MARK_ACTIVE_ORDER_AS_VIEWED,
  MarkActiveOrderAsViewedPayload,
  MARK_ACTIVE_ORDER_AS_LOADED,
  MarkActiveOrderAsLoadedPayload,
