import { ServedOrder } from '@/modules/store/data/resources/orders-list/order-types'
import {
  defaultOrdersResponse,
  OrdersResponse
} from '@/modules/orders/services/live-orders/abstract-live-orders/orders-response'
import { Getters, TEMPORARY_EDIT_PRODUCT_QUANTITY_GETTER } from './getters'
import { State } from './state'
import isEmpty from 'lodash/isEmpty'

export function actionsFactory(
  liveOrdersService: AbstractLiveOrdersService,
  fetchService: AbstractFetchService
) {
  const {
    markActiveOrderAsShown,
    markCancelledOrderAsShown,
    orders,
    accept,
    assignTrackingCode,
    revert,
    replace,
    dispatchOrder,
    orderDetails,
    markReadyForPickup,
    editProduct
  } = ordersApiFactory(fetchService)

  const { removeProductStockout } = stockApiFactory(fetchService)

  return {
    [INIT_STORE]({ dispatch }: ActionContext<Action>) {
      dispatch(SUBSCRIBE_TO_LIVE_ORDERS)
    },
    [ADD_ACCEPTED_ORDERS](
      { commit }: ActionContext<Action>,
      payload: AddActiveOrdersPayload
    ) {
      commit(ADD_ACCEPTED_ORDERS, payload)
    },
    [ADD_PENDING_ORDERS](
      { commit }: ActionContext<Action>,
      payload: AddPendingOrdersPayload
    ) {
      commit(ADD_PENDING_ORDERS, payload)
    },
    [ADD_CANCELLED_ORDERS](
      { commit }: ActionContext<Action>,
      payload: AddCancelledOrdersPayload
    ) {
      commit(ADD_CANCELLED_ORDERS, payload)
    },
    [ADD_SERVED_ORDERS](
      { commit }: ActionContext<Action>,
      payload: AddServedOrdersPayload
    ) {
      commit(ADD_SERVED_ORDERS, payload)
    },
    async [GET_ORDERS]({ rootGetters, dispatch }: ActionContext<Action>) {
      const storeAddressIds =
        rootGetters[`authentication/${GET_SELECTED_STORE_ADDRESS_IDS_GETTER}`]
      if (storeAddressIds.length > 0) {
        let response: OrdersResponse
        try {
          response = await orders(storeAddressIds)
        } catch (error) {
          updateIncorrectlyRequestedStoreAddressIdsIfNecessary(
            error,
            dispatch,
            storeAddressIds
          )
          throw error
        } finally {
          recordOrdersPollingMetrics(rootGetters)
        }
        return response
      } else {
        return Promise.resolve(defaultOrdersResponse)
      }
    },
    async [MARK_ACTIVE_ORDER_AS_SHOWN](
      _: ActionContext<Action>,
      payload: MarkActiveOrderAsShownPayload
    ) {
      await markActiveOrderAsShown(payload)
    },
    async [MARK_CANCELLED_ORDER_AS_SHOWN](
      { dispatch }: ActionContext<Action>,
      payload: MarkCancelledOrderAsShownPayload
    ) {
      try {
        await markCancelledOrderAsShown(payload)
        dispatch(MARK_CANCELLED_ORDER_AS_ACKNOWLEDGED, payload, { root: true })
      } catch (err) {
        dispatch(MARK_CANCELLED_ORDER_AS_SHOWN_FAILURE, err)
      }
    },
    [MARK_ACTIVE_ORDER_AS_LOADED](
      { dispatch }: ActionContext<Action>,
      { orderId, storeAdminId }: MarkActiveOrderAsLoadedPayload
    ) {
      mParticle.logEvent(
        BusinessEventNames.notificationPixelLoaded,
        mParticle.EventType.UserContent,
        {
          orderId,
          storeAdminId,
          deviceId: getDeviceId()
        }
      )
      dispatch(MARK_ORDER_AS_LOADED, orderId, { root: true })
      dispatch(MARK_ACTIVE_ORDER_AS_SHOWN, orderId)
    },
    [MARK_ACTIVE_ORDER_AS_VIEWED](
      { dispatch }: ActionContext<Action>,
      { orderId, storeAdminId }: MarkActiveOrderAsViewedPayload
    ) {
      mParticle.logEvent(
        BusinessEventNames.notificationPixelViewed,
        mParticle.EventType.UserContent,
        {
          orderId,
          storeAdminId,
          deviceId: getDeviceId()
        }
      )
      dispatch(MARK_ORDER_AS_VIEWED, orderId, { root: true })
    },
    [MARK_CANCELLED_ORDER_AS_SHOWN_FAILURE](
      _: ActionContext<Action>,
      payload: MarkCancelledOrderAsShownFailurePayload
    ) {
      // tslint:disable-next-line: no-console
      console.log(payload)
    },
    [SUBSCRIBE_TO_LIVE_ORDERS]({ dispatch }: ActionContext<Action>) {
      liveOrdersService.subscribe(response => {
        const { accepted, pending, cancelled, served } = response
        logErrorToDatadogIfIncorrectResponseFound(response)
        dispatch(ADD_ACCEPTED_ORDERS, accepted)
        dispatch(ADD_PENDING_ORDERS, pending)
        dispatch(ADD_CANCELLED_ORDERS, cancelled)
        dispatch(ADD_SERVED_ORDERS, served)
        const allOrders = [
          ...accepted, // deliberately
          ...pending, // adding line breaks
          ...cancelled, // to see when the error comes up (delete these comments when fixed)
          ...served // https://glovoapp.atlassian.net/browse/PAR-2360
        ]
        dispatch(data_state_ADD_ORDERS, allOrders, { root: true })
      })
    },
    [START_LIVE_ORDERS]() {
      liveOrdersService.start()
    },
    [STOP_LIVE_ORDERS]() {
      liveOrdersService.stop()
    },
    [RESUME_LIVE_ORDERS]() {
      liveOrdersService.resume()
    },
    [PAUSE_LIVE_ORDERS]() {
      liveOrdersService.pause()
    },
    async [ACCEPT_ORDER](
      { dispatch }: ActionContext<Action>,
      { storeAddressId, orderId }: AcceptOrderPayload
    ) {
      try {
        await accept(storeAddressId, orderId)
        dispatch(ACCEPT_ORDER_SUCCESS)
      } catch (error) {
        dispatch(ACCEPT_ORDER_FAILURE, error)
      }
    },
    async [ACCEPT_ORDER_SUCCESS]({ commit }: ActionContext<Action>) {
      commit(SET_ORDER_DETAILS_AS_ACCEPTED)
    },
    [ACCEPT_ORDER_FAILURE](
      _: ActionContext<Action>,
      error: AcceptOrderFailurePayload
    ) {
      telemetry.Log.error(ACCEPT_ORDER_FAILURE, error.toString())
    },
    async [ASSIGN_KITCHEN_CODE](
      { dispatch, commit }: ActionContext<Action>,
      { order, storeTrackingCode }: AssignKitchenCodePayload
    ) {
      try {
        await assignTrackingCode(order.storeDetails.storeAddressId, order.id, {
          code: storeTrackingCode
        })
        commit(SET_ORDER_DETAILS_STORE_TRACKING_CODE, storeTrackingCode)
      } catch (error) {
        dispatch(ASSIGN_KITCHEN_CODE_FAILURE, error)
      }
    },
    [ASSIGN_KITCHEN_CODE_FAILURE](
      _: ActionContext<Action>,
      error: AssignKitchenCodeFailurePayload
    ) {
      telemetry.Log.error(ASSIGN_KITCHEN_CODE_FAILURE, error.toString())
    },
    async [REVERT_PRODUCT](
      { dispatch, commit }: ActionContext<Action>,
      { productId, order }: RevertProductPayload
    ) {
      try {
        const response = await revert(
          order.storeDetails.storeAddressId,
          order.id,
          productId
        )

        // TODO: move this mapping to API level - https://glovoapp.atlassian.net/browse/PAR-1701
        const products = parseProductsFromResponse(response.servedProducts)
        commit(SET_ORDER_DETAILS_PRODUCTS, products)
      } catch (error) {
        dispatch(REVERT_PRODUCT_FAILURE, error)
      }
    },
    [REVERT_PRODUCT_FAILURE](
      _: ActionContext<Action>,
      error: RevertProductFailurePayload
    ) {
      telemetry.Log.error(REVERT_PRODUCT_FAILURE, error.toString())
    },
    async [EDIT_PRODUCT](
      { dispatch, commit }: ActionContext<Action>,
      { order, product }: EditProductPayload
    ) {
      try {
        const response = await replace(
          order.storeDetails.storeAddressId,
          order.id,
          formatServedProductsPayload(product)
        )

        // TODO: move this mapping to API level - https://glovoapp.atlassian.net/browse/PAR-1701
        const products = parseProductsFromResponse(response.servedProducts)
        commit(SET_ORDER_DETAILS_DESCRIPTION, response.description)
        commit(SET_ORDER_DETAILS_PRODUCTS, products)
        commit(SET_ORDER_DETAILS_TOTAL_NUMBER_OF_PRODUCTS, products)
      } catch (error) {
        dispatch(EDIT_PRODUCT_FAILURE, error)
      }
    },
    [EDIT_PRODUCT_FAILURE](
      _: ActionContext<Action>,
      error: EditProductFailurePayload
    ) {
      telemetry.Log.error(EDIT_PRODUCT_FAILURE, error.toString())
    },
    async [EDIT_PRODUCT_QUANTITY](
      { dispatch, commit }: ActionContext<Action>,
      {
        orderId,
        storeAddressId,
        product,
        quantity,
        hasAlsoBeenDisabledInMenu
      }: EditProductQuantityPayload
    ) {
      try {
        const modifiedProduct = {
          ...product,
          quantity
        }

        const response = await editProduct(
          storeAddressId,
          orderId,
          modifiedProduct
        )

        // TODO: move this mapping to API level - https://glovoapp.atlassian.net/browse/PAR-1701
        const products = parseProductsFromResponse(response.servedProducts)
        commit(SET_ORDER_DETAILS_DESCRIPTION, response.description)
        commit(SET_ORDER_DETAILS_PRODUCTS, products)
        commit(SET_ORDER_DETAILS_TOTAL_NUMBER_OF_PRODUCTS, products)
        commit(ADD_TEMPORARY_EDIT_PRODUCT_QUANTITY, {
          orderId,
          storeAddressId,
          product,
          hasAlsoBeenDisabledInMenu,
          quantity: product.quantity
        })
      } catch (error) {
        dispatch(EDIT_PRODUCT_FAILURE, error)
      }
    },
    async [REMOVE_PRODUCT](
      { dispatch }: ActionContext<Action>,
      {
        orderId,
        storeAddressId,
        product,
        hasAlsoBeenDisabledInMenu
      }: RemoveProductPayload
    ) {
      dispatch(EDIT_PRODUCT_QUANTITY, {
        orderId,
        storeAddressId,
        product,
        hasAlsoBeenDisabledInMenu,
        quantity: 0
      })
    },
    async [RESTORE_EDIT_PRODUCT_QUANTITY]({
      commit,
      dispatch,
      getters
    }: ActionContext<Action, State, Getters>) {
      const temporaryEditProductQuantity =
        getters[TEMPORARY_EDIT_PRODUCT_QUANTITY_GETTER]
      if (temporaryEditProductQuantity) {
        const {
          storeAddressId,
          orderId,
          product,
          hasAlsoBeenDisabledInMenu
        } = temporaryEditProductQuantity

        if (hasAlsoBeenDisabledInMenu) {
          await removeProductStockout(storeAddressId, {
            productIds: [product.productId]
          })
        }
        dispatch(EDIT_PRODUCT_QUANTITY, {
          orderId,
          storeAddressId,
          product,
          quantity: product.quantity
        })
        commit(CLEAR_TEMPORARY_EDIT_PRODUCT_QUANTITY)
      }
    },
    async [REPLACE_PRODUCT](
      { dispatch, commit }: ActionContext<Action>,
      { order, replacedProductId, productToReplaceWith }: ReplaceProductPayload
    ) {
      try {
        const productReplacement = generateProductReplacement(
          replacedProductId,
          productToReplaceWith
        )
        const response = await replace(
          order.storeDetails.storeAddressId,
          order.id,
          {
            servedProducts: [productReplacement]
          }
        )

        // TODO: move this mapping to API level - https://glovoapp.atlassian.net/browse/PAR-1701
        const products = parseProductsFromResponse(response.servedProducts)
        commit(SET_ORDER_DETAILS_DESCRIPTION, response.description)
        commit(SET_ORDER_DETAILS_PRODUCTS, products)
      } catch (error) {
        dispatch(REPLACE_PRODUCT_FAILURE, error)
      }
    },
    [REPLACE_PRODUCT_FAILURE](
      _: ActionContext<Action>,
      error: ReplaceProductFailurePayload
    ) {
      telemetry.Log.error(REPLACE_PRODUCT_FAILURE, error.toString())
    },
    async [SET_IS_GROCERIES_CHECKBOX_CHECKED](
      { dispatch, commit }: ActionContext<Action>,
      payload: SetIsGroceriesCheckboxCheckedPayload
    ) {
      dispatch(MARK_IS_GROCERIES_CHECKBOX_CHECKED, payload, { root: true })
      commit(SET_IS_GROCERIES_CHECKBOX_CHECKED, payload)
    },
    async [DISPATCH_ORDER](
      { dispatch }: ActionContext<Action>,
      payload: DispatchOrderPayload
    ) {
      try {
        dispatch(PAUSE_LIVE_ORDERS)
        await dispatchOrder(payload.storeAddressId, payload.id)
        dispatch(DISPATCH_ORDER_SUCCESS, payload)
      } catch (error) {
        dispatch(DISPATCH_ORDER_FAILURE, error)
      } finally {
        dispatch(RESUME_LIVE_ORDERS)
      }
    },
    [DISPATCH_ORDER_SUCCESS](
      { dispatch, commit }: ActionContext<Action | AnotherModuleAction>,
      payload: DispatchOrderSuccessPayload
    ) {
      const servedOrder: ServedOrder = {
        // this transformation will be easier after tech debt ticket: https://glovoapp.atlassian.net/browse/PAR-2190
        id: payload.id,
        storeAddressId: payload.storeAddressId,
        code: payload.orderInformation.code,
        pickupCode: payload.orderInformation.pickupCode,
        servingTime: moment().toISOString(),
        totalNumberOfProducts: payload.orderInformation.totalNumberOfProducts,
        statusDetails: payload.statusDetails
      }
      dispatch(data_state_ADD_ORDERS, [servedOrder], { root: true })
      commit(DISPATCH_ORDER_SUCCESS, servedOrder)
    },
    [DISPATCH_ORDER_FAILURE](
      _: ActionContext<Action>,
      payload: DispatchOrderFailurePayload
    ) {
      console.error(payload)
    },
    [CLEAR_ORDER_DETAILS_UI_STATE]({ commit }: ActionContext<Action>) {
      commit(UPDATE_ORDER_DETAILS_LOADING, false)
      commit(UPDATE_ORDER_DETAILS_FAILURE, false)
    },
    async [GET_ORDER_DETAILS](
      { dispatch, commit }: ActionContext<Action>,
      { storeAddressId, orderId }: GetOrderDetailsPayload
    ) {
      try {
        commit(UPDATE_ORDER_DETAILS_FAILURE, false)
        commit(UPDATE_ORDER_DETAILS_LOADING, true)
        const details = await orderDetails(storeAddressId, orderId)
        dispatch(GET_ORDER_DETAILS_SUCCESS, details)
      } catch (error) {
        dispatch(GET_ORDER_DETAILS_FAILURE, error)
      } finally {
        commit(UPDATE_ORDER_DETAILS_LOADING, false)
      }
    },
    [GET_ORDER_DETAILS_SUCCESS](
      { commit }: ActionContext<Action>,
      orderDetailsResponse: GetOrderDetailsSuccessPayload
    ) {
      commit(GET_ORDER_DETAILS_SUCCESS, orderDetailsResponse)
    },
    [GET_ORDER_DETAILS_FAILURE](
      { commit }: ActionContext<Action>,
      error: GetOrderDetailsFailurePayload
    ) {
      telemetry.Log.error(GET_ORDER_DETAILS_FAILURE, error.toString())
      commit(UPDATE_ORDER_DETAILS_FAILURE, true)
    },
    [UPDATE_ORDER_DETAILS_LOADING](
      { commit }: ActionContext<Action>,
      payload: UpdateOrderDetailsLoadingPayload
    ) {
      commit(UPDATE_ORDER_DETAILS_LOADING, payload)
    },
    async [MARK_ORDER_AS_READY](
      { dispatch }: ActionContext<Action>,
      {
        storeAddressId,
        orderId,
        buttonTextKey,
        storeAdminId,
        view
      }: MarkOrderAsReadyPayload
    ) {
      try {
        mParticle.logEvent(
          BusinessEventNames.orderIsReadyButtonClicked,
          mParticle.EventType.UserContent,
          {
            orderId,
            storeAdminId,
            buttonTextKey,
            deviceId: getDeviceId(),
            view
          }
        )
        datadogRum.addUserAction('order_ready_button_clicked', {
          view,
          buttonTextKey,
          orderId,
          storeAdminId,
          deviceId: getDeviceId()
        })
        await markReadyForPickup(storeAddressId, orderId)
        dispatch(MARK_ORDER_AS_READY_SUCCESS, { storeAddressId, orderId })
      } catch (error) {
        dispatch(MARK_ORDER_AS_READY_FAILURE, error)
      }
    },
    [MARK_ORDER_AS_READY_SUCCESS](
      { commit }: ActionContext<Action>,
      payload: MarkOrderAsReadySuccessPayload
    ) {
      commit(MARK_ORDER_AS_READY, payload)
      commit(SET_IS_ORDER_READY, payload, { root: true })
    },
    [MARK_ORDER_AS_READY_FAILURE](
      _: ActionContext<Action>,
      error: MarkOrderAsReadyFailurePayload
    ) {
      telemetry.Log.error(MARK_ORDER_AS_READY_FAILURE, error.toString())
    },
    [MARK_ORDER_IS_READY_BUTTON_VIEWED](
      { dispatch }: ActionContext<Action>,
      {
        orderId,
        buttonTextKey,
        storeAdminId
      }: MarkOrderIsReadyButtonViewedPayload
    ) {
      mParticle.logEvent(
        BusinessEventNames.orderIsReadyButtonViewed,
        mParticle.EventType.UserContent,
        {
          orderId,
          storeAdminId,
          buttonTextKey,
          deviceId: getDeviceId()
        }
      )
      dispatch(MARK_ORDER_IS_READY_BUTTON_AS_VIEWED, orderId, { root: true })
    },
    [OPEN_ORDER_PRODUCT_EDIT_MODAL](
      { commit }: ActionContext<Action>,
      { productId }: OpenOrderProductEditModalPayload
    ) {
      commit(UPDATE_ORDER_PRODUCT_EDIT_MODAL, { isDisplayed: true, productId })
    },
    [CLOSE_ORDER_PRODUCT_EDIT_MODAL]({ commit }: ActionContext<Action>) {
      commit(UPDATE_ORDER_PRODUCT_EDIT_MODAL, {
        isDisplayed: false,
        productId: null
      })
    },
    [OPEN_ORDER_PRODUCT_QUANTITY_MODAL](
      { commit }: ActionContext<Action>,
      productId: OpenOrderProductQuantityModalPayload
    ) {
      commit(UPDATE_ORDER_PRODUCT_QUANTITY_MODAL, {
        isDisplayed: true,
        productId
      })
    },
    [CLOSE_ORDER_PRODUCT_QUANTITY_MODAL]({ commit }: ActionContext<Action>) {
      commit(UPDATE_ORDER_PRODUCT_QUANTITY_MODAL, {
        isDisplayed: false,
        productId: null
      })
    },
    [OPEN_ORDER_PRODUCT_ATTRIBUTES_MODAL](
      { commit }: ActionContext<Action>,
      productId: OpenOrderProductAttributesModalPayload
    ) {
      commit(UPDATE_ORDER_PRODUCT_ATTRIBUTES_MODAL, {
        isDisplayed: true,
        productId
      })
    },
    [CLOSE_ORDER_PRODUCT_ATTRIBUTES_MODAL]({ commit }: ActionContext<Action>) {
      commit(UPDATE_ORDER_PRODUCT_ATTRIBUTES_MODAL, {
        isDisplayed: false,
        productId: null
      })
    }
  }
}

const generateProductReplacement = (
  replacedProductId: number,
  productToReplaceWith: Product
) => {
  const productReplacementTemplate = {
    replacedBougthProductId: replacedProductId,
    servedProduct: {
      quantity: productToReplaceWith.quantity || 1,
      id: productToReplaceWith.id
    }
  }
  if (productToReplaceWith.persoAttributes) {
    return {
      ...productReplacementTemplate,
      servedProduct: {
        ...productReplacementTemplate.servedProduct,
        customizations: productToReplaceWith.persoAttributes.map(
          ({ quantity, id }) => ({
            quantity: quantity!,
            attributeId: Number(id),
            name: '',
            priceImpact: ''
          })
        )
      }
    }
  } else {
    return productReplacementTemplate
  }
}

function updateIncorrectlyRequestedStoreAddressIdsIfNecessary(
  error: UndefinableError,
  dispatch: Dispatch,
  storeAddressIds: StoreAddressId[]
) {
  if (error.response && error.response.status === 403) {
    const invalidId = getInvalidStoreAddressIdFromError(
      error.response.data.error.message
    )
    dispatch(
      AUTHENTICATION__SET_SELECTED_STORE_ADDRESS_IDS,
      without(storeAddressIds, invalidId),
      { root: true }
    )
  }
}

function getInvalidStoreAddressIdFromError(message: string): number {
  const expr = /cannot manage storeAddress (?<invalidID>\d+)/
  const result = expr.exec(message)
  if (!result || !result.groups) {
    throw new Error('Error message does not contain an invalid storeAddress id')
  }
  return Number(result.groups.invalidID)
}

function recordOrdersPollingMetrics(rootGetters: GlobalGetters) {
  const featureToggles = rootGetters[GET_TOGGLES_GETTER]
  if (featureToggles) {
    if (featureToggles.FE_PARTNERS_ORDERS_POLLING_METRIC_ENABLED) {
      telemetry.Metric.count('polling_orders', 1) // downsampled in src/modules/telemetry/helpers/init-telemetry-client.ts
    }
    if (featureToggles.FE_PARTNERS_NEW_POLLING_USER_ACTION_ENABLED) {
      datadogRum.addUserAction('polling_orders', {})
    }
  }
}

export function logErrorToDatadogIfIncorrectResponseFound(
  response: OrdersResponse | any // adding any to enable testing
) {
  if (response) {
    const responseContainsNonArrayValue = Object.values(response).some(
      orders => !Array.isArray(orders)
    )
    if (responseContainsNonArrayValue || isEmpty(response)) {
      telemetry.Log.error('orders_response', 'subscription', {
        attr: {
          response: JSON.stringify(response)
        }
      })
    }
  }
}
