
import Vue from 'vue'
import Component from 'vue-class-component'
import { Prop } from 'vue-property-decorator'
import GlovoBadge from '@/components/common/ui/GlovoBadge.vue'
import { currency } from '@/modules/core/filters'
import type {
  AttributeId,
  AttributeGroup
} from '@/modules/store/data/resources'

@Component({
  name: 'ProductCustomization',
  components: {
    GlovoBadge
  }
})
export default class ProductCustomization extends Vue {
  @Prop({ default: () => {} })
  attributeGroup!: AttributeGroup

  selectedAttributes: AttributeId[] = Object.values(
    this.attributeGroup.attributes
  )
    .filter(({ selected }) => selected)
    .map(({ id }) => id)

  formattedCurrency(price: number) {
    return currency(price)
  }
}
