import { StoreProductsListDTO, StoreProductDTO } from '../api/dtos'
import { StoreProductsList, StoreProductVM, ProductsMapById } from '../models'

export const mapProduct = (dto: StoreProductDTO): StoreProductVM => {
  return new StoreProductVM(dto)
}

export const mapStoreProducts = (
  products: StoreProductDTO[] | null
): ProductsMapById => {
  if (!Array.isArray(products)) {
    return {}
  }

  return products.reduce((acc, product) => {
    acc[product.productExternalId] = mapProduct(product)
    return acc
  }, {} as ProductsMapById)
}

export const mapStoreProductsList = (
  storeProductsListDTO: StoreProductsListDTO | null
): StoreProductsList => {
  const { products } = storeProductsListDTO || {}
  return (products || []).map(mapProduct)
}
