import forEach from 'lodash/forEach'
import { Product, Attribute, AttributeGroup } from 'models/Product'

export default {
  validSelection(parentProduct) {
    let isValid = true
    forEach(parentProduct.attributeGroups, function (group) {
      group.totalSelected = group.totalSelected || 0
      if (group.totalSelected < group.min || group.totalSelected > group.max) {
        isValid = false
      }
    })
    return isValid
  },
  parseAndCloneProduct(product, initial) {
    const _attributeGroups = []
    forEach(product.attributeGroups, attributeGroup => {
      const _attributes = []
      forEach(attributeGroup.attributes, attribute => {
        let existingAttribute = null
        forEach(initial.customizations, customization => {
          if (customization.attributeId === attribute.id) {
            existingAttribute = customization
            return false
          }
        })
        const _quantity = existingAttribute
          ? existingAttribute.quantity
          : attribute.quantity || (attribute.selected ? 1 : 0)
        const _attribute = new Attribute(
          attribute.id,
          attribute.name,
          attribute.priceImpact,
          _quantity,
          existingAttribute ? true : attribute.selected
        )
        _attributes.push(_attribute)
      })
      const _attributeGroup = new AttributeGroup(
        attributeGroup.name,
        attributeGroup.min,
        attributeGroup.max,
        attributeGroup.multipleSelection,
        attributeGroup.collapsedByDefault,
        _attributes
      )
      _attributeGroups.push(_attributeGroup)
    })
    const prod = new Product(
      product.id,
      initial.quantity,
      product.name,
      product.price,
      product.type,
      _attributeGroups
    )
    return prod
  }
}
