import initTelemetryClient from './helpers/init-telemetry-client'
import {
  TelemetryClientLogFunction,
  TelemetryClientMetricFunction,
  NetworkRequestEventFunction,
  PageLoadingTimeEventFunction,
  RuntimeErrorEventFunction,
  MemoryUsageEventFunction,
  TelemetryClientSetDefaultTagFunction,
  TelemetryClientRemoveDefaultTagFunction,
  TelemetryClientSetDefaultAttributeFunction,
  TelemetryClientRemoveDefaultAttributeFunction,
  TelemetryClientBypassDownsamplingFunction
} from '@glovo/telemetry-client-web'
import Store from '@/modules/store'
import { GET_TOGGLES_GETTER } from '@/modules/feature-toggles'

function isTelemetryEnabled() {
  return (
    Store.store &&
    Store.store.getters[GET_TOGGLES_GETTER].FE_PARTNERS_TELEMETRY_CLIENT
  )
}

const telemetryClientInstance = initTelemetryClient()

export interface Telemetry {
  Log: {
    info: TelemetryClientLogFunction
    warn: TelemetryClientLogFunction
    error: TelemetryClientLogFunction
  }
  Metric: {
    gauge: TelemetryClientMetricFunction
    rate: TelemetryClientMetricFunction
    count: TelemetryClientMetricFunction
  }
  Event: {
    networkRequest: NetworkRequestEventFunction
    pageLoadingTime: PageLoadingTimeEventFunction
    runtimeError: RuntimeErrorEventFunction
    memoryUsage: MemoryUsageEventFunction
  }
  setDefaultTag: TelemetryClientSetDefaultTagFunction
  removeDefaultTag: TelemetryClientRemoveDefaultTagFunction
  setDefaultAttribute: TelemetryClientSetDefaultAttributeFunction
  removeDefaultAttribute: TelemetryClientRemoveDefaultAttributeFunction
  bypassDownsampling: TelemetryClientBypassDownsamplingFunction
}

const telemetry: Telemetry = {
  Log: {
    info(eventName, message, options) {
      if (!isTelemetryEnabled()) return false
      return telemetryClientInstance.Log.info(eventName, message, options)
    },
    warn(eventName, message, options) {
      if (!isTelemetryEnabled()) return false
      return telemetryClientInstance.Log.warn(eventName, message, options)
    },
    error(eventName, message, options) {
      if (!isTelemetryEnabled()) return false
      return telemetryClientInstance.Log.error(eventName, message, options)
    }
  },
  Metric: {
    gauge(metricName, value, options) {
      if (!isTelemetryEnabled()) return false
      return telemetryClientInstance.Metric.gauge(metricName, value, options)
    },
    rate(metricName, value, options) {
      if (!isTelemetryEnabled()) return false
      return telemetryClientInstance.Metric.rate(metricName, value, options)
    },
    count(metricName, value, options) {
      if (!isTelemetryEnabled()) return false
      return telemetryClientInstance.Metric.count(metricName, value, options)
    }
  },
  Event: {
    networkRequest(options) {
      if (!isTelemetryEnabled()) return false
      return telemetryClientInstance.Event.networkRequest(options)
    },
    pageLoadingTime() {
      if (!isTelemetryEnabled()) return false
      return telemetryClientInstance.Event.pageLoadingTime()
    },
    runtimeError(options, safeMode = true) {
      if (!isTelemetryEnabled()) return false
      return telemetryClientInstance.Event.runtimeError(options, safeMode)
    },
    memoryUsage(context, timeIndex) {
      if (!isTelemetryEnabled()) return
      return telemetryClientInstance.Event.memoryUsage(context, timeIndex)
    }
  },

  setDefaultTag(tagName, tagValue) {
    return telemetryClientInstance.setDefaultTag(tagName, tagValue)
  },
  removeDefaultTag(tagName) {
    return telemetryClientInstance.removeDefaultTag(tagName)
  },

  setDefaultAttribute(attrName, attrValue) {
    return telemetryClientInstance.setDefaultAttribute(attrName, attrValue)
  },
  removeDefaultAttribute(attrName) {
    return telemetryClientInstance.removeDefaultAttribute(attrName)
  },
  bypassDownsampling(enabled) {
    return telemetryClientInstance.bypassDownsampling(enabled)
  }
}

export default telemetry
