import { Decimal } from 'decimal.js'
import moment from 'moment-timezone'
import {
  Line,
  LineBreak,
  Logo,
  Text,
  TextBox
} from '@/services/printer/elements'
import {
  Alignment,
  LineType,
  TextSize,
  Typeface
} from '@/services/printer/enums'
import { TicketLabels } from '@/services/ticket/TicketLabels'
import CurrencyService from '@/services/currency/CurrencyService'
import TicketPreferences from '@/services/ticket/TicketPreferences'
import {
  OrderDetails,
  ProductDetails,
  ProductCustomization
} from '@/modules/store/data/resources/order-details'
import Order from 'models/Order'
import telemetry from '@/modules/telemetry/telemetry'

const LINE_BREAK = new LineBreak()

const RIGHT = Alignment.RIGHT
const CENTER = Alignment.CENTER
const LEFT = Alignment.LEFT

const GIANT_TEXT = 124
const XXX_LARGE = TextSize.XXX_LARGE
const XX_LARGE = TextSize.XX_LARGE
const X_LARGE = TextSize.X_LARGE
const X_SMALL = TextSize.X_SMALL
const LARGE = TextSize.LARGE
const MEDIUM = TextSize.MEDIUM
const BOLD = Typeface.BOLD

interface TicketOrder extends OrderDetails {
  shortCode: string
}

export class TicketHelper {
  private readonly order: TicketOrder

  constructor(
    orderDetails: OrderDetails,
    private labels: TicketLabels,
    private preferences: TicketPreferences
  ) {
    const shortCode = Order.getShortCode({
      code: orderDetails.orderInformation.code,
      pickupCode: orderDetails.orderInformation.pickupCode,
      storeTrackingCode: orderDetails.orderInformation.storeTrackingCode,
      ...preferences
    })
    this.order = { ...orderDetails, shortCode }
  }

  *logo() {
    yield new Logo()
    yield LINE_BREAK
  }

  *shortOrderCode() {
    if (this.order.shortCode) {
      const boxHeight = 164
      yield new Text(MEDIUM, CENTER, this.labels.pickupCode.toUpperCase())
      yield LINE_BREAK
      yield new TextBox(GIANT_TEXT, LEFT, this.order.shortCode, boxHeight)
      yield LINE_BREAK
    }
    if (this.order.customerDetails) {
      yield new Text(
        MEDIUM,
        LEFT,
        this.labels.clientName.toUpperCase().concat(': ')
      )
      yield new Text(MEDIUM, LEFT, `${this.order.customerDetails.name}\n`, BOLD)
    }
    yield new Text(MEDIUM, LEFT, `${this.labels.orderCode}: `.toUpperCase())
    yield new Text(
      MEDIUM,
      LEFT,
      `${this.order.orderInformation.code}\n\n`.toUpperCase(),
      BOLD
    )
  }

  *longOrderCode() {
    yield new Text(38, LEFT, `${this.labels.orderCode}:`.toUpperCase())
    yield LINE_BREAK
    yield new Text(42, LEFT, this.order.orderInformation.code, BOLD)
    yield LINE_BREAK
    yield LINE_BREAK
  }

  *priceBreakdown() {
    const {
      minBasketSurcharge,
      deliveryFee,
      discountsTotal
    } = this.order.paymentDetails

    yield* this.priceField(
      this.labels.productPrice,
      this.order.paymentDetails.orderTotalPrice
    )

    if (minBasketSurcharge) {
      yield* this.priceField(this.labels.minBasketSurcharge, minBasketSurcharge)
    }

    if (deliveryFee != null) {
      const realDeliveryFee = new Decimal(deliveryFee || 0).minus(
        minBasketSurcharge || 0
      )
      yield* this.priceField(
        this.labels.deliveryFee,
        realDeliveryFee.toNumber()
      )
    }

    if (Order.hasDiscount(this.order)) {
      yield* this.priceField(this.labels.discounts, -discountsTotal)
    }
  }

  *priceTotal() {
    const total = Order.getTotalPriceForTicket(this.order)
    const formattedTotal = CurrencyService.format(total.toNumber())

    yield new Text(XXX_LARGE, LEFT, `${this.labels.total}:`, BOLD)
    yield LINE_BREAK
    yield new Text(XXX_LARGE, RIGHT, `${formattedTotal}`, BOLD)
    yield LINE_BREAK
  }

  *timestamp() {
    yield LINE_BREAK
    const formattedDate = moment().format('YYYY-MM-DD HH:mm')
    yield new Text(X_SMALL, LEFT, formattedDate, BOLD)
    yield LINE_BREAK
  }

  *newClient() {
    if (this.order.orderInformation.isFirstOrder) {
      yield new Text(LARGE, LEFT, `\n${this.labels.newClient}\n`, BOLD)
    }
  }

  *products() {
    const products = this.order.products
    yield new Line(LineType.DASH, TextSize.SMALL)
    let first = true
    for (const product of products) {
      if (!first) {
        yield LINE_BREAK
      }
      first = false
      yield* this.product(product)
    }
    yield new Line(LineType.DASH, TextSize.SMALL)
  }

  *cutlery() {
    const cutlery = this.order.orderInformation.isCutleryRequested
    if (cutlery == null) return
    yield LINE_BREAK
    if (cutlery) {
      yield new Text(LARGE, LEFT, `${this.labels.cutleryRequested}`, BOLD)
    } else {
      yield new Text(LARGE, LEFT, `${this.labels.cutleryNotRequested}`, BOLD)
    }
    yield LINE_BREAK
    yield LINE_BREAK
    yield new Line(LineType.DASH, TextSize.SMALL)
  }

  *allergyInfo() {
    if (!this.order.orderInformation.allergyInfo) {
      return
    }
    yield new Line(LineType.DASH, TextSize.SMALL)
    yield new Text(MEDIUM, LEFT, `${this.labels.allergyInfo}\n`, BOLD)
    yield new Text(MEDIUM, LEFT, `${this.order.orderInformation.allergyInfo}\n`)
    yield new Line(LineType.DASH, TextSize.SMALL)
  }

  *companyInfo() {
    if (!this.order.companyDetails) {
      return
    }
    yield new Line(LineType.DOUBLE_DOT, X_SMALL)
    yield new Text(MEDIUM, LEFT, `${this.labels.customerWantInvoice}:\n\n`)
    yield new Text(MEDIUM, LEFT, `${this.order.companyDetails.name}\n`, BOLD)
    yield new Text(MEDIUM, LEFT, `${this.order.companyDetails.addressLine1}\n`)
    if (this.order.companyDetails.addressLine2 !== '') {
      yield new Text(
        MEDIUM,
        LEFT,
        `${this.order.companyDetails.addressLine2}\n`
      )
    }
    yield new Text(
      MEDIUM,
      LEFT,
      `${this.labels.companyTaxId}: ${this.order.companyDetails.taxId}\n`
    )
    yield new Line(LineType.DOUBLE_DOT, X_SMALL)
  }

  *customerInfo() {
    yield new Line(LineType.DOUBLE_DOT, X_SMALL)
    if (this.order.deliveryAddressDetails) {
      yield* this.field(
        this.labels.deliveryAddress,
        this.order.deliveryAddressDetails.label
      )
    }

    if (this.order.customerDetails) {
      yield LINE_BREAK
      yield* this.field(
        this.labels.customerPhone,
        this.order.customerDetails.phoneNumber
      )
      yield LINE_BREAK
      yield* this.field(
        this.labels.customerName,
        this.order.customerDetails.name
      )
    }
    yield new Line(LineType.DOUBLE_DOT, X_SMALL)
  }

  *paymentMethod() {
    if (Order.paymentMethodIsCash(this.order)) {
      yield new TextBox(XX_LARGE, CENTER, this.labels.payByCash)
    }
  }

  *customerCashAmount() {
    if (
      this.order.paymentDetails.customerCashPaymentAmount &&
      this.order.paymentDetails.customerCashPaymentAmount > 0
    ) {
      yield LINE_BREAK
      yield new Text(X_SMALL, LEFT, this.labels.customerCashAmount)
      yield LINE_BREAK
    }
    return
  }

  *estimation() {
    const pickupTimeRange = this.order.statusDetails.pickupTimeRange
    if (!pickupTimeRange) {
      return
    }

    telemetry.Log.info(
      'ETA_RANGE_PICKUP',
      `Eta range pickup: ${pickupTimeRange.start} - ${pickupTimeRange.end}`,
      {
        attr: {
          orderId: this.order.id
        }
      }
    )

    yield new Text(30, LEFT, this.labels.pickupEstimation.toUpperCase(), BOLD)
    yield LINE_BREAK
    yield new Text(
      X_LARGE,
      LEFT,
      `${pickupTimeRange.start} - ${pickupTimeRange.end}`,
      BOLD
    )
    yield new Text(X_SMALL, LEFT, '\n')
  }

  private *priceField(label: string, price: number) {
    yield new Text(LARGE, LEFT, `${label}:`)
    yield LINE_BREAK
    yield new Text(LARGE, RIGHT, `${CurrencyService.format(price)}`)
    yield LINE_BREAK
  }

  private *product(product: ProductDetails) {
    yield new Text(X_LARGE, LEFT, `${product.quantity}`, BOLD)
    yield new Text(MEDIUM, LEFT, ` x ${product.name}\n`)
    yield* this.customizations(product.customizations)
  }

  private *customizations(customizations: ProductCustomization[] | null) {
    if (customizations === null || customizations.length < 1) {
      return
    }
    for (const customization of customizations) {
      yield* this.customization(customization)
    }
  }

  private *customization(customization: ProductCustomization) {
    const quantityLine =
      customization.quantity > 1 ? ` x ${customization.quantity}` : ''
    yield new Text(MEDIUM, LEFT, ` > ${customization.name}${quantityLine}`)
    yield LINE_BREAK
  }

  private *field(title: string, detail: string) {
    yield new Text(MEDIUM, Alignment.LEFT, `${title}:`)
    yield LINE_BREAK
    yield new Text(X_LARGE, Alignment.LEFT, detail, BOLD)
    yield LINE_BREAK
  }
}
