import { analyticsEvents } from '../analytics-library'
import { StoreAddressId, StoreAdminId } from '@/modules/store/data/resources'
import { OrderId } from '@/modules/store'

// TD Task added to fix ts-ignore - https://glovoapp.atlassian.net/browse/PAR-2254

export const orderDetailsEvents = {
  onAcceptOrderClicked(
    orderId: OrderId,
    storeAddressId: StoreAddressId,
    storeAdminId: StoreAdminId
  ): void {
    const { OrderAcceptedPressed } = analyticsEvents
    // @ts-ignore
    this.trackMParticleEvent(
      new OrderAcceptedPressed(orderId, storeAddressId, storeAdminId)
    )
  }
}
