import { Store } from 'vuex'
import { State } from '@/modules/store'
import { RequestInterceptor } from '@/modules/http'
import { GET_TOKEN_INFORMATION_GETTER } from '../store/getters'
import { TokenInformation } from '../store/state'

export function authorizationHeaderInterceptor(
  store: Store<State>
): RequestInterceptor {
  return requestConfig => {
    const token: TokenInformation | null =
      store.getters[`authentication/${GET_TOKEN_INFORMATION_GETTER}`]
    if (token !== null) {
      const accessToken = token.accessToken
      return {
        ...requestConfig,
        headers: { ...requestConfig.headers, Authorization: accessToken }
      }
    }
    return requestConfig
  }
}
