import { Module } from 'vuex'
import { getters } from './getters'
import { mutations } from './mutations'
import { actionsFactory } from './actions'
import { initialState as state } from './state'
import type { State } from './state'
import type { State as RootState } from '@/modules/store'
import type { AbstractFetchService } from '@/modules/http'
import type { AbstractLiveOrdersService } from '@/modules/orders/services/live-orders'

export type { State }
export * from './action-types'

export function storeFactory(
  liveOrdersService: AbstractLiveOrdersService,
  fetchService: AbstractFetchService
): Module<State, RootState> {
  const namespaced = true
  return {
    namespaced,
    getters,
    actions: actionsFactory(liveOrdersService, fetchService),
    mutations,
    state
  }
}
