import { UndefinableError } from '@/../types/any'
import {
  CreatePromotion,
  PromotionsList,
  PromotionQualityTiersListVM
} from '../models'
import { GetPromotionsPayload, UpdatePromotionPayload } from '../api/promotions'
import {
  PromotionDTO,
  PromotionsListDTO,
  PromotionQualityTiersListDTO,
  PromotionQualityTier,
  PromotionQualityRequestDTO
} from '../api/dtos'
import { GlvFormattedHttpError } from '@/modules/http'

export const CREATE_PROMOTION = 'CREATE_PROMOTION'
export type CreatePromotionPayload = CreatePromotion

export const CREATE_PROMOTION_SUCCESS = 'CREATE_PROMOTION_SUCCESS'
export type CreatePromotionSuccessPayload = PromotionDTO

export const CREATE_PROMOTION_FAILURE = 'CREATE_PROMOTION_FAILURE'
export type CreatePromotionFailurePayload = GlvFormattedHttpError

export const GET_PROMOTIONS = 'GET_PROMOTIONS'

export const UPDATE_PROMOTIONS_LOADING = 'UPDATE_PROMOTIONS_LOADING'
export type PromotionsLoadingPayload = boolean

export const GET_PROMOTIONS_SUCCESS = 'GET_PROMOTIONS_SUCCESS'
export type GetPromotionsSuccessPayload = PromotionsListDTO | null

export const GET_PROMOTIONS_FAILURE = 'GET_PROMOTIONS_FAILURE'
export type GetPromotionsFailurePayload = UndefinableError

export type GetPromotionsSuccessMutationPayload = PromotionsList

export const UPDATE_PROMOTION = 'UPDATE_PROMOTION'
export const UPDATE_PROMOTION_SUCCESS = 'UPDATE_PROMOTION_SUCCESS'

export const UPDATE_PROMOTION_FAILURE = 'UPDATE_PROMOTION_FAILURE'
export type UpdatePromotionFailurePayload = UndefinableError

export const UPDATE_NEWLY_CREATED_PROMOTION_ID =
  'UPDATE_NEWLY_CREATED_PROMOTION_ID'
export type UpdateNewlyCreatedPromotionIdPayload = number | null

export const GET_PROMOTION_QUALITY_TIERS = 'GET_PROMOTION_QUALITY_TIERS'
export const GET_PROMOTION_QUALITY_TIERS_SUCCESS =
  'GET_PROMOTION_QUALITY_TIERS_SUCCESS'
export type GetPromotionQualityTiersSuccessPayload = PromotionQualityTiersListVM | null

export const GET_PROMOTION_QUALITY_TIERS_FAILURE =
  'GET_PROMOTION_QUALITY_TIERS_FAILURE'
export type GetPromotionQualityTiersFailurePayload = UndefinableError

export const GET_PROMOTION_QUALITY = 'GET_PROMOTION_QUALITY'

export const GET_PROMOTION_QUALITY_SUCCESS = 'GET_PROMOTION_QUALITY_SUCCESS'
export type GetPromotionQualitySuccessPayload = PromotionQualityTier | null

export const GET_PROMOTION_QUALITY_FAILURE = 'GET_PROMOTION_QUALITY_FAILURE'
export type GetPromotionQualityFailurePayload = UndefinableError

export const UPDATE_PROMOTION_QUALITY_LOADING =
  'UPDATE_PROMOTION_QUALITY_LOADING'

export type Action =
  | {
      type: typeof CREATE_PROMOTION
      payload: CreatePromotionPayload
    }
  | {
      type: typeof CREATE_PROMOTION_SUCCESS
      payload: CreatePromotionSuccessPayload
    }
  | {
      type: typeof CREATE_PROMOTION_FAILURE
      payload: CreatePromotionFailurePayload
    }
  | {
      type: typeof GET_PROMOTIONS
      payload: GetPromotionsPayload
    }
  | {
      type: typeof GET_PROMOTIONS_SUCCESS
      payload: GetPromotionsSuccessPayload
    }
  | {
      type: typeof GET_PROMOTIONS_FAILURE
      payload: GetPromotionsFailurePayload
    }
  | {
      type: typeof UPDATE_PROMOTIONS_LOADING
      payload: PromotionsLoadingPayload
    }
  | {
      type: typeof UPDATE_PROMOTION
      payload: UpdatePromotionPayload
    }
  | { type: typeof UPDATE_PROMOTION_SUCCESS; payload: UpdatePromotionPayload }
  | {
      type: typeof UPDATE_PROMOTION_FAILURE
      payload: UpdatePromotionFailurePayload
    }
  | {
      type: typeof UPDATE_NEWLY_CREATED_PROMOTION_ID
      payload: UpdateNewlyCreatedPromotionIdPayload
    }
  | {
      type: typeof GET_PROMOTION_QUALITY_TIERS
      payload: PromotionQualityTiersListDTO
    }
  | {
      type: typeof GET_PROMOTION_QUALITY_TIERS_SUCCESS
      payload: GetPromotionQualityTiersSuccessPayload
    }
  | {
      type: typeof GET_PROMOTION_QUALITY_TIERS_FAILURE
      payload: GetPromotionQualityTiersFailurePayload
    }
  | {
      type: typeof GET_PROMOTION_QUALITY
      payload: PromotionQualityRequestDTO
    }
  | {
      type: typeof GET_PROMOTION_QUALITY_SUCCESS
      payload: GetPromotionQualitySuccessPayload
    }
  | {
      type: typeof GET_PROMOTION_QUALITY_FAILURE
      payload: GetPromotionQualityFailurePayload
    }
  | {
      type: typeof UPDATE_PROMOTION_QUALITY_LOADING
      payload: PromotionsLoadingPayload
    }
