import { AbstractFetchService } from '@/modules/http'
import { InvoiceDTO } from '@/api/dtos'
import { host } from '@/api/localConfig'
import { Invoice } from '@/modules/invoices/types'
import { StoreAddressId } from '@/modules/store/data/resources'
interface FetchInvoicesRequestPayload {
  storeAddressIds: StoreAddressId[]
  yearMonth: string
}
export default function (fetch: AbstractFetchService) {
  return {
    fetchInvoices: async ({
      storeAddressIds,
      yearMonth
    }: FetchInvoicesRequestPayload) => {
      const invoiceDtos = await fetch.get<InvoiceDTO[]>(
        `${host}/store_addresses/invoices`,
        { yearMonth, storeAddressIds },
        null
      )
      return invoiceDtos as Invoice[]
    }
  }
}
