import Categories from '@/models/Categories'
import {
  INIT_CATEGORIES,
  ENABLE_CATEGORIES
} from '@/modules/store/services/mutation-types'
import { safeCall } from '@/modules/shared'

const state = {
  categories: null
}

const getters = {
  categories: _state =>
    safeCall(() => _state.categories.getEnabledCategories()) || [],
  categoryWithName: _state => categoryName =>
    safeCall(() => _state.categories.getCategoryWithName(categoryName)),
  defaultCategory: _state =>
    safeCall(() => _state.categories.getDefaultCategory()) || null
}

const actions = {
  initCategories({ commit }) {
    commit(INIT_CATEGORIES, new Categories())
  },

  enableCategory({ commit }, categoryName) {
    commit(ENABLE_CATEGORIES, categoryName)
  }
}

const mutations = {
  [INIT_CATEGORIES](_state, categories) {
    _state.categories = categories
  },
  [ENABLE_CATEGORIES](_state, categoryName) {
    _state.categories.enableCategoryWithName(categoryName)
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}
