import * as types from '@/modules/store/services/mutation-types'
import * as displays from '@/modules/store/services/display-types'
import forEach from 'lodash/forEach'

const state = {
  online: true,
  display: {},
  referrer: {
    name: 'outter',
    params: {}
  }
}

const getters = {
  display: state => state.display,
  referrer: state => state.referrer,
  isDesktop: state => state.display.desktop,
  online: state => state.online
}

const actions = {
  displayState({ commit }, displayState) {
    const desktop = window.innerWidth > 900
    let mutation = displayState.mutation
    const isMutationDesktop = displayState.desktop

    if (desktop && !isMutationDesktop) {
      mutation += '_DESKTOP'
    } else if (!desktop && isMutationDesktop) {
      const tempMutation = displayState.mutation.split('_')
      tempMutation.pop()
      mutation = tempMutation.join('_')
    }
    commit(mutation, displays[mutation])
  },
  updateOnlineStatus({ commit }, online = false) {
    commit(types.UPDATE_ONLINE_STATUS, online)
  }
}

const _mutations = {
  [types.REFERRER_CHANGED](state, referrer) {
    state.referrer = referrer
  },
  [types.UPDATE_ONLINE_STATUS](state, online) {
    state.online = online
  }
}
forEach(displays, displ => {
  _mutations[displ.mutation] = function (state, displayState) {
    state.display = displayState
  }
})

const mutations = _mutations

export default {
  state,
  getters,
  actions,
  mutations
}
