
import Component from 'vue-class-component'
import Vue from 'vue'
import { Prop } from 'vue-property-decorator'
import { Size, BadgeBackgroundColor } from '../../types'

@Component({ name: 'Glovo-Badge' })
export default class GlovoBadge extends Vue {
  @Prop({ default: '' })
  size!: Size
  @Prop({ default: BadgeBackgroundColor.DARK_GREY })
  color!: BadgeBackgroundColor

  get cssClasses() {
    return [this.size, `badge--${this.color}`]
  }
}
