
import Vue from 'vue'
import Component from 'vue-class-component'
import { Prop } from 'vue-property-decorator'
import GlovoConfirmation from 'common/ui/GlovoConfirmation.vue'
import { StoreAction } from '@/modules/store/types/store'
import { orderManagement } from '@/modules/orders/store/namespace'
import { ProductDetails } from '@/modules/store/data/resources/order-details'
import { OrderId } from '@/modules/store'
import { StoreAddressId } from '@/modules/store/data/resources'
import {
  RemoveProductPayload,
  REMOVE_PRODUCT,
  RESTORE_EDIT_PRODUCT_QUANTITY
} from '@/modules/orders/store'
import DisableProduct from '../disable/DisableProduct.vue'

@Component({
  name: 'RemoveProduct',
  components: {
    GlovoConfirmation,
    DisableProduct
  }
})
export default class RemoveProduct extends Vue {
  @Prop() orderId!: OrderId
  @Prop() storeAddressId!: StoreAddressId
  @Prop() product!: ProductDetails
  shouldShowDisableProductPopup: boolean = false
  productWasDisabled: boolean = false

  @orderManagement.Action(REMOVE_PRODUCT)
  removeProduct!: StoreAction<RemoveProductPayload>

  @orderManagement.Action(RESTORE_EDIT_PRODUCT_QUANTITY)
  restoreRemoveProductFromOrder!: StoreAction<void>

  showUndoBanner() {
    const message = this.productWasDisabled
      ? this.$t('product.removed-and-disabled').toString()
      : this.$t('product.removed').toString()
    this.$toast.show({
      message,
      actionLabel: this.$t('common.undo').toString(),
      onActionClick: () => this.restoreRemoveProductFromOrder()
    })
  }

  async removeProductFromOrder() {
    await this.removeProduct({
      orderId: this.orderId,
      storeAddressId: this.storeAddressId,
      product: this.product,
      hasAlsoBeenDisabledInMenu: this.productWasDisabled
    })
    this.showUndoBanner()
    this.close()
  }

  showDisableProductConfirmation() {
    this.shouldShowDisableProductPopup = true
  }

  handleProductStockout() {
    this.productWasDisabled = true
    this.removeProductFromOrder()
  }

  close() {
    this.$emit('close')
  }
}
