import { Store } from 'vuex'
import { State } from '@/modules/store'
import { RequestInterceptor } from '@/modules/http'
import { GET_TOGGLES_GETTER } from '@/modules/feature-toggles'
import { oldTokenRefreshInterceptor } from '@/modules/authentication/interceptors/old-token-refresh-interceptor'
import { newTokenRefreshInterceptor } from '@/modules/authentication/interceptors/new-token-refresh-interceptor'

export function tokenRefreshInterceptor(
  store: Store<State>
): RequestInterceptor {
  return async requestConfig => {
    const isImprovedLogicEnabled =
      store.getters[GET_TOGGLES_GETTER] &&
      store.getters[GET_TOGGLES_GETTER].FE_PARTNERS_LOG_OUT_MITIGATION_ENABLED

    if (isImprovedLogicEnabled) {
      return newTokenRefreshInterceptor(store)(requestConfig)
    }
    return oldTokenRefreshInterceptor(store)(requestConfig)
  }
}
