
import Vue from 'vue'
import Component from 'vue-class-component'
import { Prop } from 'vue-property-decorator'
import Icon from 'common/Icon.vue'
import GlovoBtn from 'common/GlovoBtn.vue'
import {
  storeManagement,
  ConfirmCheckInPayload,
  CONFIRM_CHECK_IN,
  DismissCheckInPayload,
  DISMISS_CHECK_IN,
  SHOW_STORE_CLOSING_MODAL
} from '@/modules/store-management/store'
import {
  GET_HAS_DISMISSING_CHECK_IN_FAILED_GETTER,
  GET_IS_ALLOWED_TO_CHOOSE_CLOSING_TYPE_GETTER,
  GET_IS_CHECK_IN_CONFIRMATION_LOADING_GETTER,
  GET_IS_OPEN_GETTER,
  GET_IS_START_OF_SHIFT_GETTER
} from '@/modules/store-management/store/getters'
import { StoreAction } from '@/modules/store/types/store'
import { StoreAddressId, StoreAdmin } from '../../../store/data/resources'
import { ToBeMigrated } from 'types/any'
import { Action } from 'vuex-class'
import { authentication } from '@/modules/authentication/store/namespace'
import {
  GET_ADMIN_ROUTES_GETTER,
  GET_CURRENT_STORE_ADMIN_GETTER,
  IS_ADMIN_MODE_GETTER
} from '@/modules/authentication'
import { RestrictedView } from '@/modules/store/data/resources/restricted-view'
import { DISPLAY_CHECK_IN } from '@/modules/store/services/display-types'
import { getDeviceId } from '@/modules/shared/helpers'

@Component({
  name: 'CheckInOverlay',
  components: { Icon, GlovoBtn }
})
export default class CheckInOverlay extends Vue {
  @Prop() storeAddressId!: StoreAddressId

  @authentication.Getter(IS_ADMIN_MODE_GETTER) isAdmin!: boolean
  @authentication.Getter(GET_ADMIN_ROUTES_GETTER) adminRoutes!: RestrictedView[]
  @authentication.Getter(GET_CURRENT_STORE_ADMIN_GETTER)
  storeAdmin!: StoreAdmin

  @storeManagement.Getter(GET_IS_ALLOWED_TO_CHOOSE_CLOSING_TYPE_GETTER)
  isAllowedToChooseClosingType!: boolean

  @storeManagement.Getter(GET_IS_OPEN_GETTER)
  isStoreOpen!: boolean

  @storeManagement.Getter(GET_IS_START_OF_SHIFT_GETTER)
  isStartOfShift!: boolean

  @storeManagement.Getter(GET_HAS_DISMISSING_CHECK_IN_FAILED_GETTER)
  hasDismissingCheckInFailed!: boolean

  @storeManagement.Getter(GET_IS_CHECK_IN_CONFIRMATION_LOADING_GETTER)
  isCheckInConfirmationLoading!: boolean

  @storeManagement.Action(CONFIRM_CHECK_IN) confirmCheckIn!: StoreAction<
    ConfirmCheckInPayload
  >

  @storeManagement.Action(DISMISS_CHECK_IN) dismissCheckIn!: StoreAction<
    DismissCheckInPayload
  >

  @storeManagement.Action(SHOW_STORE_CLOSING_MODAL)
  showStoreClosingModal!: StoreAction<void>

  @Action('displayState') displayState!: ToBeMigrated

  @Action('changeModalDisplay') changeModalDisplay: ToBeMigrated

  get getCheckInText(): string {
    return this.isStoreOpen
      ? 'check.in.store.open.text'
      : 'check.in.store.closed.text'
  }

  get getButtonText(): string {
    return this.isAllowedToChooseClosingType
      ? 'check.in.button.close.temp.text'
      : 'check.in.button.close.text'
  }

  get deviceId(): string {
    return getDeviceId()
  }

  checkIn(): void {
    this.confirmCheckIn(this.storeAddressId)
  }

  mounted() {
    this.displayState(DISPLAY_CHECK_IN)
    // TS Ignore comment to be taken care of in task - https://glovoapp.atlassian.net/browse/PAR-2254
    // @ts-ignore
    this.$analytics.onCheckInOverlayShown(
      this.storeAddressId,
      this.storeAdmin.id,
      this.deviceId,
      this.isStartOfShift
    )
    this.$telemetry.Log.info('check-in', 'check-in-shown')
  }

  async closeStore() {
    await this.dismissCheckIn(this.storeAddressId)

    if (this.isAllowedToChooseClosingType) {
      if (!this.hasDismissingCheckInFailed) {
        this.redirectToStoreClosingPage()
      }
    }
  }

  redirectToStoreClosingPage() {
    const isStoreClosingARestrictedView = this.adminRoutes.includes(
      'close_store'
    )
    const hasPermissions = this.isAdmin || !isStoreClosingARestrictedView
    if (hasPermissions) {
      this.changeModalDisplay({ key: 'CLOSE', display: true })
    } else {
      this.changeModalDisplay({ key: 'PERMISSIONS', display: true })
      this.showStoreClosingModal()
    }
  }
}
