import { ONE_HOUR_IN_SECONDS } from '@/modules/core/helpers'

export const REFRESH_META_TAG_ELEMENT_ID = 'refresh-meta-tag'
export const DEFAULT_APP_REFRESHING_INTERVAL = ONE_HOUR_IN_SECONDS

export function updateMetaRefreshContent(content: string) {
  const refreshMetaTag = getMetaRefreshTag()
  if (refreshMetaTag) {
    refreshMetaTag.setAttribute('content', content)
  }
}

export function readMetaRefreshInterval(): number | undefined {
  const refreshMetaTag = getMetaRefreshTag()
  if (refreshMetaTag) {
    const interval = Number(refreshMetaTag.content.split(';')[0])
    if (interval) return interval
  }
}

function getMetaRefreshTag(): HTMLMetaElement {
  return document.getElementById(REFRESH_META_TAG_ELEMENT_ID) as HTMLMetaElement
}
