import {
  Action,
  SET_NUMBER_OF_ORDERS_REQUESTED,
  SetNumberOfOrdersRequestedPayload
} from '@/modules/benchmarking/store/action-types'
import {
  SET_IS_IN_BENCHMARKING_MODE,
  SetIsInBenchmarkingModePayload
} from '@/modules/benchmarking/store/action-types'
import { ActionContext } from 'glovo'
import telemetry from '@/modules/telemetry/telemetry'
import { getDeviceId } from '@/modules/shared/helpers'
import { BENCHMARKING_MODULE_NAME } from '@/modules/benchmarking/benchmarking-module'
import { GET_NUMBER_OF_ORDERS_REQUESTED } from '@/modules/benchmarking/store/getters'
import {
  DEVICE_ID_TAG,
  NUMBER_OF_ORDERS_REQUESTED_TAG
} from '@/modules/telemetry/constants'
import { FIVE_SECONDS_IN_MILLIS } from '@/modules/core/helpers'

const INITIAL_METRICS_DELAY = FIVE_SECONDS_IN_MILLIS

const actions = {
  [SET_IS_IN_BENCHMARKING_MODE](
    { commit, rootGetters }: ActionContext<Action>,
    payload: SetIsInBenchmarkingModePayload
  ) {
    const numberOfOrdersRequested =
      rootGetters[
        `${BENCHMARKING_MODULE_NAME}/${GET_NUMBER_OF_ORDERS_REQUESTED}`
      ]
    if (payload === true) {
      setDefaultTelemetryTags(numberOfOrdersRequested)
      telemetry.bypassDownsampling(true)
      setTimeout(emitBenchmarkingInitialMetrics, INITIAL_METRICS_DELAY)
    } else {
      removeDefaultTelemetryTags()
      telemetry.bypassDownsampling(false)
    }
    commit(SET_IS_IN_BENCHMARKING_MODE, payload)
  },
  [SET_NUMBER_OF_ORDERS_REQUESTED](
    { commit }: ActionContext<Action>,
    numberOfOrders: SetNumberOfOrdersRequestedPayload
  ) {
    telemetry.setDefaultTag(NUMBER_OF_ORDERS_REQUESTED_TAG, numberOfOrders)
    commit(SET_NUMBER_OF_ORDERS_REQUESTED, numberOfOrders)
  }
}

function emitBenchmarkingInitialMetrics() {
  if (navigator.deviceMemory) {
    telemetry.Metric.gauge('device_memory', navigator.deviceMemory)
  }
  if (performance.timing) {
    const elapsed =
      (Date.now() - performance.timing.domLoading - INITIAL_METRICS_DELAY) /
      1000
    telemetry.Metric.gauge('application_loading', elapsed)
  }
  if (performance.memory) {
    const initialMemory = Math.round(
      (performance.memory.usedJSHeapSize * 100) /
        performance.memory.jsHeapSizeLimit
    )
    telemetry.Metric.gauge('initial_used_memory_relative', initialMemory)
  }
}

function setDefaultTelemetryTags(numberOfOrdersRequested: number) {
  telemetry.setDefaultTag(DEVICE_ID_TAG, getDeviceId())
  telemetry.setDefaultTag(
    NUMBER_OF_ORDERS_REQUESTED_TAG,
    numberOfOrdersRequested
  )
}

function removeDefaultTelemetryTags() {
  telemetry.removeDefaultTag(DEVICE_ID_TAG)
  telemetry.removeDefaultTag(NUMBER_OF_ORDERS_REQUESTED_TAG)
}

export default actions
