
import Vue from 'vue'
import Component from 'vue-class-component'
import { Action, Getter } from 'vuex-class'
import { authentication } from '@/modules/authentication/store/namespace'
import { storeManagement } from '@/modules/store-management/store/namespace'
import TabsMenu from './TabsMenu.vue'
import ConnectivityBanner from '@/components/main/top/ConnectivityBanner.vue'
import Icon from '../../common/Icon.vue'
import {
  IS_ADMIN_MODE_GETTER,
  GET_AVAILABLE_STORE_ADDRESS_IDS_GETTER,
  GET_ADMIN_ROUTES_GETTER
} from '@/modules/authentication'

const Categories = () => import('@/components/main/navigation/Categories.vue')
const StoreClosing = () =>
  import('@/modules/store-management/components/StoreClosing.vue')
const StoreWidget = () => import('@/components/common/StoreWidget.vue')
const PermissionsModal = () =>
  import('@/components/main/popups/PermissionsPopup.vue')
import {
  GET_IS_OPEN_GETTER,
  HIDE_STORE_CLOSING_MODAL
} from '@/modules/store-management/store'
import { StoreAddressId } from '@/modules/store/data/resources'
import { Category } from 'models/Category'
import { RestrictedView } from '@/modules/store/data/resources/restricted-view'
import { DisplayType } from '@/modules/store/services/display-types'
import { ToBeMigrated } from 'types/any'
import { StoreAction } from '@/modules/store/types/store'
import { GET_SHOULD_SHOW_STORE_CLOSING_MODAL_GETTER } from '@/modules/store-management/store/getters'

@Component({
  name: 'TopBar',
  components: {
    ConnectivityBanner,
    Icon,
    TabsMenu,
    Categories,
    StoreClosing,
    StoreWidget,
    PermissionsModal
  }
})
export default class TopBar extends Vue {
  // migrate these getters to the core getters PAR-1806
  @Getter('display') display!: DisplayType
  @Getter('closeModalDisplayed') closeModalDisplayed!: boolean
  @Getter('adminModalDisplayed') adminModalDisplayed!: boolean
  @Getter('permissionsModalDisplayed') permissionsModalDisplayed!: boolean
  @Getter('defaultCategory') defaultCategory!: Category
  // replace this getter with the GET_IS_ONLINE_GETTER ticket PAR-1806
  @Getter('isDesktop') isDesktop!: boolean

  @storeManagement.Getter(GET_IS_OPEN_GETTER) isOpen!: boolean
  @storeManagement.Getter(GET_SHOULD_SHOW_STORE_CLOSING_MODAL_GETTER)
  shouldShowStoreClosingModal!: boolean

  @authentication.Getter(IS_ADMIN_MODE_GETTER) isAdmin!: boolean
  @authentication.Getter(GET_AVAILABLE_STORE_ADDRESS_IDS_GETTER)
  availableStoreAddresses!: StoreAddressId[]
  @authentication.Getter(GET_ADMIN_ROUTES_GETTER) adminRoutes!: RestrictedView[]

  @Action('changeModalDisplay') changeModalDisplay: ToBeMigrated

  @storeManagement.Action(HIDE_STORE_CLOSING_MODAL)
  hideStoreClosingModal!: StoreAction<void>

  get hasClosePermissions() {
    return this.isAdmin || !this.adminRoutes.includes('close_store')
  }
  get isSingleStoreClosed() {
    return !this.isOpen && this.availableStoreAddresses.length === 1
  }

  changeCloseModalDisplay(display: boolean) {
    if (this.hasClosePermissions || !display) {
      this.changeModalDisplay({ key: 'CLOSE', display })
    } else {
      this.changePermissionsModalDisplay(true)
    }
  }

  changePermissionsModalDisplay(display: boolean) {
    this.changeModalDisplay({ key: 'PERMISSIONS', display })
  }

  onStoreClosingModalClose() {
    this.changeCloseModalDisplay(false)
    this.hideStoreClosingModal()
  }

  get _shouldShowStoreClosingModal() {
    return this.closeModalDisplayed || this.shouldShowStoreClosingModal
  }
}
