import { State } from './state'

export const CREATE_PROMOTION_ERROR_GETTER = 'CREATE_PROMOTION_ERROR_GETTER'
export const GET_PROMOTIONS_GETTER = 'GET_PROMOTIONS_GETTER'
export const GET_PROMOTIONS_ERROR_GETTER = 'GET_PROMOTIONS_ERROR_GETTER'
export const PROMOTIONS_LOADING_GETTER = 'PROMOTIONS_LOADING_GETTER'
export const UPDATE_PROMOTION_ERROR_GETTER = 'UPDATE_PROMOTION_ERROR_GETTER'
export const NEWLY_CREATED_PROMOTION_ID_GETTER =
  'NEWLY_CREATED_PROMOTION_ID_GETTER'

export const GET_PROMOTION_QUALITY_TIERS_GETTER =
  'GET_PROMOTION_QUALITY_TIERS_GETTER'
export const GET_PROMOTION_QUALITY_TIERS_ERROR_GETTER =
  'GET_PROMOTION_QUALITY_TIERS_ERROR_GETTER'
export const GET_PROMOTION_QUALITY_GETTER = 'GET_PROMOTION_QUALITY_GETTER'
export const PROMOTION_QUALITY_LOADING_GETTER =
  'PROMOTION_QUALITY_LOADING_GETTER'

export const getters = {
  [CREATE_PROMOTION_ERROR_GETTER](state: State) {
    return state.uiState.createPromotionApiError
  },
  [GET_PROMOTIONS_GETTER](state: State) {
    return state.dataState.promotions
  },
  [GET_PROMOTIONS_ERROR_GETTER](state: State) {
    return state.uiState.promotionsGetApiError
  },
  [PROMOTIONS_LOADING_GETTER](state: State) {
    return state.uiState.loading
  },
  [UPDATE_PROMOTION_ERROR_GETTER](state: State) {
    return state.uiState.promotionsUpdateApiError
  },
  [NEWLY_CREATED_PROMOTION_ID_GETTER](state: State) {
    return state.uiState.newlyCreatedPromotionId
  },
  [GET_PROMOTION_QUALITY_TIERS_GETTER](state: State) {
    return state.dataState.promotionQualityTiers
  },
  [GET_PROMOTION_QUALITY_TIERS_ERROR_GETTER](state: State) {
    return state.uiState.promotionQualityGetApiError
  },
  [GET_PROMOTION_QUALITY_GETTER](state: State) {
    return state.dataState.currentPromotionQualityTier
  },
  [PROMOTION_QUALITY_LOADING_GETTER](state: State) {
    return state.uiState.promotionQualityLoading
  }
}
