import { Vue as _Vue } from 'vue/types/vue'
import IdleVue from 'idle-vue'
import { Store } from 'vuex'

import { State } from '@/modules/store'
import { FIVE_MINUTES_IN_MILLIS } from '../helpers'
interface IdleVueOptions {
  store: Store<State>
}
export default function install(Vue: typeof _Vue, { store }: IdleVueOptions) {
  Vue.use(IdleVue, {
    store,
    idleTime: FIVE_MINUTES_IN_MILLIS
  })
}
