
import Vue from 'vue'
import Component from 'vue-class-component'
import { Prop } from 'vue-property-decorator'

@Component({
  name: 'ZoomedImageView'
})
export default class ZoomedImageView extends Vue {
  @Prop({ default: null }) src!: string | null
  @Prop({ default: false }) isVisible!: boolean
  @Prop({ default: '' }) title!: string

  closeDialog() {
    this.$emit('zoom-out')
  }
}
