import { Vue as _Vue } from 'vue/types/vue'
import VueI18n from 'vue-i18n'
import VueRouter from 'vue-router'
import { Store } from 'vuex'
import { GlvModule, GlvModuleOptions } from 'glovo'
import { assertDependencies } from '@/modules/shared'
import { State } from '@/modules/store'
import App from './components/App.vue'
import { library } from '@fortawesome/fontawesome-svg-core'
import {
  faAngleUp,
  faAngleDown,
  faComment,
  faCaretDown,
  faCaretRight,
  faMinusSquare,
  faSearch
} from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'
import { storeFactory } from './store'
import { State as RootState } from '@/modules/store'

import {
  installElementUI,
  installVueGtm,
  installVCalendar,
  installIdleVue,
  installVueLocalStorage,
  installErrorMessage,
  installVueGoogleMaps,
  installVueObserveVisibility,
  installVueSimplePortal,
  installToast,
  installPhoneFormatter
} from './plugins'
import {
  currency,
  formatHours,
  formatDate,
  dateFilter,
  formatMinutes
} from './filters'
import installStyles from './styles'

function configureFontAwesome() {
  library.add(faAngleUp)
  library.add(faAngleDown)
  library.add(faCaretDown)
  library.add(faComment)
  library.add(faMinusSquare)
  library.add(faCaretRight)
  library.add(faSearch)
}
export interface CoreModuleOptions extends GlvModuleOptions {
  gtmId?: string
  deps?: CoreModuleDependencies
}

export interface CoreModuleDependencies {
  i18n?: VueI18n
  router: VueRouter
  store: Store<State>
}

export class CoreModule implements GlvModule<CoreModuleOptions> {
  readonly name = 'core' // don't refactor into constant and export e.g. CORE_MODULE_NAME - tests start breaking otherwise
  components: Record<string, typeof _Vue> = { App }
  constructor(private store: Store<RootState>) {}
  install(Vue: typeof _Vue, options?: CoreModuleOptions): void {
    assertDependencies(options, ['i18n', 'router', 'store'])

    const { i18n, router, store } = options!.deps!

    if (options) {
      installElementUI(Vue, { i18n: i18n! })
      installVueGtm(Vue, {
        gtmId: options.gtmId,
        vueRouter: router!
      })
      installVCalendar(Vue, i18n!.locale)
      installVueGoogleMaps(Vue)
      installIdleVue(Vue, { store })
      installVueLocalStorage(Vue)
      installErrorMessage(Vue)
      installVueObserveVisibility(Vue)
      installVueSimplePortal(Vue)
      installToast(Vue)
      installPhoneFormatter(Vue)

      // FontAwesome
      configureFontAwesome()
      Vue.component('FontAwesomeIcon', FontAwesomeIcon)

      // Filters
      Vue.filter('currency', currency)
      Vue.filter('date', dateFilter)
      Vue.filter('formatHours', formatHours)
      Vue.filter('formatDate', formatDate)
      Vue.filter('formatMinutes', formatMinutes)

      // Global styles
      installStyles()

      // Store
      const coreStore = storeFactory()
      this.store.registerModule([this.name], coreStore)
    } else {
      throw new Error('Core module requires options!')
    }
  }
}
