import { host } from './localConfig'
import { MAX_RETRIES } from './networkConfig'
import {
  StoreAddressId,
  StoreAdminDTO,
  Timestamp,
  StoreAdminId
} from '@/modules/store/data/resources'
import { AbstractFetchService } from '@/modules/http'
import { ContactDetail } from '@/modules/store/data/resources/contactDetail'
import { Interval, IntervalWithReason } from 'models/Interval'
import { ClosingOption } from '@/modules/store-management/types'
import { CloseStoreAddressesTemporarilyPayload } from '@/modules/store-management/store/action-types'
import { NonEmptyArrayOf } from 'types/NonEmptyArrayOf'
import OpeningTimes from '@/modules/store/data/resources/opening-times'
export interface StatusResponse {
  forcedlyClosed: boolean
  generationTime: Timestamp
  nextClosingTime: Timestamp | null
  nextOpeningTime: Timestamp | null
  open: boolean
  storeAddressId: StoreAddressId
  checkIn: CheckIn
}

export interface CheckIn {
  isCheckInPending: boolean
  isAllowedToChooseClosingType: boolean
  shouldPlayNotification: boolean
  isStartOfShift: boolean
}

export interface ClosedStatusResponse {
  closedStoreAddressIds: StoreAddressId[]
}

export interface ClosingDaysResponse {
  days: DayDTO[]
}

export interface TemporaryClosingOptionsResponse {
  untilOptions: ClosingOption[]
  reasonOptions: ClosingOption[]
}

export interface DayDTO {
  start: string
  end: string
  reason: string
}

export interface StatusUpdateRequestPayload {
  closedUntil: Timestamp | null
  reason?: string
}

export interface OpenStoreAddressesRequestPayload {
  storeAddressIds: NonEmptyArrayOf<StoreAddressId>
}

export interface RequestSupportPayload {
  changes: string | null
  description: string
  name: string | null
  problem: string
  selectedId: number
  subject: string
}

export interface MarkCommunicationAsViewedPayload {
  communicationId: number
}

export interface CommunicationPayload {
  storeAddressId: StoreAddressId
  storeAdminId: StoreAdminId
}

export interface CommunicationResponse {
  id: number
  title: string
  bodyMessage: string
  linkText: string
  linkUrl: string
  countryCode: string
}

export default function (fetch: AbstractFetchService) {
  return {
    me: () =>
      fetch.get<StoreAdminDTO>(
        `${host}/me`,
        undefined,
        undefined,
        undefined,
        MAX_RETRIES
      ),
    getFeatureToggles: (name: string) =>
      fetch.get(`${host}/feature_toggles/${name}`),
    requestSupport: (
      storeAddressId: StoreAddressId,
      data: RequestSupportPayload
    ) =>
      fetch.post<void>(
        `${host}/store_addresses/${storeAddressId}/support`,
        null,
        data
      ),
    status: (storeAddressId: StoreAddressId) =>
      fetch.get<StatusResponse>(
        `${host}/store_addresses/${storeAddressId}/status`
      ),
    statusUpdate: (
      storeAddressId: StoreAddressId,
      data: StatusUpdateRequestPayload // TODO we can delete this when old code is migrated, since you can pass a single store address id to the statusUpdateByAddressIds
    ) =>
      fetch.put(`${host}/store_addresses/${storeAddressId}/status`, null, data),
    statusUpdateByAddressIds: (
      storeAddressIds: StoreAddressId[],
      data: StatusUpdateRequestPayload
    ) => fetch.put(`${host}/store_addresses/status`, { storeAddressIds }, data),
    confirmCheckIn: (storeAddressId: StoreAddressId) =>
      fetch.post<void>(`${host}/store_addresses/${storeAddressId}/checkin`),
    dismissCheckIn: (storeAddressId: StoreAddressId) =>
      fetch.post<void>(
        `${host}/store_addresses/${storeAddressId}/checkin/dismiss`
      ),
    closingDays: (storeAddressId: StoreAddressId) =>
      fetch.get<ClosingDaysResponse>(
        `${host}/store_addresses/${storeAddressId}/closingdays`,
        null
      ),
    openStoreAddresses: (payload: OpenStoreAddressesRequestPayload) =>
      fetch.remove(`${host}/store_addresses/close`, null, payload),
    getInvoicingContactDetailsByStoreAddressId: (
      storeAddressId: StoreAddressId
    ) =>
      fetch.get<ContactDetail>(
        `${host}/store_addresses/${storeAddressId}/invoicing/contact`
      ),
    setInvoicingContactDetailsByStoreAddressId: (
      storeAddressId: StoreAddressId,
      contact: ContactDetail
    ) =>
      fetch.put(
        `${host}/store_addresses/${storeAddressId}/invoicing/contact`,
        null,
        contact
      ),
    getBusinessContactDetailsByStoreAddressId: (
      storeAddressId: StoreAddressId
    ) =>
      fetch.get<ContactDetail>(
        `${host}/store_addresses/${storeAddressId}/business/contact`
      ),
    setBusinessContactDetailsByStoreAddressId: (
      storeAddressId: StoreAddressId,
      contact: ContactDetail
    ) =>
      fetch.put(
        `${host}/store_addresses/${storeAddressId}/business/contact`,
        null,
        contact
      ),
    getDailyOperationContactDetailsByStoreAddressId: (
      storeAddressId: StoreAddressId
    ) =>
      fetch.get<ContactDetail>(
        `${host}/store_addresses/${storeAddressId}/daily_operations/contact`
      ),
    setDailyOperationContactDetailsByStoreAddressId: (
      storeAddressId: StoreAddressId,
      contact: ContactDetail
    ) =>
      fetch.put(
        `${host}/store_addresses/${storeAddressId}/daily_operations/contact`,
        null,
        contact
      ),
    setClosingDay: (storeAddressId: StoreAddressId, data: IntervalWithReason) =>
      fetch.post<void>(
        `${host}/store_addresses/${storeAddressId}/closingdays`,
        null,
        data
      ),
    getTemporaryClosingOptions: () =>
      fetch.get<TemporaryClosingOptionsResponse>(
        `${host}/store_addresses/close/options`
      ),
    closeTemporarily: (payload: CloseStoreAddressesTemporarilyPayload) =>
      fetch.post(`${host}/store_addresses/close`, null, payload),
    getClosedStoreAddressIds: (storeAddressIds: StoreAddressId[]) =>
      fetch.get<ClosedStatusResponse>(
        `${host}/store_addresses/closed_status`,
        { storeAddressIds },
        null
      ),
    setClosingDaysByAddressIds: (
      storeAddressIds: StoreAddressId[],
      interval: IntervalWithReason
    ) =>
      fetch.post(
        `${host}/store_addresses/closingdays`,
        { storeAddressIds },
        interval
      ),
    updateSchedule: (storeAddressId: StoreAddressId, schedule: OpeningTimes) =>
      fetch.put(
        `${host}/store_addresses/${storeAddressId}/schedule`,
        null,
        schedule
      ),
    removeClosingDay: (storeAddressId: StoreAddressId, interval: Interval) =>
      fetch.remove(
        `${host}/store_addresses/${storeAddressId}/closingdays/${interval.start}/${interval.end}`
      ),
    communication: (storeAddressId: StoreAddressId) =>
      fetch.get<CommunicationResponse>(
        `${host}/store_addresses/${storeAddressId}/communication`,
        null
      ),
    markCommunicationAsViewed: (
      storeAddressId: StoreAddressId,
      data: MarkCommunicationAsViewedPayload
    ) =>
      fetch.post<void>(
        `${host}/store_addresses/${storeAddressId}/communication/view`,
        null,
        data
      )
  }
}
