
import Component from 'vue-class-component'
import Vue from 'vue'
import { Prop } from 'vue-property-decorator'
import { OrderDetails } from '@/modules/store/data/resources/order-details'
import Order from 'models/Order'

@Component({ name: 'Countdown' })
export default class Countdown extends Vue {
  @Prop() order!: OrderDetails

  get isArriving() {
    return (
      Order.isCourierArriving(this.order) ||
      this.order.statusDetails.minutesUntilPickup === 0
    )
  }

  get shouldShowMinutesUntilPickup() {
    return Order.shouldShowMinutesUntilPickup(this.order)
  }
}
