
import {
  IS_ADMIN_MODE_GETTER,
  GET_AVAILABLE_STORE_ADDRESS_IDS_GETTER,
  GET_CURRENT_STORE_ADMIN_GETTER
} from '@/modules/authentication'
import {
  GET_IS_OPEN_GETTER,
  storeManagement,
  GET_IS_CHECK_IN_PENDING_GETTER
} from '@/modules/store-management/store'
import Component from 'vue-class-component'
import Vue from 'vue'
import { Action, Getter } from 'vuex-class'
import { authentication } from '@/modules/authentication/store/namespace'
import { NonEmptyArrayOf } from 'types/NonEmptyArrayOf'
import { StoreAddressId, StoreAdmin } from '@/modules/store/data/resources'
import {
  Category,
  SubCategory,
  INVOICES_SUBCATEGORY_NAME,
  DASHBOARD_SUBCATEGORY_NAME
} from 'models/Category'
import { GET_TOGGLES_GETTER } from '@/modules/feature-toggles'
import { FeatureToggle } from '@/modules/feature-toggles/store/state'
import {
  DisplayType,
  DISPLAY_ORDER_DETAILS,
  DISPLAY_STOCK_PRODUCTS
} from '@/modules/store/services/display-types'
import { ToBeMigrated } from 'types/any'
import Icon from '../../common/Icon.vue'
import { core } from '@/modules/core/store/namespace'
import { GET_IS_SEARCHING_GETTER } from '@/modules/core/store/getters'

@Component({
  name: 'Categories',
  components: {
    Icon
  }
})
export default class Categories extends Vue {
  @Getter('categories') categories!: Category[]
  @Getter('categoryWithName') categoryWithName!: (
    categoryName: Category['name']
  ) => Category
  @Getter('display') display!: DisplayType

  @Getter(GET_TOGGLES_GETTER) toggles!: Record<FeatureToggle, boolean>

  @storeManagement.Getter(GET_IS_OPEN_GETTER) isOpen!: boolean
  @storeManagement.Getter(GET_IS_CHECK_IN_PENDING_GETTER)
  isPendingCheckIn!: boolean

  @core.Getter(GET_IS_SEARCHING_GETTER)
  isSearchingForProductsOrAttributes!: boolean

  @authentication.Getter(IS_ADMIN_MODE_GETTER) isAdmin!: boolean

  @authentication.Getter(GET_AVAILABLE_STORE_ADDRESS_IDS_GETTER)
  availableStoreAddresses!: NonEmptyArrayOf<StoreAddressId>

  @authentication.Getter(GET_CURRENT_STORE_ADMIN_GETTER)
  storeAdmin?: StoreAdmin

  @Action('displayState') displayState!: ToBeMigrated

  get reportsCategories() {
    return this.categoryWithName('REPORTS')
  }

  get reportsCategoriesTypes() {
    let categories = this.reportsCategories.subCategories
    if (!this.shouldShowInvoicesSection) {
      categories = categories!.filter(
        ({ name }) => name !== INVOICES_SUBCATEGORY_NAME
      )
    }
    if (!this.shouldShowDashboardSection) {
      categories = categories!.filter(
        ({ name }) => name !== DASHBOARD_SUBCATEGORY_NAME
      )
    }
    return categories
  }

  get shouldShowDashboardSection() {
    return this.toggles.FE_PARTNERS_DASHBOARD_ENABLED
  }

  get shouldShowInvoicesSection() {
    return this.storeAdmin && this.storeAdmin.areInvoicesEnabled
  }

  get isSingleStoreClosed() {
    return !this.isOpen && this.availableStoreAddresses.length === 1
  }

  get shouldShowPendingCheckinTooltip() {
    return (
      this.isPendingCheckIn &&
      this.toggles.FE_PARTNERS_WEB_CHECK_IN_ENABLED &&
      this.display.category !== 'ORDERS'
    )
  }

  goToCategory(category: Category) {
    this.$router.push({ name: category.route })
  }

  goToReports(subCategory: SubCategory) {
    this.displayState(subCategory.display)
    this.$router.push({ name: subCategory.route })
  }

  get isInStockPage() {
    return this.display.category === DISPLAY_STOCK_PRODUCTS.category
  }

  get isInOrderDetailsPage() {
    return this.display.category === DISPLAY_ORDER_DETAILS.category
  }

  get isHiddenForMobile() {
    return (
      this.isInOrderDetailsPage ||
      (this.isInStockPage && this.isSearchingForProductsOrAttributes)
    )
  }
}
