import {
  AttributeId,
  Product,
  ProductId,
  RequestPriceUpdateType,
  StoreAdmin,
  StoreCollectionSectionVM,
  StoreContent,
  StoresAddressesMap,
  CitiesMap,
  StoresMap
} from './resources'
import { OrderTypes, State } from './state'
import isEqual from 'lodash/isEqual'
import cloneDeep from 'lodash/cloneDeep'
import {
  ADD_ATTRIBUTES,
  ADD_CITIES,
  ADD_PRODUCTS,
  ADD_REQUEST_PRICE_UPDATES,
  ADD_STORE_ADDRESSES,
  ADD_STORE_ADMINS,
  ADD_STORE_CONTENT,
  ADD_STORES,
  AddAttributesPayload,
  AddCitiesPayload,
  AddProductsPayload,
  AddRequestPriceUpdatesPayload,
  AddStoreAddressesPayload,
  AddStoreAdminsPayload,
  AddStoreContentPayload,
  AddStoresPayload,
  MARK_CANCELLED_ORDER_AS_ACKNOWLEDGED,
  MARK_COURIER_NEARBY_AS_NOTIFIED,
  MARK_ORDER_AS_LOADED,
  MARK_ORDER_AS_VIEWED,
  MarkCancelledOrderAsAcknowledged,
  MarkOrderAsLoadedPayload,
  MarkOrderAsViewedPayload,
  REMOVE_REQUEST_PRICE_UPDATE,
  RemoveRequestPriceUpdatesPayload,
  UPDATE_ATTRIBUTES,
  UpdateAttributesPayload,
  MARK_IS_GROCERIES_CHECKBOX_CHECKED,
  MarkIsGroceriesCheckboxCheckedPayload,
  SET_IS_ORDER_READY,
  SetIsOrderReadyPayload,
  MARK_ORDER_IS_READY_BUTTON_AS_VIEWED,
  MarkOrderIsReadyButtonAsViewedPayload,
  ADD_ORDERS,
  AddOrdersPayload,
  MarkCourierNearbyAsNotifiedPayload
} from './action-types'

import telemetry from '@/modules/telemetry/telemetry'
import _Store from '@/modules/store'
import { BENCHMARKING_MODULE_NAME } from '@/modules/benchmarking/benchmarking-module'
import { GET_IS_IN_BENCHMARKING_MODE_GETTER } from '@/modules/benchmarking/store/getters'
import { PreparationStage } from '@/modules/orders/constants'
import { CancelledOrder } from '@/modules/store/data/resources/orders-list/order-types'
const NULLABLE_STORE_COLLECTION_GROUP_ID = 0

export const mutations = {
  [ADD_ORDERS](state: State, payload: AddOrdersPayload) {
    const t1 = Date.now() // for metrics
    const newState = { ...state.orders }
    payload.forEach(order => {
      const orderInState = state.orders[order.id]
      if (orderInState) {
        newState[order.id] = combineOrders(orderInState, order)
      } else {
        newState[order.id] = order
      }
    })
    state.orders = isEqual(newState, state.orders) ? state.orders : newState
    recordOrdersProcessingTime(t1)
  },
  [MARK_CANCELLED_ORDER_AS_ACKNOWLEDGED](
    state: State,
    orderId: MarkCancelledOrderAsAcknowledged
  ) {
    const order = state.orders[orderId] as CancelledOrder
    if (order) {
      order.isCancellationAcknowledged = true
    }
  },
  [MARK_ORDER_AS_VIEWED](state: State, orderId: MarkOrderAsViewedPayload) {
    const order = state.orders[orderId]
    if (order) {
      order.statusDetails.isNotificationPixelViewed = true
    }
  },
  [MARK_ORDER_AS_LOADED](state: State, orderId: MarkOrderAsLoadedPayload) {
    const order = state.orders[orderId]
    if (order) {
      order.statusDetails.isNotificationPixelLoaded = true
    }
  },
  [MARK_ORDER_IS_READY_BUTTON_AS_VIEWED](
    state: State,
    orderId: MarkOrderIsReadyButtonAsViewedPayload
  ) {
    const order = state.orders[orderId]
    if (order) {
      order.statusDetails.isOrderReadyButtonViewed = true
    }
  },
  [ADD_STORE_ADMINS](state: State, payload: AddStoreAdminsPayload) {
    const storeAdminsState: Record<number, StoreAdmin> = {}
    payload.forEach(storeAdmin => {
      storeAdminsState[storeAdmin.id] = cloneDeep(storeAdmin)
    })
    state.storeAdmins = { ...state.storeAdmins, ...storeAdminsState }
  },
  [ADD_PRODUCTS](state: State, payload: AddProductsPayload) {
    const productState: Record<ProductId, Product> = {}
    payload.forEach(product => {
      productState[product.id] = cloneDeep(product)
    })
    state.products = { ...state.products, ...productState }
  },
  [ADD_CITIES](state: State, payload: AddCitiesPayload) {
    const citiesState: CitiesMap = {}
    payload.forEach(city => {
      citiesState[city.code] = cloneDeep(city)
    })
    state.cities = { ...state.cities, ...citiesState }
  },
  [ADD_STORES](state: State, payload: AddStoresPayload) {
    const storesState: StoresMap = {}
    payload.forEach(store => {
      storesState[store.id] = cloneDeep(store)
    })
    state.stores = { ...state.stores, ...storesState }
  },
  [ADD_STORE_ADDRESSES](state: State, payload: AddStoreAddressesPayload) {
    const storeAddressesState: StoresAddressesMap = {}
    payload.forEach(storeAddress => {
      storeAddressesState[storeAddress.id] = cloneDeep(storeAddress)
    })
    state.storeAddresses = { ...state.storeAddresses, ...storeAddressesState }
  },
  [ADD_STORE_CONTENT](state: State, payload: AddStoreContentPayload) {
    state.storeCollectionGroups = storeCollectionGroupMutation(payload)
    state.products = contentProductsMutation(state.products, payload)
    state.storeCollections = storeCollectionMutation(
      state.storeCollections,
      payload
    )
    state.storeCollectionSections = storeCollectionSectionMutation(
      state.storeCollectionSections,
      payload
    )
  },
  [ADD_REQUEST_PRICE_UPDATES](
    state: State,
    payload: AddRequestPriceUpdatesPayload
  ) {
    const newState = cloneDeep(state.requestPriceUpdates)
    payload.forEach(request => {
      newState[request.id] = request
      if (request.type === RequestPriceUpdateType.PRODUCT) {
        state.products[request.itemId] = {
          ...cloneDeep(state.products[request.itemId]),
          requestedPriceUpdateId: request.id
        }
      } else if (request.type === RequestPriceUpdateType.ATTRIBUTE) {
        state.attributes.byId[request.itemId] = {
          ...cloneDeep(state.attributes.byId[request.itemId]),
          requestedPriceUpdateId: request.id
        }
      }
    })
    state.requestPriceUpdates = newState
  },
  [REMOVE_REQUEST_PRICE_UPDATE](
    state: State,
    payload: RemoveRequestPriceUpdatesPayload
  ) {
    const newState = { ...state.requestPriceUpdates }
    if (newState[payload]) {
      delete newState[payload]
      state.requestPriceUpdates = newState
    }
  },
  [ADD_ATTRIBUTES](
    state: State,
    { attributes, storeAddressId }: AddAttributesPayload
  ) {
    const newByIdState = cloneDeep(state.attributes.byId)
    const ids: AttributeId[] = []
    attributes.forEach(attribute => {
      newByIdState[attribute.id] = attribute
      ids.push(attribute.id)
    })
    state.attributes.byId = newByIdState
    state.attributes.byStoreAddressId[storeAddressId] = ids
  },
  [UPDATE_ATTRIBUTES](state: State, attributes: UpdateAttributesPayload) {
    const newByIdState = cloneDeep(state.attributes.byId)
    attributes.forEach(attribute => {
      newByIdState[attribute.id] = attribute
    })
    state.attributes.byId = newByIdState
  },
  [MARK_COURIER_NEARBY_AS_NOTIFIED](
    state: State,
    orderId: MarkCourierNearbyAsNotifiedPayload
  ) {
    const order = state.orders[orderId]
    if (order) {
      order.statusDetails.hasPlayedCourierNearby = true
    }
  },
  [MARK_IS_GROCERIES_CHECKBOX_CHECKED](
    state: State,
    payload: MarkIsGroceriesCheckboxCheckedPayload
  ) {
    const product = state.products[payload.id]
    if (product) {
      product.isGroceriesCheckboxChecked = payload.isChecked
    }
  },
  [SET_IS_ORDER_READY](
    state: State,
    { orderId, storeAddressId }: SetIsOrderReadyPayload
  ) {
    const order = state.orders[orderId]
    const isCorrectOrder = order && order.storeAddressId === storeAddressId
    if (isCorrectOrder) {
      order.statusDetails.isReady = true
    }
  }
}

export function combineOrders(
  orderInState: OrderTypes,
  newOrder: OrderTypes
): OrderTypes {
  const oldStatus = orderInState.statusDetails
  const newStatus = newOrder.statusDetails
  const preparationStage = getPreparationStage(
    oldStatus.preparationStage,
    newStatus.preparationStage
  )
  const isReady = getOrderFieldPreferringOldStateValue(
    oldStatus.isReady,
    newStatus.isReady
  )
  const hasPlayedCourierNearby = getOrderFieldPreferringOldStateValue(
    oldStatus.hasPlayedCourierNearby,
    newStatus.hasPlayedCourierNearby
  )
  const isNotificationPixelLoaded = getOrderFieldPreferringOldStateValue(
    oldStatus.isNotificationPixelLoaded,
    newStatus.isNotificationPixelLoaded
  )
  const isNotificationPixelViewed = getOrderFieldPreferringOldStateValue(
    oldStatus.isNotificationPixelViewed,
    newStatus.isNotificationPixelViewed
  )
  const isOrderReadyButtonViewed = getOrderFieldPreferringOldStateValue(
    oldStatus.isOrderReadyButtonViewed,
    newStatus.isOrderReadyButtonViewed
  )
  const copiedOrder = {
    ...orderInState,
    ...cloneDeep(newOrder)
  }
  return {
    ...copiedOrder,
    statusDetails: {
      ...copiedOrder.statusDetails,
      preparationStage,
      isReady,
      hasPlayedCourierNearby,
      isNotificationPixelLoaded,
      isNotificationPixelViewed,
      isOrderReadyButtonViewed
    }
  } as OrderTypes
}

function getPreparationStage(
  orderInStateStage: PreparationStage,
  newOrderStage: PreparationStage
): PreparationStage {
  // Avoids orders going from accepted -> pending acceptance, see https://glovoapp.atlassian.net/browse/PAR-865
  if (
    orderInStateStage === PreparationStage.ACCEPTED_BY_PARTNER &&
    newOrderStage === PreparationStage.ACCEPTANCE_PENDING
  ) {
    return PreparationStage.ACCEPTED_BY_PARTNER
  }
  return newOrderStage
}

function getOrderFieldPreferringOldStateValue(
  orderInStateField: boolean | undefined,
  newOrderField: boolean | undefined
): boolean {
  // Avoid changing a boolean value once it's true
  if (orderInStateField) {
    return orderInStateField
  }
  return newOrderField || false
}

export function storeCollectionGroupMutation(
  payload: AddStoreContentPayload
): State['storeCollectionGroups'] {
  const storeCollectionGroupState: State['storeCollectionGroups'] = {}
  const { storeContent } = payload
  storeContent.collections.forEach(
    ({ id, title, image, icon, collections }) => {
      if (id === null) {
        id = NULLABLE_STORE_COLLECTION_GROUP_ID
      }
      storeCollectionGroupState[id] = {
        id: id!,
        title: title!,
        image: image!,
        icon: icon!,
        collections: collections.map(({ id }) => id),
        storeAddressId: payload.storeAddressId
      }
    }
  )
  return storeCollectionGroupState
}

export function storeCollectionMutation(
  state: State['storeCollections'],
  payload: AddStoreContentPayload
): State['storeCollections'] {
  const storeCollectionState = { ...state }
  const { storeContent, storeAddressId } = payload
  storeContent.collections.forEach(({ collections }) => {
    collections.forEach(collection => {
      storeCollectionState[collection.id] = {
        ...collection,
        storeAddressId,
        sections: collection.sections.map(section => section.id)
      }
    })
  })

  return storeCollectionState
}

export function storeCollectionSectionMutation(
  state: State['storeCollectionSections'],
  payload: AddStoreContentPayload
): State['storeCollectionSections'] {
  const sections = getSectionsFromStoreContent(payload.storeContent)
  const newState = { ...state }
  sections.forEach(section => {
    newState[section.id] = {
      ...section,
      products: section.products.map(({ id }) => id)
    }
  })
  return newState
}

export function contentProductsMutation(
  state: State['products'],
  payload: AddStoreContentPayload
): State['products'] {
  const products = ([] as Product[]).concat.apply(
    [],
    getSectionsFromStoreContent(payload.storeContent).map(
      ({ products }) => products
    )
  )
  const newState = { ...state }
  products.forEach(product => {
    newState[product.id] = cloneDeep(product)
  })
  return newState
}

function getSectionsFromStoreContent(
  content: StoreContent
): StoreCollectionSectionVM[] {
  return content.collections
    .map(collection => collection.collections)
    .reduce((acc, collections) => {
      const sections = ([] as StoreCollectionSectionVM[]).concat.apply(
        [],
        collections.map(({ sections }) => sections)
      )
      return acc.concat(sections)
    }, [] as StoreCollectionSectionVM[])
}

function recordOrdersProcessingTime(t1: number) {
  // TODO : move the metrics emission to the ADD_ORDERS action
  // Tech-debt ticket : https://glovoapp.atlassian.net/browse/PAR-1142
  const t2 = Date.now()
  const elapsed = t2 - t1
  if (elapsed && isInBenchmarkingMode()) {
    telemetry.Metric.gauge('active_orders_processing_time', elapsed)
  }
}

function isInBenchmarkingMode() {
  return _Store.store.getters[
    `${BENCHMARKING_MODULE_NAME}/${GET_IS_IN_BENCHMARKING_MODE_GETTER}`
  ]
}
