import User from '@/models/User'
import Storage from '@/models/Storage'

export class BeforeEnterService {
  user: User
  accessToken: string | null = null
  refreshToken: string | null = null
  expiresIn: number | null = null

  constructor() {
    this.user = new User()
    this.accessToken = null
    this.refreshToken = null
    this.expiresIn = null
  }

  setToken(oauth: { accessToken: string }) {
    this.accessToken = oauth.accessToken
    Storage.set(Storage.items.AUTH, oauth.accessToken)
    Storage.set(Storage.items.OAUTH, oauth)
  }

  setTokenFromStorage() {
    this.accessToken = Storage.get(Storage.items.AUTH)
    const storedOauth = Storage.get(Storage.items.OAUTH)
    if (storedOauth) {
      this.refreshToken = storedOauth.refreshToken
      this.expiresIn = storedOauth.expiresIn
    }

    if (!this.accessToken && storedOauth) {
      this.accessToken = storedOauth.accessToken
    }
  }

  reset() {
    this.accessToken = null
    this.refreshToken = null
    this.expiresIn = null
    Storage.remove(Storage.items.COPIES)
  }

  getOAuth() {
    return Storage.get(Storage.items.OAUTH)
  }

  getToken() {
    return this.accessToken
  }

  getLocale() {
    return Storage.get(Storage.items.LANG, 'en')
  }

  getDevice() {
    return Storage.get(Storage.items.DEVICE)
  }

  setUser(user: User) {
    this.user = user
  }
}
