import { State } from '@/modules/core/store/state'
import _Store from '@/modules/store'
import { GET_TOGGLES_GETTER } from '@/modules/feature-toggles'
import { LoggedRequest } from './action-types'

export const GET_IS_ONLINE_GETTER = 'GET_IS_ONLINE_GETTER'
export const GET_IS_OFFLINE_BANNER_VISIBLE_GETTER =
  'GET_IS_OFFLINE_BANNER_VISIBLE'
export const GET_CONNECTIVITY_BANNER_GETTER = 'GET_CONNECTIVITY_BANNER_GETTER'
export const GET_IS_POOR_CONNECTIVITY_BANNER_VISIBLE_GETTER =
  'GET_IS_POOR_CONNECTIVITY_BANNER_VISIBLE_GETTER'
export const GET_IS_SEARCHING_GETTER = 'GET_IS_SEARCHING_GETTER'

export const POOR_CONNECTIVITY_MINIMUM_REQUESTS = 3 // Minimum total requests

export const getters = {
  [GET_IS_ONLINE_GETTER](state: State) {
    return state.dataState.isOnline
  },
  [GET_IS_OFFLINE_BANNER_VISIBLE_GETTER](state: State) {
    return state.uiState.isOfflineBannerVisible
  },
  [GET_IS_POOR_CONNECTIVITY_BANNER_VISIBLE_GETTER](state: State) {
    const POOR_CONNECTIVITY_THRESHOLD =
      _Store.store.getters[GET_TOGGLES_GETTER]
        .FE_PARTNERS_POOR_CONNECTIVITY_THRESHOLD

    const recentRequests = state.dataState.requestsLog
    const failedRequests = recentRequests.filter(
      (request: LoggedRequest) => !request.success
    )
    return (
      recentRequests.length >= POOR_CONNECTIVITY_MINIMUM_REQUESTS &&
      failedRequests.length / recentRequests.length >=
        POOR_CONNECTIVITY_THRESHOLD
    )
  },
  [GET_IS_SEARCHING_GETTER]({ uiState }: State) {
    return uiState.isSearching
  }
}
