import { Decimal } from 'decimal.js'
import { PreparationStage, OrderStatus } from '../modules/orders/constants'
import { Store } from '@/modules/store/data/resources'
import {
  OrderDetails,
  BundledOrder
} from '@/modules/store/data/resources/order-details'
import {
  AcceptedOrder,
  PendingOrder
} from '@/modules/store/data/resources/orders-list/order-types'
import { PaymentMethod } from '@/modules/store/data/resources/payment-method'
import StoreModule from '@/modules/store'
import { GET_TOGGLES_GETTER } from '@/modules/feature-toggles'

export default class Order {
  static paymentMethodIsCash(orderDetails: OrderDetails | null) {
    return (
      orderDetails &&
      orderDetails.paymentDetails.paymentMethod &&
      orderDetails.paymentDetails.paymentMethod === PaymentMethod.CASH
    )
  }

  static isPending(orderDetails: OrderDetails | null) {
    return Boolean(
      orderDetails &&
        orderDetails.statusDetails.preparationStage ===
          PreparationStage.ACCEPTANCE_PENDING
    )
  }

  static isAccepted(
    orderDetails: OrderDetails | AcceptedOrder | PendingOrder | null
  ) {
    return (
      orderDetails &&
      orderDetails.statusDetails.preparationStage ===
        PreparationStage.ACCEPTED_BY_PARTNER
    )
  }

  static isBundleAccepted(bundledOrder: BundledOrder | null) {
    return (
      bundledOrder &&
      bundledOrder.preparationStage === PreparationStage.ACCEPTED_BY_PARTNER
    )
  }

  static isEditable(orderDetails: OrderDetails) {
    return Order.isAccepted(orderDetails) && !Order.isCancelled(orderDetails)
  }

  static isFinished(orderDetails: OrderDetails | null) {
    return (
      orderDetails &&
      (orderDetails.statusDetails.currentStatus ===
        OrderStatus.DELIVERED_STATUS ||
        orderDetails.statusDetails.currentStatus ===
          OrderStatus.CANCELED_STATUS)
    )
  }

  static isBundled(
    orderDetails: OrderDetails | AcceptedOrder | PendingOrder | null
  ) {
    return (
      !!orderDetails &&
      !!orderDetails.orderInformation.bundledOrders &&
      orderDetails.orderInformation.bundledOrders.length > 0
    )
  }

  static isCancelled(orderDetails: OrderDetails | null) {
    return !!(
      orderDetails &&
      orderDetails.statusDetails.currentStatus ===
        OrderStatus.CANCELED_STATUS &&
      orderDetails.statusDetails.cancelReason
    )
  }

  static isCollectorNearby(orderDetails: OrderDetails | null) {
    return Boolean(orderDetails && orderDetails.statusDetails.isCollectorNearby)
  }

  static hasOriginalDescription(orderDetails: OrderDetails | null) {
    return (
      !!orderDetails &&
      !!orderDetails.orderInformation.originalDescription &&
      orderDetails.orderInformation.originalDescription.length > 0
    )
  }

  static hasEstimatedTimeOfPickup(orderDetails: OrderDetails | null) {
    if (
      !orderDetails ||
      !orderDetails.statusDetails.pickupEstimatedArrivalDiff
    ) {
      return false
    }
    return orderDetails.statusDetails.pickupEstimatedArrivalDiff > 0
  }

  static hasDiscount(orderDetails: OrderDetails | null) {
    return (
      !!orderDetails &&
      orderDetails.paymentDetails.discountsTotal != null &&
      orderDetails.paymentDetails.discountsTotal > 0
    )
  }

  static getTotalPriceForTicket(orderDetails: OrderDetails) {
    if (orderDetails.orderInformation.isMarketplace) {
      let total = new Decimal(orderDetails.paymentDetails.orderTotalPrice)

      // deliveryFee is composed of fee + min basket surcharge
      if (orderDetails.paymentDetails.deliveryFee != null) {
        total = total.plus(orderDetails.paymentDetails.deliveryFee)
      }

      if (Order.hasDiscount(orderDetails)) {
        total = total.minus(orderDetails.paymentDetails.discountsTotal)
      }
      return total
    }
    if (
      StoreModule.store &&
      StoreModule.store.getters[GET_TOGGLES_GETTER]
        .FE_PARTNERS_NEW_TOTAL_PRICE_FOR_TICKET
    ) {
      return new Decimal(orderDetails.paymentDetails.orderTotalPrice)
    }
    return new Decimal(orderDetails.paymentDetails.totalProductsPrice)
  }

  static isCourierArriving(
    orderDetails: OrderDetails | AcceptedOrder | PendingOrder
  ) {
    return orderDetails.statusDetails.isCollectorNearby
  }

  static shouldShowMinutesUntilPickup(orderDetails?: OrderDetails) {
    if (orderDetails && orderDetails.statusDetails.minutesUntilPickup != null) {
      return !isNaN(orderDetails.statusDetails.minutesUntilPickup)
    }
    return false
  }

  // Refactor in https://glovoapp.atlassian.net/browse/PAR-2527
  static getShortCode({
    code,
    pickupCode = '',
    storeTrackingCode = '',
    isPending = false,
    usesKitchenCode
  }: {
    code: string
    pickupCode?: string
    storeTrackingCode?: string
    isPending?: boolean
    usesKitchenCode: boolean
  }) {
    const { primary, secondary } = this.getDisplayCodes({
      code,
      pickupCode,
      storeTrackingCode,
      isPending,
      usesKitchenCode
    })
    const result = primary || secondary
    if (result === code) {
      return ''
    }
    return result
  }

  // Refactor in https://glovoapp.atlassian.net/browse/PAR-2527
  static getDisplayCodes({
    code,
    pickupCode,
    storeTrackingCode,
    isPending,
    usesKitchenCode
  }: {
    code: string
    pickupCode?: string
    storeTrackingCode?: string
    isPending?: boolean
    usesKitchenCode: boolean
  }): OrderDisplayCodes {
    if (isPending) {
      return this.makePendingDisplayCodes({
        code,
        pickupCode,
        usesKitchenCode
      })
    } else {
      return this.makeNonPendingDisplayCodes({
        code,
        pickupCode,
        storeTrackingCode,
        usesKitchenCode
      })
    }
  }

  // Refactor in https://glovoapp.atlassian.net/browse/PAR-2527
  private static makePendingDisplayCodes({
    code,
    pickupCode,
    usesKitchenCode
  }: {
    code: string
    pickupCode?: string
    usesKitchenCode: boolean
  }): OrderDisplayCodes {
    if (usesKitchenCode) {
      return makeDisplayCodes('', code)
    } else if (pickupCode) {
      return makeDisplayCodes(pickupCode.toString(), '')
    } else {
      return makeDisplayCodes('', code)
    }
  }

  // Refactor in https://glovoapp.atlassian.net/browse/PAR-2527
  private static makeNonPendingDisplayCodes({
    code,
    pickupCode,
    storeTrackingCode,
    usesKitchenCode
  }: {
    code: string
    pickupCode?: string
    storeTrackingCode?: string
    usesKitchenCode: boolean
  }): OrderDisplayCodes {
    if (usesKitchenCode) {
      const primary = storeTrackingCode ? storeTrackingCode! : code
      return makeDisplayCodes(primary, '')
    } else if (pickupCode) {
      return makeDisplayCodes(pickupCode.toString(), '')
    } else {
      return makeDisplayCodes(code, '')
    }
  }

  name = ''
  email = ''
  id = -1
  urn = ''
  store: Store | {} = {}
}
// Refactor in https://glovoapp.atlassian.net/browse/PAR-2527
const makeDisplayCodes = (
  primary: string,
  secondary: string
): OrderDisplayCodes => ({
  primary,
  secondary
})
// Refactor in https://glovoapp.atlassian.net/browse/PAR-2527
export interface OrderDisplayCodes {
  primary: string
  secondary: string
}
