export const SET_IS_IN_BENCHMARKING_MODE = 'SET_IS_IN_BENCHMARKING_MODE'
export type SetIsInBenchmarkingModePayload = boolean

export const SET_NUMBER_OF_ORDERS_REQUESTED = 'SET_NUMBER_OF_ORDERS_REQUESTED'
export type SetNumberOfOrdersRequestedPayload = number

export type Action =
  | {
      type: typeof SET_IS_IN_BENCHMARKING_MODE
      payload: SetIsInBenchmarkingModePayload
    }
  | {
      type: typeof SET_NUMBER_OF_ORDERS_REQUESTED
      payload: SetNumberOfOrdersRequestedPayload
    }
