import { Vue as _Vue } from 'vue/types/vue'
import { GlovoToast, ToastProps } from '@/components/common/ui/toast'
import { clearPortalElement } from './vue-simple-portal'

interface ToastPlugin {
  show(options: ToastProps): GlovoToast
}

class ToastPluginImpl implements ToastPlugin {
  show(options: ToastProps) {
    clearPortalElement()
    return new GlovoToast({
      el: document.createElement('div'),
      propsData: options
    })
  }
}

export default function install(Vue: typeof _Vue) {
  Vue.prototype.$toast = new ToastPluginImpl()
}

declare module 'vue/types/vue' {
  interface Vue {
    $toast: ToastPlugin
  }
}
