
import Vue from 'vue'
import Component from 'vue-class-component'
import { Prop } from 'vue-property-decorator'
import debounce from 'lodash/debounce'
import { HALF_A_SECOND_IN_MILLIS } from '@/modules/core/helpers'
import { ElInput } from '@/modules/core/plugins/element-ui'
import { core } from '@/modules/core/store/namespace'
import { StoreAction } from '@/modules/store/types/store'
import {
  SET_IS_SEARCHING,
  SetIsSearchingPayload
} from '@/modules/core/store/action-types'

@Component({
  name: 'GlovoSearchField'
})
export default class GlovoSearchField extends Vue {
  @Prop({ default: '' }) value!: string

  @core.Action(SET_IS_SEARCHING)
  setIsSearching!: StoreAction<SetIsSearchingPayload>

  created() {
    this.emitInput = debounce(this.emitInput, HALF_A_SECOND_IN_MILLIS)
    window.addEventListener('scroll', this.hideKeyboard)
  }

  beforeDestroy() {
    if (this.searchTerm.length > 0) {
      this.clearInput()
    }
  }

  destroyed() {
    window.removeEventListener('scroll', this.hideKeyboard)
  }

  get searchTerm() {
    return this.value
  }
  set searchTerm(value) {
    this.emitInput(value)
  }

  get inputElement() {
    const gSearchField: ElInput = this.$refs.gSearchField as ElInput
    return gSearchField.$refs.input
  }

  get inputIcon() {
    return this.value ? 'circle-cross' : 'search'
  }

  clearInput() {
    this.$emit('input', '')
  }

  emitInput(value: string) {
    this.$emit('input', value)
  }

  hideKeyboard() {
    this.inputElement.blur()
  }

  onFocus() {
    this.setIsSearching(true)
  }

  onBlur() {
    if (this.searchTerm.length === 0) {
      this.setIsSearching(false)
    }
  }
}
