import { analytics } from '../analytics-library'
import mParticle from '@/modules/mparticle'

const { AnalyticsDelegate } = analytics
const { EventType } = analytics.events

// TD Task added to fix any types to proper typings - https://glovoapp.atlassian.net/browse/PAR-2254

const eventTypes = {
  [EventType.Other.name]: 8
}

export default class MParticleDelegate extends AnalyticsDelegate {
  constructor() {
    super()
  }

  event(name: any, data: any, type: any): void {
    const eventType = eventTypes[type.name]
    mParticle.logEvent(name, eventType, data)
  }
}
