<template>
  <div
    class="image placeholder"
    :style="{
      width: minWidth,
      height: minHeight,
      backgroundImage: source,
      backgroundRepeat: repeat
    }"
    :class="{
      image_fullSize: fullSize,
      shine: shine,
      image_margins: margins
    }"
  />
</template>

<script>
export default {
  name: 'Placeholder',
  props: {
    width: {
      type: Number,
      default: 320
    },
    height: {
      type: Number,
      default: 80
    },
    backgroundRepeat: {
      type: Boolean,
      default: false
    },
    fullWidth: {
      type: Boolean,
      default: false
    },
    fullSize: {
      type: Boolean,
      default: false
    },
    name: {
      type: String,
      default: 'glovo'
    },
    margins: {
      type: Boolean,
      default: false
    },
    shine: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      source: null,
      repeat: this.backgroundRepeat || 'no-repeat',
      imageSize: this.size || 100
    }
  },
  computed: {
    minWidth() {
      if (this.fullWidth) {
        return '100%'
      }

      return `${this.width}px`
    },
    minHeight() {
      return `${this.height}px`
    }
  },
  mounted() {
    this.init()
  },
  updated() {
    this.init()
  },
  methods: {
    init() {
      let _source = 'url('
      _source += require(`@/assets/images/placeholder/placeholder_${this.name}.svg`)
      _source += ')'
      this.source = _source
    }
  }
}
</script>

<style lang="scss" scoped>
.image {
  position: relative;
  background-position: left top;
  background-color: transparent;
  background-size: contain;
  width: 100%;
  &_bg {
    position: absolute;
    height: 100%;
  }
  &_fullSize {
    background-size: cover !important;
  }

  &_margins {
    margin: 20px 0;
  }
}

/**
   * Icon
   */

.shine {
  position: relative;
  overflow: hidden;
}

/**
   * The "shine" element
   */

.shine:after {
  animation: shine 3s ease-out infinite;
  animation-fill-mode: forwards;
  content: '';
  position: absolute;
  top: -50%;
  left: -20%;
  width: 10px;
  height: 250%;
  opacity: 0;
  transform: rotate(-30deg);

  background: rgba(255, 255, 255, 0.23);
  background: linear-gradient(
    to bottom,
    rgba(255, 255, 255, 0.13) 0%,
    rgba(255, 255, 255, 0.13) 77%,
    rgba(255, 255, 255, 0.4) 92%,
    rgba(255, 255, 255, 0) 100%
  );
}

/* Hover state - trigger effect */

/* Active state */

.shine:active:after {
  opacity: 0;
}

@keyframes shine {
  0% {
    opacity: 0.3;
    left: -20%;
    transition-property: left, opacity;
    transition-duration: 0.3s, 0.3s;
    transition-timing-function: ease-out;
  }
  100% {
    opacity: 0;
    left: 150%;
    transition-property: left, opacity;
  }
}
</style>
