import { Module } from 'vuex'
import { initialState, State } from '@/modules/benchmarking/store/state'
import { State as RootState } from '@/modules/store'
import actions from '@/modules/benchmarking/store/actions'
import { mutations } from '@/modules/benchmarking/store/mutations'
import { getters } from '@/modules/benchmarking/store/getters'

export function storeFactory(): Module<State, RootState> {
  return {
    namespaced: true,
    state: initialState,
    actions,
    mutations,
    getters
  }
}
