import telemetry from '@/modules/telemetry/telemetry'
import { ActionContext } from 'glovo'
import { GetProductsPayload, storesApiFactory } from '../api/stores-api'
import {
  Action,
  GET_STORE_PRODUCTS,
  GET_STORE_PRODUCTS_SUCCESS,
  SET_STORE_PRODUCTS_LOADING,
  GET_STORE_PRODUCTS_FAILURE,
  GetStoreProductsFailurePayload
} from './action-types'
import { getHttpErrorMessage } from '@/modules/http'

export function actionsFactory({
  getStoreProducts
}: ReturnType<typeof storesApiFactory>) {
  return {
    async [GET_STORE_PRODUCTS](
      { commit, dispatch }: ActionContext<Action>,
      payload: GetProductsPayload
    ) {
      const { storeId } = payload || {}
      if (!storeId) {
        return
      }

      try {
        commit(SET_STORE_PRODUCTS_LOADING, true)
        const productsListDTO = await getStoreProducts(payload)
        commit(GET_STORE_PRODUCTS_SUCCESS, productsListDTO)
      } catch (error) {
        dispatch(GET_STORE_PRODUCTS_FAILURE, error)
      } finally {
        commit(SET_STORE_PRODUCTS_LOADING, false)
      }
    },
    [GET_STORE_PRODUCTS_FAILURE](
      { commit }: ActionContext<Action>,
      error: GetStoreProductsFailurePayload
    ) {
      commit(GET_STORE_PRODUCTS_FAILURE, getHttpErrorMessage(error))
      telemetry.Log.error(GET_STORE_PRODUCTS_FAILURE, error.toString())
    }
  }
}
