import store from 'store'

export default {
  items: {
    OAUTH: 'glv_sa_oauth',
    AUTH: 'glv_auth_sa',
    LANG: 'glv_sa_lang',
    COPIES: 'glv_ticket_copies',
    ADDRESSES: 'glv_multi_sa',
    DEVICE: 'glv_device',
    BUSINESS_EVENTS_LOG_COUNTERS: 'glv_business_events_log_counters'
  },

  get<T = any | null>(key: string, defaultValue?: T): T {
    return store.get(key, defaultValue)
  },
  set<T = any | null>(key: string, value: T): T {
    return store.set(key, value)
  },
  remove(key: string) {
    return store.remove(key)
  }
}
