import {
  GET_STORE_ADDRESSES_GETTER,
  GET_STORES_GETTER
} from '@/modules/store/data/getters'
import telemetry from '@/modules/telemetry/telemetry'
import { ActionContext } from 'glovo'
import {
  GetPromotionsPayload,
  promotionsApiFactory,
  UpdatePromotionPayload
} from '../api/promotions'
import {
  Action,
  CREATE_PROMOTION,
  CREATE_PROMOTION_FAILURE,
  CREATE_PROMOTION_SUCCESS,
  CreatePromotionPayload,
  GET_PROMOTIONS,
  GET_PROMOTIONS_FAILURE,
  GET_PROMOTIONS_SUCCESS,
  GetPromotionsFailurePayload,
  GetPromotionsSuccessPayload,
  UPDATE_PROMOTION,
  UPDATE_PROMOTION_FAILURE,
  UpdatePromotionFailurePayload,
  UPDATE_PROMOTION_SUCCESS,
  UPDATE_PROMOTIONS_LOADING,
  UPDATE_NEWLY_CREATED_PROMOTION_ID,
  UpdateNewlyCreatedPromotionIdPayload,
  GET_PROMOTION_QUALITY_TIERS,
  GET_PROMOTION_QUALITY_TIERS_SUCCESS,
  GET_PROMOTION_QUALITY_TIERS_FAILURE,
  GET_PROMOTION_QUALITY,
  GET_PROMOTION_QUALITY_SUCCESS,
  GET_PROMOTION_QUALITY_FAILURE,
  UPDATE_PROMOTION_QUALITY_LOADING
} from './action-types'
import {
  mapCreatePromotionDTO,
  mapPromotionsList,
  mapPromotionQualityTiers,
  mapPromotionQuality
} from '../mappers'
import { UndefinableError } from 'types/any'
import { StoresMap } from '@/modules/store/data/resources'
import { PromotionsList } from '../models'
import {
  formatHttpErrorWithGenericMessage,
  getHttpErrorMessage
} from '@/modules/http'
import { PromotionQualityRequestDTO } from '../api/dtos'

export function actionsFactory({
  createPromotion,
  getPromotionsForStoreAddresses,
  updatePromotion,
  getPromotionQualityTiers,
  getPromotionQuality
}: ReturnType<typeof promotionsApiFactory>) {
  return {
    async [CREATE_PROMOTION](
      { commit, dispatch, rootGetters }: ActionContext<Action>,
      payload: CreatePromotionPayload
    ) {
      try {
        commit(UPDATE_PROMOTIONS_LOADING, true)
        const storeAddresses = rootGetters[GET_STORE_ADDRESSES_GETTER]
        const createPromoDTO = mapCreatePromotionDTO(payload, storeAddresses)
        const promotion = await createPromotion(createPromoDTO)
        commit(CREATE_PROMOTION_SUCCESS, promotion)
        dispatch(UPDATE_NEWLY_CREATED_PROMOTION_ID, promotion.id)
      } catch (error) {
        dispatch(CREATE_PROMOTION_FAILURE, error)
      } finally {
        commit(UPDATE_PROMOTIONS_LOADING, false)
      }
    },
    [CREATE_PROMOTION_FAILURE](
      { commit }: ActionContext<Action>,
      error: UndefinableError
    ) {
      commit(CREATE_PROMOTION_FAILURE, formatHttpErrorWithGenericMessage(error))
      telemetry.Log.error(CREATE_PROMOTION_FAILURE, error.toString())
    },
    async [GET_PROMOTIONS](
      { commit, dispatch }: ActionContext<Action>,
      payload: GetPromotionsPayload
    ) {
      const { storeAddressIds = [] } = payload || {}
      if (!Array.isArray(storeAddressIds) || storeAddressIds.length === 0) {
        dispatch(GET_PROMOTIONS_SUCCESS, null)
        return
      }

      try {
        commit(UPDATE_PROMOTIONS_LOADING, true)
        const promotionsListDTO = await getPromotionsForStoreAddresses(payload)
        dispatch(GET_PROMOTIONS_SUCCESS, promotionsListDTO)
      } catch (error) {
        dispatch(GET_PROMOTIONS_FAILURE, error)
      } finally {
        commit(UPDATE_PROMOTIONS_LOADING, false)
      }
    },
    [GET_PROMOTIONS_SUCCESS](
      { commit, rootGetters }: ActionContext<Action>,
      payload: GetPromotionsSuccessPayload
    ) {
      const allStores: StoresMap = rootGetters[GET_STORES_GETTER]
      const promotions: PromotionsList = mapPromotionsList(
        payload || { promotions: [] },
        allStores
      )
      commit(GET_PROMOTIONS_SUCCESS, promotions)
    },
    [GET_PROMOTIONS_FAILURE](
      { commit }: ActionContext<Action>,
      error: GetPromotionsFailurePayload
    ) {
      commit(GET_PROMOTIONS_FAILURE, getHttpErrorMessage(error))
      telemetry.Log.error(GET_PROMOTIONS_FAILURE, error.toString())
    },
    async [UPDATE_PROMOTION](
      { commit, dispatch }: ActionContext<Action>,
      payload: UpdatePromotionPayload
    ) {
      try {
        await updatePromotion(payload)
        commit(UPDATE_PROMOTION_SUCCESS, payload)
      } catch (error) {
        dispatch(UPDATE_PROMOTION_FAILURE, error)
      }
    },
    [UPDATE_PROMOTION_FAILURE](
      { commit }: ActionContext<Action>,
      error: UpdatePromotionFailurePayload
    ) {
      const formattedError = getHttpErrorMessage(error)
      commit(UPDATE_PROMOTION_FAILURE, formattedError)
      telemetry.Log.error(UPDATE_PROMOTION_FAILURE, error.toString())
      throw formattedError
    },
    [UPDATE_NEWLY_CREATED_PROMOTION_ID](
      { commit }: ActionContext<Action>,
      payload: UpdateNewlyCreatedPromotionIdPayload
    ) {
      commit(UPDATE_NEWLY_CREATED_PROMOTION_ID, payload)
    },
    async [GET_PROMOTION_QUALITY_TIERS]({ commit }: ActionContext<Action>) {
      try {
        const qualityTiersDTO = await getPromotionQualityTiers()
        const qualityTiers = mapPromotionQualityTiers(qualityTiersDTO)
        commit(GET_PROMOTION_QUALITY_TIERS_SUCCESS, qualityTiers)
      } catch (error) {
        commit(GET_PROMOTION_QUALITY_TIERS_FAILURE, getHttpErrorMessage(error))
        telemetry.Log.error(
          GET_PROMOTION_QUALITY_TIERS_FAILURE,
          error.toString()
        )
      }
    },
    async [GET_PROMOTION_QUALITY](
      { commit }: ActionContext<Action>,
      payload: PromotionQualityRequestDTO
    ) {
      try {
        commit(UPDATE_PROMOTION_QUALITY_LOADING, true)
        const promoQualityDTO = await getPromotionQuality(payload)
        const promoQuality =
          promoQualityDTO && mapPromotionQuality(promoQualityDTO)
        commit(GET_PROMOTION_QUALITY_SUCCESS, promoQuality)
      } catch (error) {
        commit(GET_PROMOTION_QUALITY_FAILURE, getHttpErrorMessage(error))
        telemetry.Log.error(GET_PROMOTION_QUALITY_FAILURE, error.toString())
      } finally {
        commit(UPDATE_PROMOTION_QUALITY_LOADING, false)
      }
    }
  }
}
