import mapValues from 'lodash/mapValues'
import { State } from './state'
import { State as RootState } from '@/modules/store/state'
import { Getters as RootGetters } from '@/modules/store'
import {
  GET_STORE_ADMINS_GETTER,
  GET_CITIES_GETTER,
  GET_STORE_ADDRESSES_GETTER,
  GET_STORES_GETTER
} from '@/modules/store/data/getters'
import {
  StoreAdmin,
  StoreAddressId,
  CityDTO,
  StoreAddressDTO,
  StoreDTO,
  CitiesMap,
  StoreAddress,
  City,
  StoresMap,
  StoresAddressesMap
} from '@/modules/store/data/resources'

export const GET_CURRENT_STORE_ADMIN_GETTER = 'GET_CURRENT_STORE_ADMIN_GETTER'
export const IS_ADMIN_MODE_GETTER = 'IS_ADMIN_MODE_GETTER'
export const GET_SELECTED_STORE_ADDRESS_IDS_GETTER =
  'GET_SELECTED_STORE_ADDRESS_IDS_GETTER'
export const GET_SELECTED_CITIES = 'GET_SELECTED_CITIES'
export const GET_AVAILABLE_STORE_ADDRESS_IDS_GETTER =
  'GET_AVAILABLE_STORE_ADDRESS_IDS_GETTER'
export const GET_ENABLED_STORE_ADDRESS_IDS_GETTER =
  'GET_ENABLED_STORE_ADDRESS_IDS_GETTER'
export const IS_AUTHENTICATED_GETTER = 'IS_AUTHENTICATED_GETTER'
export const GET_TOKEN_INFORMATION_GETTER = 'GET_TOKEN_INFORMATION_GETTER'
export const GET_TOKEN_EXPIRATION_TIME_GETTER =
  'GET_TOKEN_EXPIRATION_TIME_GETTER'
export const IS_REFRESH_IN_PROGRESS_GETTER = 'IS_REFRESH_IN_PROGRESS_GETTER'
export const GET_ADMIN_ROUTES_GETTER = 'GET_ADMIN_ROUTES_GETTER'
export const IS_MULTIVIEW_GETTER = 'IS_MULTIVIEW_GETTER'
export const IS_STORE_ADMIN_LOADED_GETTER = 'IS_STORE_ADMIN_LOADED_GETTER'

export const getters = {
  [GET_CURRENT_STORE_ADMIN_GETTER](
    state: State,
    localGetters: Getters,
    rootState: RootState,
    rootGetters: RootGetters
  ) {
    const { storeAdminId } = state
    if (storeAdminId === null) {
      return null
    } else {
      const storeAdmin = rootGetters[GET_STORE_ADMINS_GETTER][storeAdminId]
      return storeAdmin ? storeAdmin : null
    }
  },
  [IS_ADMIN_MODE_GETTER](state: State) {
    return state.isAdminMode
  },
  [GET_SELECTED_STORE_ADDRESS_IDS_GETTER](state: State) {
    return state.selectedStoreAddressIds
  },
  [GET_SELECTED_CITIES](
    state: State,
    localGetters: Getters,
    rootState: RootState,
    rootGetters: RootGetters
  ): CitiesMap {
    const selectedAddressIds =
      localGetters[GET_SELECTED_STORE_ADDRESS_IDS_GETTER] ?? []
    const citiesMap = rootGetters[GET_CITIES_GETTER] ?? {}
    const addressesMap = rootGetters[GET_STORE_ADDRESSES_GETTER] ?? {}
    const storesMap = rootGetters[GET_STORES_GETTER] ?? {}

    const selectedAddressesMap = getSelectedAddresses(
      addressesMap,
      selectedAddressIds
    )
    const selectedAddresses = Object.values(selectedAddressesMap)
    const selectedStoresMap = getSelectedStores(storesMap, selectedAddresses)
    const selectedCitiesMap = getSelectedCities(citiesMap, selectedAddresses)

    return mapValues(selectedCitiesMap, city =>
      mapSelectedCity(city, selectedStoresMap, selectedAddressesMap)
    )
  },
  [GET_AVAILABLE_STORE_ADDRESS_IDS_GETTER](
    _state: State,
    localGetters: Getters
  ) {
    const storeAdmin = localGetters[GET_CURRENT_STORE_ADMIN_GETTER]
    return storeAdmin === null
      ? []
      : getStoreAddressesFromStoreAdmin({
          storeAdmin,
          shouldFilterByEnabledState: false
        }).map(storeAddress => storeAddress.id)
  },
  [GET_ENABLED_STORE_ADDRESS_IDS_GETTER](_state: State, localGetters: Getters) {
    const storeAdmin = localGetters[GET_CURRENT_STORE_ADMIN_GETTER]
    return storeAdmin === null
      ? []
      : getStoreAddressesFromStoreAdmin({
          storeAdmin,
          shouldFilterByEnabledState: true
        }).map(storeAddress => storeAddress.id)
  },
  [IS_AUTHENTICATED_GETTER](state: State) {
    return !!state.tokenInformation
  },
  [GET_TOKEN_INFORMATION_GETTER](state: State) {
    return state.tokenInformation
  },
  [GET_TOKEN_EXPIRATION_TIME_GETTER](state: State) {
    return state.tokenExpirationTime
  },
  [IS_REFRESH_IN_PROGRESS_GETTER](state: State) {
    return state.refreshInProgress
  },
  [GET_ADMIN_ROUTES_GETTER](state: State, localGetters: Getters) {
    const storeAdmin = localGetters[GET_CURRENT_STORE_ADMIN_GETTER]
    return storeAdmin
      ? storeAdmin.restrictedViews.map(view => view.toLowerCase())
      : []
  },
  [IS_MULTIVIEW_GETTER](_state: State, localGetters: Getters) {
    return localGetters[GET_ENABLED_STORE_ADDRESS_IDS_GETTER].length > 1
  },
  [IS_STORE_ADMIN_LOADED_GETTER](state: State) {
    return state.storeAdminLoaded
  }
}

function getStoreAddressesFromStoreAdmin({
  storeAdmin,
  shouldFilterByEnabledState = false
}: {
  storeAdmin: StoreAdmin
  shouldFilterByEnabledState: boolean
}): StoreAddressDTO[] {
  const result: StoreAddressDTO[] = []
  return result.concat.apply(
    [],
    storeAdmin.cities.map(city =>
      getStoreAddressesFromCity({ city, shouldFilterByEnabledState })
    )
  )
}

function getStoreAddressesFromCity({
  city,
  shouldFilterByEnabledState = false
}: {
  city: CityDTO
  shouldFilterByEnabledState: boolean
}): StoreAddressDTO[] {
  const result: StoreAddressDTO[] = []
  return result.concat.apply(
    [],
    city.stores.map(store =>
      getStoreAddressesFromStore({ store, shouldFilterByEnabledState })
    )
  )
}

function getStoreAddressesFromStore({
  store,
  shouldFilterByEnabledState = false
}: {
  store: StoreDTO
  shouldFilterByEnabledState: boolean
}): StoreAddressDTO[] {
  if (shouldFilterByEnabledState) {
    return store.isEnabled ? store.storeAddresses : []
  }
  return store.storeAddresses
}

function getSelectedAddresses(
  addressesMap: StoresAddressesMap,
  addressIds: StoreAddressId[]
): StoresAddressesMap {
  return addressIds.reduce((acc, addressId) => {
    acc[addressId] = addressesMap[addressId]
    return acc
  }, {} as StoresAddressesMap)
}

function getSelectedStores(
  storesMap: StoresMap,
  selectedAddresses: StoreAddress[]
): StoresMap {
  return [
    ...new Set(selectedAddresses.map(address => address.parentStoreId))
  ].reduce((acc, storeId) => {
    acc[storeId] = storesMap[storeId]
    return acc
  }, {} as StoresMap)
}

function getSelectedCities(
  citiesMap: CitiesMap,
  storeAddresses: StoreAddress[]
): CitiesMap {
  return [...new Set(storeAddresses.map(address => address.cityCode))].reduce(
    (acc, cityCode) => {
      acc[cityCode] = citiesMap[cityCode]
      return acc
    },
    {} as CitiesMap
  )
}

function mapSelectedCity(
  city: City,
  selectedStoresMap: StoresMap,
  selectedAddressesMap: StoresAddressesMap
): City {
  const { stores, ...restCity } = city
  return {
    ...restCity,
    stores: stores
      .filter(({ id }) => selectedStoresMap[id])
      .map(({ id }) => selectedStoresMap[id])
      .map(store => mapSelectedStore(store, selectedAddressesMap))
  }
}

function mapSelectedStore(
  store: StoreDTO,
  selectedAddressesMap: StoresAddressesMap
): StoreDTO {
  const { storeAddresses, ...restStore } = store
  return {
    ...restStore,
    storeAddresses: storeAddresses
      .filter(({ id }) => selectedAddressesMap[id])
      .map(({ id }) => selectedAddressesMap[id])
  }
}

export interface Getters {
  [GET_CURRENT_STORE_ADMIN_GETTER]: StoreAdmin | null
  [IS_ADMIN_MODE_GETTER]: boolean
  [GET_SELECTED_STORE_ADDRESS_IDS_GETTER]: StoreAddressId[]
  [GET_SELECTED_CITIES]: CitiesMap
  [GET_AVAILABLE_STORE_ADDRESS_IDS_GETTER]: StoreAddressId[]
  [GET_ENABLED_STORE_ADDRESS_IDS_GETTER]: StoreAddressId[]
  [IS_AUTHENTICATED_GETTER]: boolean
  [IS_MULTIVIEW_GETTER]: boolean
}
