
import Vue from 'vue'
import Component from 'vue-class-component'
import { Watch } from 'vue-property-decorator'
import Icon from '@/components/common/Icon.vue'
import telemetry from '@/modules/telemetry/telemetry'
import mParticle from '@/modules/mparticle'
import { BusinessEventNames } from 'models/BusinessEvents'
import { getDeviceId } from '@/modules/shared/helpers'
import { GET_CURRENT_STORE_ADMIN_GETTER } from '@/modules/authentication'
import { authentication } from '@/modules/authentication/store/namespace'
import { StoreAdmin } from '@/modules/store/data/resources'
import { core } from '@/modules/core/store/namespace'
import {
  GET_IS_OFFLINE_BANNER_VISIBLE_GETTER,
  GET_IS_POOR_CONNECTIVITY_BANNER_VISIBLE_GETTER
} from '@/modules/core/store/getters'

@Component({
  name: 'ConnectivityBanner',
  components: {
    Icon
  }
})
export default class ConnectivityBanner extends Vue {
  @authentication.Getter(GET_CURRENT_STORE_ADMIN_GETTER)
  storeAdmin?: StoreAdmin

  @Watch('isConnectivityVisible')
  onIsVisibleChange() {
    if (this.isConnectivityVisible) {
      this.logChangeConnectionStatus()
    }
  }

  @core.Getter(GET_IS_OFFLINE_BANNER_VISIBLE_GETTER)
  isOfflineBannerVisible!: boolean
  @core.Getter(GET_IS_POOR_CONNECTIVITY_BANNER_VISIBLE_GETTER)
  isPoorConnectivityBannerVisible!: boolean

  get isConnectivityVisible() {
    return this.isOfflineBannerVisible || this.isPoorConnectivityBannerVisible
  }
  get getConnectivityTextKey() {
    return this.isOfflineBannerVisible
      ? 'notification.connectivity.no_internet'
      : 'notification.connectivity.unstable'
  }

  get getConnectivityType() {
    return this.isOfflineBannerVisible ? 'no_internet' : 'unstable'
  }

  logChangeConnectionStatus() {
    const storeAdminId = this.storeAdmin ? this.storeAdmin.id : 0

    telemetry.Log.info(
      'BANNER_CONNECTIVITY_SHOWN',
      `Visibility banner shown (type=${this.getConnectivityType})`,
      {
        attr: {
          type: this.getConnectivityType
        }
      }
    )
    mParticle.logEvent(
      BusinessEventNames.bannerConnectivityShown,
      mParticle.EventType.UserContent,
      {
        deviceId: getDeviceId(),
        storeAdminId,
        type: this.getConnectivityType
      }
    )
  }
}
