import { PromotionType } from '../api/dtos'
import { FeatureToggleMap } from '@/modules/feature-toggles/store/state'

export const arePromotionsEnabled = (toggles: FeatureToggleMap): boolean => {
  return !!toggles?.PARTNERS_SELF_SERVICE_PROMOS_ENABLED
}

export const areDiscountPromotionsEnabled = (
  toggles: FeatureToggleMap
): boolean => {
  return !!toggles?.PARTNERS_SELF_SERVICE_DISCOUNT_PROMOS_ENABLED
}

export const areDiscountPromotionsWithProductsEnabled = (
  toggles: FeatureToggleMap
): boolean => {
  return !!toggles?.PARTNERS_SELF_SERVICE_PRODUCT_DISCOUNT_PROMOS_ENABLED
}

export const arePromotionVisibilityRulesEnabled = (
  toggles: FeatureToggleMap
): boolean => {
  return !!toggles?.PARTNERS_PROMO_VISIBILITY_RULES_ENABLED
}

export const isOneStorePerPromotionEnabled = (
  toggles: FeatureToggleMap
): boolean => {
  return !!toggles?.PARTNERS_SELF_SERVICE_ONE_STORE_PER_PROMO_ENABLED
}

export const getEnabledPromotionTypes = (
  toggles: FeatureToggleMap
): PromotionType[] => {
  const enabledPromoTypes: PromotionType[] = []
  if (arePromotionsEnabled(toggles)) {
    enabledPromoTypes.push(PromotionType.FREE_DELIVERY)
  }
  if (areDiscountPromotionsEnabled(toggles)) {
    enabledPromoTypes.push(PromotionType.PERCENTAGE_DISCOUNT)
  }
  return enabledPromoTypes
}
