export class Product {
  constructor(
    id,
    quantity = 1,
    name,
    price,
    type = 'product',
    attributeGroups
  ) {
    this.id = id
    this.quantity = quantity
    this.name = name
    this.type = type
    this.price = price
    this.attributeGroups = attributeGroups
  }

  clone() {
    return Object.assign(Object.create(Object.getPrototypeOf(this)), this)
  }
}

export class PersoProduct extends Product {
  constructor(
    id = performance.now(),
    quantity = 1,
    persoAttributes,
    note,
    product = new Product()
  ) {
    super(id, quantity, name, product.price, 'perso', product.attributeGroups)
    this.product = product
    this.persoAttributes = persoAttributes
    this.note = note
  }

  parse(data) {
    this.name = data.name
    this.persoAttributes = data.persoAttributes
    this.note = data.note
    this.product = data.product
    this.price = data.product.price
  }
}

export class AttributeGroup {
  constructor(
    name,
    min,
    max,
    multipleSelection,
    collapsedByDefault,
    attributes
  ) {
    this.name = name
    this.min = min
    this.max = max
    this.collapsedByDefault = collapsedByDefault
    this.multipleSelection = multipleSelection
    this.attributes = attributes
    this.expanded = false
  }

  toggleAttribute(attribute) {
    const type = this.getType()
    if (!this.isOptional() && type === 'SINGLE') {
      this.attributes.forEach(attr => {
        const equals = attr.equals(attribute)
        attr.selected = equals
        attr.quantity = equals ? 1 : 0
      })
    } else {
      if (this.isFull() && !attribute.selected) {
        return
      }
      attribute.selected = !attribute.selected
      attribute.quantity = attribute.selected ? 1 : 0
    }
  }

  updateAttributeQuantity(operator, attribute) {
    if (operator === 'increment') {
      if (this.isFull()) return
      attribute.quantity += 1
    } else {
      attribute.quantity -= 1
      if (attribute.quantity === 0) attribute.selected = false
    }
  }

  numberOfAttributesSelected() {
    let total = 0
    this.attributes.forEach(attr => {
      total += attr.selected ? attr.quantity : 0
    })
    return total
  }

  isFull() {
    if (this.getType() === 'NO_CAP') {
      return false
    }
    return this.numberOfAttributesSelected() >= this.max
  }

  isValid() {
    if (this.isOptional()) {
      return true
    }
    let valid = true
    switch (this.getType()) {
      case 'NO_CAP':
        valid = this.numberOfAttributesSelected() > this.min
        break
      case 'SINGLE':
        valid = this.numberOfAttributesSelected() === 1
        break
      case 'CAP':
      default:
        valid =
          this.numberOfAttributesSelected() >= this.min &&
          this.numberOfAttributesSelected() <= this.max
        break
    }
    return valid
  }

  isOptional() {
    return !this.min || this.min === 0
  }

  getType() {
    if (!this.max) return 'NO_CAP'
    else if (this.max === 1) return 'SINGLE'
    else return 'CAP'
  }

  getSubtitle() {
    let subtitle = 'store.perso.subtitle.'
    let params = {}
    switch (this.getType()) {
      case 'NO_CAP':
        subtitle += this.isOptional() ? 'optional.noCap' : 'mandatory.noCap'
        params = { minNumber: this.min }
        break
      case 'SINGLE':
        subtitle += this.isOptional() ? 'optional.single' : 'mandatory.single'
        break
      case 'CAP':
        if (this.isOptional()) {
          subtitle += 'optional.cap'
          params.maxNumber = this.max
        } else {
          subtitle += 'mandatory.cap'
          params.minNumber = this.min
          params.maxNumber = this.max
        }
        break
    }
    return { subtitle, params }
  }
}

export class Attribute {
  constructor(id, name, priceImpact = 0, quantity = 0, selected = false) {
    this.id = id
    this.name = name
    this.priceImpact = priceImpact
    this.quantity = quantity
    this.selected = selected
  }

  equals(attribute) {
    return this.id === attribute.id
  }
}
