import { Store } from 'vuex'
import { State } from '@/modules/store'
import Storage from 'models/Storage'
import moment from 'moment-timezone'
import { getNetworkType, getRtt } from '@/modules/shared'
import { BusinessEventNames } from 'models/BusinessEvents'
import { AxiosError } from 'axios'
import mParticle from '@/modules/mparticle'
import { OrdersList } from '@/modules/store/data/resources/orders-list/orders-list'

export const CACHE_MAXIMUM_SIZE = 100
export const GENERIC_ERROR = 'GENERIC_ERROR'

let mParticleEvents: Record<string, MParticlePollingEvent> = {}

type MParticlePollingEvent = {
  pollingType: BusinessEventNames
  requestStartTimeMillis: number
  deviceId: string
  networkType: string
  networkRTT: string
  orders: number
  storeAdminId: number
  selectedStoreAddressIds: string
  availableStoreAddressIds: string
  errorCode: string
  statusCode: number
}

export class MparticlePollingEventsService {
  store: Store<State>

  constructor(store: Store<State>) {
    this.store = store
  }

  public beforePollingRequest(): (correlationId: string) => void {
    return (correlationId: string) => {
      MparticlePollingEventsService.cleanCacheIfMaximumSizeExceeded()
      const storeAdmin = this.store.getters[
        'authentication/GET_CURRENT_STORE_ADMIN_GETTER'
      ]
      const selectedStoreAddressIds = this.store.getters[
        'authentication/GET_SELECTED_STORE_ADDRESS_IDS_GETTER'
      ]
      const availableStoreAddressIds = this.store.getters[
        'authentication/GET_AVAILABLE_STORE_ADDRESS_IDS_GETTER'
      ]

      const device = Storage.get(Storage.items.DEVICE)

      mParticleEvents[correlationId] = {
        pollingType: BusinessEventNames.ordersPolling,
        requestStartTimeMillis: moment().valueOf(),
        networkRTT: String(getRtt()),
        deviceId: (device || {}).id || '',
        networkType: getNetworkType(),
        orders: 0,
        storeAdminId: (storeAdmin && storeAdmin.id) || '',
        selectedStoreAddressIds: (selectedStoreAddressIds || []).join(','),
        availableStoreAddressIds: (availableStoreAddressIds || []).join(','),
        errorCode: '',
        statusCode: 0
      }
    }
  }

  public afterPollingResponse(): (
    correlationId: string,
    orders: OrdersList
  ) => void {
    return (correlationId: string, orders: OrdersList) => {
      const cachedEvent = mParticleEvents[correlationId]
      if (cachedEvent) {
        delete mParticleEvents[correlationId]
        cachedEvent.orders = MparticlePollingEventsService.getNumberOfOrders(
          orders
        )
        cachedEvent.statusCode = 200
        this.logEvent(cachedEvent)
      }
    }
  }

  public onPollingError(): (correlationId: string, error: AxiosError) => void {
    return (correlationId: string, error: AxiosError) => {
      const cachedEvent = mParticleEvents[correlationId]
      if (cachedEvent) {
        delete mParticleEvents[correlationId]
        MparticlePollingEventsService.addErrorDetailsToEvent(cachedEvent, error)
        this.logEvent(cachedEvent)
      }
    }
  }

  private static cleanCacheIfMaximumSizeExceeded(): void {
    // Reset cache if its size grows over the limit and events are not being flushed
    if (Object.keys(mParticleEvents).length >= CACHE_MAXIMUM_SIZE) {
      mParticleEvents = {}
    }
  }

  private static addErrorDetailsToEvent(
    cachedEvent: MParticlePollingEvent,
    error: AxiosError
  ) {
    if (error && error.code) {
      cachedEvent.errorCode = error.code
    } else {
      cachedEvent.errorCode = GENERIC_ERROR
    }
    if (error && error.response && error.response.status) {
      cachedEvent.statusCode = error.response.status
    }
  }

  private static getNumberOfOrders({
    pending,
    accepted,
    cancelled,
    served
  }: OrdersList): number {
    return pending.length + accepted.length + cancelled.length + served.length
  }

  private logEvent({
    deviceId,
    networkType,
    networkRTT,
    orders,
    storeAdminId,
    selectedStoreAddressIds,
    availableStoreAddressIds,
    requestStartTimeMillis,
    errorCode,
    statusCode
  }: MParticlePollingEvent): void {
    const latency = moment().valueOf() - requestStartTimeMillis

    mParticle.logEvent(
      BusinessEventNames.ordersPolling,
      mParticle.EventType.UserContent,
      {
        deviceId,
        networkType,
        networkRTT,
        orders,
        storeAdminId,
        selectedStoreAddressIds,
        availableStoreAddressIds,
        latency,
        errorCode,
        statusCode
      }
    )
  }
}
