
import Vue from 'vue'
import Component from 'vue-class-component'
import { Prop } from 'vue-property-decorator'
import { Padding, Size } from '../../../types'

@Component({ name: 'GlovoCheckbox' })
export default class GlovoCheckbox extends Vue {
  @Prop() padding!: Padding
  @Prop({ default: 'regular' }) size!: Size

  get cssClasses() {
    const classes: string[] = []
    if (this.padding) {
      classes.push(`padding-${this.padding}`)
    }
    if (this.size) {
      classes.push(`size-${this.size}`)
    }
    return classes
  }
}
