/**
 * This class is for extending the Error interface
 * to be more verbose if needed
 */
export default class GlvError extends Error {
  static fromError(err: Error | string | object) {
    const errMsg = GlvError.createErrorMsg(err)
    const glvError = new GlvError(errMsg)
    glvError.wrappedError = err
    return glvError
  }

  static createErrorMsg(err: Error | string | object) {
    if (typeof err === 'string') {
      return err
    } else if (typeof err === 'object') {
      return 'message' in err ? err.message : JSON.stringify(err)
    } else {
      return `${err}`
    }
  }

  wrappedError?: Error | string | object

  constructor(message?: string) {
    super(message)
    this.name = 'GlvError'
  }
}
