
import Vue from 'vue'
import Component from 'vue-class-component'
import OrderModel from 'models/Order'
import {
  GET_IS_KITCHEN_CODE_STORE_ADDRESS_GETTER,
  GET_CANCELLED_ORDERS_GETTER
} from '../../store/getters'
import { MARK_CANCELLED_ORDER_AS_SHOWN } from '../../store'
import { StoreAction } from '@/modules/store/types/store'
import { orderManagement } from '@/modules/orders/store/namespace'
import { OrderId, StoreAddressId } from '@/modules/store/data/resources'
import { Watch } from 'vue-property-decorator'
import AudioService, { Sounds } from '@/services/audio/AudioService'
import { missingTranslationLogger } from '@/modules/orders/helpers/missing-translation-logger'
import { CancelledOrder } from '../../../store/data/resources/orders-list/order-types'
import { GlovoModal } from 'common/ui/modal'
import GlovoBtn from 'common/GlovoBtn.vue'
export const THREE_AND_A_HALF_SECONDS = 3.5

@Component({
  name: 'CancelledOrderDialog',
  components: {
    GlovoModal,
    GlovoBtn
  }
})
export default class CancelledOrderDialog extends Vue {
  isMarkingAsShown = false

  @orderManagement.Getter(GET_CANCELLED_ORDERS_GETTER)
  cancelledOrders!: CancelledOrder[]

  @orderManagement.Getter(GET_IS_KITCHEN_CODE_STORE_ADDRESS_GETTER)
  isKitchenCodeStoreAddress!: (id: StoreAddressId) => boolean

  @orderManagement.Action(MARK_CANCELLED_ORDER_AS_SHOWN)
  markCancelledOrderAsShown!: StoreAction<OrderId>

  get cancelledOrder(): CancelledOrder | null {
    const notAcknowledgedCancelledOrders = this.cancelledOrders.filter(
      order => !order.isCancellationAcknowledged
    )
    return notAcknowledgedCancelledOrders.length
      ? notAcknowledgedCancelledOrders[0]
      : null
  }

  get isVisible(): boolean {
    return Boolean(this.cancelledOrder && !this.isMarkingAsShown)
  }

  get isModalDisplayed() {
    return this.isVisible
  }

  set isModalDisplayed(display: boolean) {
    if (!display) {
      this.$emit('close')
    }
  }

  get displayPickupCode(): ReturnType<typeof OrderModel.getShortCode> | void {
    if (this.cancelledOrder) {
      return OrderModel.getShortCode({
        code: this.cancelledOrder.code,
        pickupCode: this.cancelledOrder.pickupCode,
        usesKitchenCode: this.isKitchenCodeStoreAddress(
          this.cancelledOrder.storeAddressId
        )
      })
    }
  }

  get cancelReason() {
    if (!this.cancelledOrder) {
      return null
    }
    const reasonTranslationKey = `cancelReason.${this.cancelledOrder.cancelReason}`
    const translationForReasonExists = this.$te(reasonTranslationKey)
    if (translationForReasonExists) {
      return this.$t(reasonTranslationKey)
    } else {
      missingTranslationLogger.logMissingTranslationOnce(
        reasonTranslationKey,
        `Translation of cancel order reason is missing (key = ${reasonTranslationKey}) (locale = ${this.$i18n.locale})`
      )
      return this.$t('cancelReason.OTHER')
    }
  }

  @Watch('isVisible')
  onDialogVisibilityChange() {
    if (this.isVisible) {
      AudioService.playSoundForSeconds(
        Sounds.cancelled,
        THREE_AND_A_HALF_SECONDS
      )
    }
  }

  async markAsShown(id: OrderId) {
    this.isMarkingAsShown = true
    await this.markCancelledOrderAsShown(id)
    setTimeout(() => (this.isMarkingAsShown = false), 1000)
  }

  async handleClose() {
    if (this.cancelledOrder) {
      await this.markAsShown(this.cancelledOrder.id)
    }
  }
}
