import {
  SET_TOKEN,
  SetTokenPayload,
  LOG_OUT,
  SET_CURRENT_STORE_ADMIN,
  SetCurrentStoreAdminPayload,
  SET_ADMIN_MODE,
  SetAdminModePayload,
  SET_SELECTED_STORE_ADDRESS_IDS,
  SetSelectedStoreAddressIdsPayload,
  RESTORE_SELECTED_STORE_ADDRESS_IDS,
  INIT_STORE,
  RESTORE_SESSION,
  GET_CURRENT_STORE_ADMIN,
  REFRESH_TOKEN,
  LOG_IN,
  LogInPayload,
  SetCurrencyPayload,
  SET_CURRENCY,
  SET_LANGUAGE,
  TRACK_CURRENT_STORE_ADMIN,
  TrackCurrentStoreAdminPayload,
  SetLanguagePayload,
  REFRESH_TOKEN_AFTER,
  REFRESH_TOKEN_BEFORE,
  SET_TOKEN_SUCCESS,
  SetTokenSuccessPayload,
  RESTORE_SESSION_SUCCESS,
  RestoreSessionSuccessPayload,
  Action,
  INIT_DEVICE_INFO,
  INIT_BUSINESS_EVENTS_LOG_COUNTERS,
  REFRESH_TOKEN_SUCCESS,
  RefreshTokenSuccessPayload,
  STORE_ADMIN_LOADED_SUCCESS,
  STORE_ADMIN_LOADED_FAILURE
} from './action-types'
import { ADD_STORE_ADMIN_DTOS } from '@/modules/store/data/action-types'
import { RESET_STATE as feature_toggles_RESET_STATE } from '@/modules/feature-toggles/store/action-types'
