
import Vue from 'vue'
import Component from 'vue-class-component'
import { Prop } from 'vue-property-decorator'
import { Getter } from 'vuex-class'
import SelectProductModal from '@/components/main/popups/SelectProductModal.vue'
import GlovoWarning from 'common/ui/GlovoWarning.vue'
import ProductCustomization from '../customize/ProductCustomization.vue'
import { StoreAction } from '@/modules/store/types/store'
import { OrderDetails } from '@/modules/store/data/resources/order-details'
import { orderManagement } from '@/modules/orders/store/namespace'
import { GET_ORDER_PRODUCT_ATTRIBUTES_MODAL_GETTER } from '@/modules/orders/store/getters'
import {
  CLOSE_ORDER_PRODUCT_ATTRIBUTES_MODAL,
  OPEN_ORDER_PRODUCT_EDIT_MODAL,
  OpenOrderProductEditModalPayload
} from '@/modules/orders/store'
import { GET_PRODUCTS_GETTER } from '@/modules/store/data/getters'
import type { Product, ProductId } from '@/modules/store/data/resources'
import type { BoughtProductId } from '@/modules/store/data/resources/order-details'

@Component({
  name: 'OrderProductAttributesModal',
  components: {
    GlovoWarning,
    SelectProductModal,
    ProductCustomization
  }
})
export default class OrderProductAttributesModal extends Vue {
  @Prop() order!: OrderDetails

  @orderManagement.Getter(GET_ORDER_PRODUCT_ATTRIBUTES_MODAL_GETTER)
  orderProductAttributesModal!: {
    isDisplayed: boolean
    productId: BoughtProductId | null
  }

  @Getter(GET_PRODUCTS_GETTER)
  storeProducts!: Record<ProductId, Product>

  @orderManagement.Action(CLOSE_ORDER_PRODUCT_ATTRIBUTES_MODAL)
  closeOrderProductAttributesModal!: StoreAction<void>

  @orderManagement.Action(OPEN_ORDER_PRODUCT_EDIT_MODAL)
  openOrderProductEditModal!: StoreAction<OpenOrderProductEditModalPayload>

  get isVisible() {
    return this.orderProductId && this.orderProductAttributesModal.isDisplayed
  }

  get orderProductId(): BoughtProductId | null {
    return this.orderProductAttributesModal.productId
  }

  get productDetails() {
    return this.order.products.find(({ id }) => id === this.orderProductId)
  }

  get productId(): ProductId | undefined {
    return this.productDetails?.productId
  }

  get product() {
    return this.productId ? this.storeProducts[this.productId] : null
  }

  get productName() {
    return this.productDetails?.name
  }

  get productPrice() {
    return this.productDetails?.price
  }

  get warningMessage() {
    return this.$t('order.products.warning')
  }

  get selectedAttributeGroups() {
    return this.productDetails?.customizations?.map(
      ({ attributeId }) => attributeId
    )
  }

  get attributeGroups() {
    return this.product?.attributeGroups?.map(attributeGroup => ({
      ...attributeGroup,
      attributes: {
        ...attributeGroup.attributes.map(attribute => ({
          ...attribute,
          selected: this.selectedAttributeGroups?.includes(attribute.id)
        }))
      }
    }))
  }

  closeModal() {
    if (this.orderProductId) {
      this.openOrderProductEditModal({ productId: this.orderProductId })
    }
    this.closeOrderProductAttributesModal()
  }
}
