import { ActionContext } from 'glovo'
import userApiFactory from '@/api/user'
import {
  Action,
  ADD_CLOSED_STORE_ADDRESSES,
  CLOSE_STORE_ADDRESSES_TEMPORARILY,
  CLOSE_STORE_ADDRESSES_TEMPORARILY_FAILURE,
  CLOSE_STORE_ADDRESSES_TEMPORARILY_SUCCESS,
  CloseStoreAddressesTemporarilyFailurePayload,
  CloseStoreAddressesTemporarilyPayload,
  CloseStoreAddressesTemporarilySuccessPayload,
  CONFIRM_CHECK_IN,
  CONFIRM_CHECK_IN_FAILURE,
  CONFIRM_CHECK_IN_SUCCESS,
  ConfirmCheckInFailurePayload,
  ConfirmCheckInPayload,
  ConfirmCheckInSuccessPayload,
  DISMISS_CHECK_IN,
  DISMISS_CHECK_IN_FAILURE,
  DISMISS_CHECK_IN_SUCCESS,
  DismissCheckInFailurePayload,
  DismissCheckInPayload,
  DismissCheckInSuccessPayload,
  GET_STATUS,
  GET_STATUS_FAILURE,
  GET_STATUS_SUCCESS,
  GetStatusFailurePayload,
  GetStatusPayload,
  GetStatusSuccessPayload,
  HIDE_STORE_CLOSING_MODAL,
  OPEN_STORE_ADDRESSES,
  OPEN_STORE_ADDRESSES_FAILURE,
  OPEN_STORE_ADDRESSES_NEW,
  OPEN_STORE_ADDRESSES_SUCCESS,
  OpenStoreAddressesFailurePayload,
  OpenStoreAddressesPayload,
  OpenStoreAddressesSuccessPayload,
  RESET_STATE,
  SET_CLOSING_DAYS,
  SET_CLOSING_DAYS_FAILURE,
  SET_CLOSING_DAYS_SUCCESS,
  SET_STATUS,
  SetClosingDaysFailurePayload,
  SetClosingDaysPayload,
  SetClosingDaysSuccessPayload,
  SETUP_STATUS_POLLING,
  SetupStatusPollingPayload,
  SHOW_STORE_CLOSING_MODAL,
  STOP_STATUS_POLLING,
  UPDATE_IS_CHECK_IN_LOADING,
  UPDATE_DISMISS_CHECK_IN_FAILURE,
  UPDATE_IS_CHECK_IN_PENDING
} from './action-types'
import {
  setupStatusPolling,
  stopStatusPolling
} from '@/modules/store-management/services/status-polling'
import telemetry from '@/modules/telemetry/telemetry'
import { State } from '@/modules/store-management/store/state'
import { GET_AVAILABLE_STORE_ADDRESS_IDS_GETTER } from '@/modules/authentication'
import { AUTHENTICATION_MODULE_NAME } from '@/modules/authentication/authentication-module'
import AudioService, { Sounds } from '@/services/audio/AudioService'

export function actionsFactory({
  status,
  confirmCheckIn,
  dismissCheckIn,
  closeTemporarily,
  statusUpdateByAddressIds,
  setClosingDaysByAddressIds,
  openStoreAddresses
}: ReturnType<typeof userApiFactory>) {
  return {
    [RESET_STATE]({ commit }: ActionContext<Action>) {
      commit(RESET_STATE)
    },
    async [GET_STATUS](
      { dispatch }: ActionContext<Action>,
      payload: GetStatusPayload
    ) {
      try {
        if (payload) {
          const response = await status(payload)
          dispatch(GET_STATUS_SUCCESS, response)
        } else {
          dispatch(GET_STATUS_FAILURE, `Empty request payload: ${payload}`)
        }
      } catch (error) {
        dispatch(GET_STATUS_FAILURE, error)
      }
    },
    [GET_STATUS_SUCCESS](
      { commit }: ActionContext<Action>,
      payload: GetStatusSuccessPayload
    ) {
      commit(SET_STATUS, payload)
    },
    [GET_STATUS_FAILURE](
      _: ActionContext<Action>,
      error: GetStatusFailurePayload
    ) {
      telemetry.Log.error(GET_STATUS_FAILURE, error.toString())
    },
    async [CONFIRM_CHECK_IN](
      { commit, dispatch }: ActionContext<Action>,
      payload: ConfirmCheckInPayload
    ) {
      try {
        commit(UPDATE_IS_CHECK_IN_LOADING, true)
        await confirmCheckIn(payload)
        dispatch(CONFIRM_CHECK_IN_SUCCESS, payload)
      } catch (error) {
        dispatch(CONFIRM_CHECK_IN_FAILURE, error)
      } finally {
        commit(UPDATE_IS_CHECK_IN_LOADING, false)
      }
    },
    [CONFIRM_CHECK_IN_SUCCESS](
      { commit, dispatch }: ActionContext<Action>,
      payload: ConfirmCheckInSuccessPayload
    ) {
      AudioService.stopSound(Sounds.checkin)
      commit(UPDATE_IS_CHECK_IN_PENDING, false)
      dispatch(GET_STATUS, payload)
    },
    [CONFIRM_CHECK_IN_FAILURE](
      _: ActionContext<Action>,
      error: ConfirmCheckInFailurePayload
    ) {
      telemetry.Log.error(CONFIRM_CHECK_IN_FAILURE, error.toString())
    },
    async [DISMISS_CHECK_IN](
      { dispatch, commit }: ActionContext<Action>,
      payload: DismissCheckInPayload
    ) {
      commit(UPDATE_DISMISS_CHECK_IN_FAILURE, false)
      try {
        await dismissCheckIn(payload)
        dispatch(DISMISS_CHECK_IN_SUCCESS, payload)
      } catch (error) {
        dispatch(DISMISS_CHECK_IN_FAILURE, error)
      }
    },
    [DISMISS_CHECK_IN_SUCCESS](
      { state, dispatch }: ActionContext<Action, State>,
      payload: DismissCheckInSuccessPayload
    ) {
      const { isAllowedToChooseClosingType } = state.dataState.checkIn

      if (!isAllowedToChooseClosingType) {
        dispatch(GET_STATUS, payload)
      }
    },
    [DISMISS_CHECK_IN_FAILURE](
      { commit }: ActionContext<Action>,
      error: DismissCheckInFailurePayload
    ) {
      commit(UPDATE_DISMISS_CHECK_IN_FAILURE, true)
      telemetry.Log.error(DISMISS_CHECK_IN_FAILURE, error.toString())
    },
    [SHOW_STORE_CLOSING_MODAL]({ commit }: ActionContext<Action>) {
      commit(SHOW_STORE_CLOSING_MODAL)
    },
    [HIDE_STORE_CLOSING_MODAL]({ commit }: ActionContext<Action>) {
      commit(HIDE_STORE_CLOSING_MODAL)
    },
    [SETUP_STATUS_POLLING](
      { rootGetters, dispatch }: ActionContext<Action>,
      storeAddressId: SetupStatusPollingPayload
    ) {
      setupStatusPolling(storeAddressId, rootGetters, dispatch, status)
    },
    [STOP_STATUS_POLLING]() {
      stopStatusPolling()
    },
    async [OPEN_STORE_ADDRESSES](
      { dispatch }: ActionContext<Action>,
      payload: OpenStoreAddressesPayload
    ) {
      try {
        await statusUpdateByAddressIds(payload, { closedUntil: null })
        dispatch(OPEN_STORE_ADDRESSES_SUCCESS, payload)
      } catch (error) {
        dispatch(OPEN_STORE_ADDRESSES_FAILURE, error)
      }
    },
    async [OPEN_STORE_ADDRESSES_NEW](
      { dispatch }: ActionContext<Action>,
      payload: OpenStoreAddressesPayload
    ) {
      try {
        await openStoreAddresses({ storeAddressIds: payload })
        dispatch(OPEN_STORE_ADDRESSES_SUCCESS, payload)
      } catch (error) {
        dispatch(OPEN_STORE_ADDRESSES_FAILURE, error)
      }
    },
    [OPEN_STORE_ADDRESSES_SUCCESS](
      { commit }: ActionContext<Action>,
      payload: OpenStoreAddressesSuccessPayload
    ) {
      commit(OPEN_STORE_ADDRESSES, payload)
    },
    [OPEN_STORE_ADDRESSES_FAILURE](
      _: ActionContext<Action>,
      payload: OpenStoreAddressesFailurePayload
    ) {
      console.error(payload)
    },
    async [CLOSE_STORE_ADDRESSES_TEMPORARILY](
      { dispatch }: ActionContext<Action>,
      payload: CloseStoreAddressesTemporarilyPayload
    ) {
      try {
        await closeTemporarily(payload)
        dispatch(
          CLOSE_STORE_ADDRESSES_TEMPORARILY_SUCCESS,
          payload.storeAddressIds
        )
      } catch (error) {
        dispatch(CLOSE_STORE_ADDRESSES_TEMPORARILY_FAILURE, error)
      }
    },
    [CLOSE_STORE_ADDRESSES_TEMPORARILY_SUCCESS](
      { commit, state, dispatch, rootGetters }: ActionContext<Action, State>,
      payload: CloseStoreAddressesTemporarilySuccessPayload
    ) {
      commit(ADD_CLOSED_STORE_ADDRESSES, payload)

      const {
        isCheckInPending,
        isAllowedToChooseClosingType
      } = state.dataState.checkIn
      const isVisible = state.uiState.shouldShowStoreClosingModal
      if (isCheckInPending && isAllowedToChooseClosingType) {
        const storeAddressId =
          rootGetters[
            `${AUTHENTICATION_MODULE_NAME}/${GET_AVAILABLE_STORE_ADDRESS_IDS_GETTER}`
          ][0]
        dispatch(GET_STATUS, storeAddressId)

        if (isVisible) {
          commit(HIDE_STORE_CLOSING_MODAL)
        }
      }
    },
    [CLOSE_STORE_ADDRESSES_TEMPORARILY_FAILURE](
      _: ActionContext<Action>,
      payload: CloseStoreAddressesTemporarilyFailurePayload
    ) {
      console.error(payload)
    },
    async [SET_CLOSING_DAYS](
      { dispatch }: ActionContext<Action>,
      payload: SetClosingDaysPayload
    ) {
      try {
        await setClosingDaysByAddressIds(
          payload.storeAddressIds,
          payload.interval
        )
        dispatch(SET_CLOSING_DAYS_SUCCESS, payload)
      } catch (error) {
        dispatch(SET_CLOSING_DAYS_FAILURE, error)
      }
    },
    [SET_CLOSING_DAYS_SUCCESS](
      { commit }: ActionContext<Action>,
      payload: SetClosingDaysSuccessPayload
    ) {
      commit(SET_CLOSING_DAYS, payload)
    },
    [SET_CLOSING_DAYS_FAILURE](
      _: ActionContext<Action>,
      payload: SetClosingDaysFailurePayload
    ) {
      console.error(payload)
    }
  }
}
