import { Store } from 'vuex'
import { GlvModule } from 'glovo'
import { AbstractFetchService } from '@/modules/http'
import { State as RootState } from '@/modules/store'
import { storeFactory } from './store'

export class PromotionsModule implements GlvModule {
  readonly name = 'promotions'
  constructor(
    private readonly store: Store<RootState>,
    private readonly fetchService: AbstractFetchService
  ) {}

  install() {
    const promotionsStore = storeFactory(this.fetchService)
    this.store.registerModule([this.name], promotionsStore)
  }
}
