import { PromotionQualityTierDTO } from '../api/dtos'

export class PromotionQualityTierVM {
  constructor(private readonly qualityTier: PromotionQualityTierDTO) {}

  get type(): PromotionQualityTierDTO['type'] {
    return this.qualityTier?.type
  }

  get label(): string {
    return this.tierData.label
  }

  get fullLabel(): string {
    return this.tierData.fullLabel
  }

  get characteristics(): PromotionQualityTierDTO['data']['characteristics'] {
    return this.tierData.characteristics ?? {}
  }

  get criteria(): Partial<PromotionQualityTierDTO['data']['criteria']> {
    return this.tierData.criteria ?? {}
  }

  get imageId(): string {
    return this.tierData.imageId
  }

  private get tierData(): PromotionQualityTierDTO['data'] {
    return this.qualityTier?.data ?? {}
  }
}
