export const UPDATE_ONLINE_STATUS = 'UPDATE_ONLINE_STATUS'
export type UpdateOnlineStatusPayload = boolean

export const UPDATE_IS_OFFLINE_BANNER_VISIBLE =
  'UPDATE_IS_OFFLINE_BANNER_VISIBLE'
export type UpdateIsOfflineBannerVisible = boolean

export const LOG_REQUEST = 'LOG_REQUEST'
export type LoggedRequest = {
  timestamp: number
  success: boolean
}

export const CLEAR_LOGGED_REQUESTS = 'CLEAR_LOGGED_REQUESTS'

export const ENABLE_SCROLL = 'ENABLE_SCROLL'
export const DISABLE_SCROLL = 'DISABLE_SCROLL'

export const UPDATE_REFRESHING_INTERVAL = 'UPDATE_REFRESHING_INTERVAL'

export const SET_IS_SEARCHING = 'SET_IS_SEARCHING'
export type SetIsSearchingPayload = boolean

export type Action =
  | {
      type: typeof UPDATE_IS_OFFLINE_BANNER_VISIBLE
      payload: UpdateIsOfflineBannerVisible
    }
  | {
      type: typeof UPDATE_ONLINE_STATUS
      payload: UpdateOnlineStatusPayload
    }
  | {
      type: typeof LOG_REQUEST
      payload: LoggedRequest
    }
  | {
      type: typeof CLEAR_LOGGED_REQUESTS
    }
  | {
      type: typeof ENABLE_SCROLL
    }
  | {
      type: typeof DISABLE_SCROLL
    }
  | {
      type: typeof SET_IS_SEARCHING
      payload: SetIsSearchingPayload
    }
  | {
      type: typeof UPDATE_REFRESHING_INTERVAL
    }
