import size from 'lodash/size'
import {
  PromotionQualityTierDTO,
  PromotionQualityTiersListDTO,
  PromotionQualityTier
} from '../api/dtos'
import { PromotionQualityTierVM } from './promotion-quality-tier'

export type PromotionQualityTiersMap = Record<
  PromotionQualityTier,
  PromotionQualityTierVM
>
export class PromotionQualityTiersListVM {
  public readonly tiers: PromotionQualityTiersMap

  constructor(qualityTiersDTO: PromotionQualityTiersListDTO) {
    this.tiers = qualityTiersDTO?.tiers?.reduce((tiers, tier) => {
      tiers[tier.type] = mapPromotionQualityTier(tier)
      return tiers
    }, {} as PromotionQualityTiersMap)
  }

  getTier(type: PromotionQualityTier | null): PromotionQualityTierVM | null {
    if (!type || !this.hasTiers) {
      return null
    }

    return this.tiers[type] ?? {}
  }

  get featuredTier(): PromotionQualityTierVM | null {
    return this.getTier(PromotionQualityTier.FEATURED)
  }

  get hasTiers() {
    return size(this.tiers) > 0
  }
}

const mapPromotionQualityTier = (dto: PromotionQualityTierDTO) => {
  return new PromotionQualityTierVM(dto)
}
