import { GlvModule } from 'glovo'
import { Store } from 'npm/vuex'
import { State as RootState } from '@/modules/store'
import { storeFactory } from '@/modules/benchmarking/store'

export const BENCHMARKING_MODULE_NAME = 'benchmarking'

export class BenchmarkingModule implements GlvModule {
  readonly name = BENCHMARKING_MODULE_NAME
  constructor(private store: Store<RootState>) {}

  install() {
    const benchmarkingStore = storeFactory()
    this.store.registerModule([this.name], benchmarkingStore)
  }
}
