import { AbstractFetchService } from '@/modules/http'
import { State as RootState } from '@/modules/store'
import { GlvModule } from 'glovo'
import VueRouter from 'vue-router'
import { Vue as _Vue } from 'vue/types/vue'
import { Module, Store } from 'vuex'
import routes from './authentication-routes'
import authorization from './hooks/authorization'
import {
  authorizationHeaderInterceptor,
  tokenRefreshInterceptor
} from './interceptors'
import { State, storeFactory } from './store'
import { INIT_STORE } from './store/action-types'
import VueI18n from 'vue-i18n'

export const AUTHENTICATION_MODULE_NAME = 'authentication'

export class AuthenticationModule implements GlvModule {
  readonly name = AUTHENTICATION_MODULE_NAME
  constructor(
    private router: VueRouter,
    private store: Store<RootState>,
    private fetchService: AbstractFetchService,
    private i18n: VueI18n
  ) {
    router.addRoutes(routes)
    router.beforeEach(authorization(store))
  }
  install(Vue: typeof _Vue) {
    const authenticationModule = storeFactory(
      this.fetchService,
      this.router,
      Vue.glvTracker,
      this.i18n
    )
    this.fetchService.beforeEach(tokenRefreshInterceptor(this.store))
    this.fetchService.beforeEach(authorizationHeaderInterceptor(this.store))
    this.initStore(authenticationModule)
  }
  initStore(storeModule: Module<State, RootState>) {
    this.store.registerModule([this.name], storeModule)
    this.store.dispatch(`${this.name}/${INIT_STORE}`)
  }
}
