import {
  CREATE_PROMOTION_FAILURE,
  CREATE_PROMOTION_SUCCESS,
  GET_PROMOTIONS_SUCCESS,
  GetPromotionsSuccessMutationPayload,
  GET_PROMOTIONS_FAILURE,
  GetPromotionsFailurePayload,
  UPDATE_NEWLY_CREATED_PROMOTION_ID,
  UPDATE_PROMOTION_FAILURE,
  UPDATE_PROMOTION_SUCCESS,
  UPDATE_PROMOTIONS_LOADING,
  UpdateNewlyCreatedPromotionIdPayload,
  UpdatePromotionFailurePayload,
  PromotionsLoadingPayload,
  GET_PROMOTION_QUALITY_TIERS_SUCCESS,
  GET_PROMOTION_QUALITY_TIERS_FAILURE,
  GetPromotionQualityTiersFailurePayload,
  GetPromotionQualityTiersSuccessPayload,
  GET_PROMOTION_QUALITY_SUCCESS,
  GetPromotionQualitySuccessPayload,
  GET_PROMOTION_QUALITY_FAILURE,
  GetPromotionQualityFailurePayload,
  UPDATE_PROMOTION_QUALITY_LOADING
} from './action-types'
import { State } from './state'
import { UpdatePromotionPayload } from '../api/promotions'
import { PromotionStatusName } from '../api/dtos'

export const mutations = {
  [CREATE_PROMOTION_SUCCESS](state: State) {
    state.uiState = { ...state.uiState, createPromotionApiError: null }
  },
  [CREATE_PROMOTION_FAILURE](state: State, error: GetPromotionsFailurePayload) {
    state.uiState = { ...state.uiState, createPromotionApiError: error }
  },
  [GET_PROMOTIONS_SUCCESS](
    state: State,
    payload: GetPromotionsSuccessMutationPayload
  ) {
    state.uiState = {
      ...state.uiState,
      createPromotionApiError: null,
      promotionsGetApiError: null,
      promotionsUpdateApiError: null
    }
    state.dataState = { ...state.dataState, promotions: payload }
  },
  [GET_PROMOTIONS_FAILURE](state: State, error: GetPromotionsFailurePayload) {
    state.uiState = { ...state.uiState, promotionsGetApiError: error }
  },
  [UPDATE_PROMOTIONS_LOADING](state: State, payload: PromotionsLoadingPayload) {
    state.uiState = { ...state.uiState, loading: payload }
  },
  [UPDATE_PROMOTION_SUCCESS](state: State, payload: UpdatePromotionPayload) {
    state.uiState = { ...state.uiState, promotionsUpdateApiError: null }

    const { promotions } = state.dataState.promotions || {}
    if (!promotions) {
      return
    }
    const promotionToUpdate = promotions.find(
      promotion => promotion.id === payload.id
    )
    if (!promotionToUpdate) {
      return
    }

    promotionToUpdate.status.name = payload.enabled
      ? PromotionStatusName.ENABLED
      : PromotionStatusName.DISABLED
  },
  [UPDATE_PROMOTION_FAILURE](
    state: State,
    error: UpdatePromotionFailurePayload
  ) {
    state.uiState = { ...state.uiState, promotionsUpdateApiError: error }
  },
  [UPDATE_NEWLY_CREATED_PROMOTION_ID](
    state: State,
    payload: UpdateNewlyCreatedPromotionIdPayload
  ) {
    state.uiState = { ...state.uiState, newlyCreatedPromotionId: payload }
  },
  [GET_PROMOTION_QUALITY_TIERS_SUCCESS](
    state: State,
    payload: GetPromotionQualityTiersSuccessPayload
  ) {
    state.uiState = {
      ...state.uiState,
      promotionQualityTiersGetApiError: null
    }
    state.dataState = { ...state.dataState, promotionQualityTiers: payload }
  },
  [GET_PROMOTION_QUALITY_TIERS_FAILURE](
    state: State,
    error: GetPromotionQualityTiersFailurePayload
  ) {
    state.uiState = {
      ...state.uiState,
      promotionQualityTiersGetApiError: error
    }
  },
  [GET_PROMOTION_QUALITY_SUCCESS](
    state: State,
    payload: GetPromotionQualitySuccessPayload
  ) {
    state.uiState = {
      ...state.uiState,
      promotionQualityGetApiError: null
    }
    state.dataState = {
      ...state.dataState,
      currentPromotionQualityTier: payload
    }
  },
  [GET_PROMOTION_QUALITY_FAILURE](
    state: State,
    error: GetPromotionQualityFailurePayload
  ) {
    state.uiState = {
      ...state.uiState,
      promotionQualityGetApiError: error
    }
  },
  [UPDATE_PROMOTION_QUALITY_LOADING](
    state: State,
    payload: PromotionsLoadingPayload
  ) {
    state.uiState = { ...state.uiState, promotionQualityLoading: payload }
  }
}
