import {
  UpdateOnlineStatusPayload,
  UPDATE_ONLINE_STATUS,
  UPDATE_IS_OFFLINE_BANNER_VISIBLE,
  LOG_REQUEST,
  UpdateIsOfflineBannerVisible,
  LoggedRequest,
  CLEAR_LOGGED_REQUESTS,
  ENABLE_SCROLL,
  DISABLE_SCROLL,
  SET_IS_SEARCHING,
  SetIsSearchingPayload
} from '@/modules/core/store/action-types'
import _Store from '@/modules/store'
import { State } from '@/modules/core/store/state'
import { ONE_SECOND_IN_MILLIS } from '@/modules/core/helpers'
import { GET_TOGGLES_GETTER } from '@/modules/feature-toggles'
import {
  disableBodyScroll,
  enableBodyScroll
} from '@/components/common/ui/modal/scroll-helpers'

export const mutations = {
  [UPDATE_ONLINE_STATUS](state: State, payload: UpdateOnlineStatusPayload) {
    state.dataState.isOnline = payload
  },
  [UPDATE_IS_OFFLINE_BANNER_VISIBLE](
    state: State,
    payload: UpdateIsOfflineBannerVisible
  ) {
    state.uiState.isOfflineBannerVisible = payload
  },
  [LOG_REQUEST](state: State, payload: LoggedRequest) {
    // Only keep recent requests in requestsLog for better performance
    const RECENT_REQUEST_TIMESTAMP_THRESHOLD =
      _Store.store.getters[GET_TOGGLES_GETTER]
        .FE_PARTNERS_POOR_CONNECTIVITY_TIME_LIMIT * ONE_SECOND_IN_MILLIS

    const recentRequests = state.dataState.requestsLog.filter(
      (request: LoggedRequest) =>
        request.timestamp > Date.now() - RECENT_REQUEST_TIMESTAMP_THRESHOLD
    )
    state.dataState.requestsLog = [...recentRequests, payload]
  },
  [CLEAR_LOGGED_REQUESTS](state: State) {
    state.dataState.requestsLog = []
  },
  [ENABLE_SCROLL](state: State) {
    if (!state.uiState.scrollEnabled) {
      enableBodyScroll()
      state.uiState.scrollEnabled = true
    }
  },
  [DISABLE_SCROLL](state: State) {
    if (state.uiState.scrollEnabled) {
      disableBodyScroll()
      state.uiState.scrollEnabled = false
    }
  },
  [SET_IS_SEARCHING]({ uiState }: State, payload: SetIsSearchingPayload) {
    uiState.isSearching = payload
  }
}
