import { Module } from 'vuex'
import authApiFactory from '../api/auth'
import { actionsFactory } from './actions'
import { getters } from './getters'
import { mutations } from './mutations'
import { initialState as state } from './state'
import type VueI18n from 'vue-i18n'
import type VueRouter from 'vue-router'
import type { AbstractFetchService } from '@/modules/http'
import type { AbstractErrorTracker } from '@/modules/observability'
import type { State } from './state'
import type { State as RootState } from '@/modules/store'

export type { State }
export {
  GET_AVAILABLE_STORE_ADDRESS_IDS_GETTER,
  GET_CURRENT_STORE_ADMIN_GETTER,
  GET_SELECTED_STORE_ADDRESS_IDS_GETTER,
  GET_SELECTED_CITIES,
  IS_ADMIN_MODE_GETTER,
  IS_AUTHENTICATED_GETTER,
  GET_ADMIN_ROUTES_GETTER,
  IS_STORE_ADMIN_LOADED_GETTER
} from './getters'
export {
  LOG_IN,
  LOG_OUT,
  RESTORE_SELECTED_STORE_ADDRESS_IDS,
  SET_ADMIN_MODE,
  SET_SELECTED_STORE_ADDRESS_IDS
} from './action-types'
export { authentication } from './namespace'

export function storeFactory(
  fetchService: AbstractFetchService,
  router: VueRouter,
  errorTracker: AbstractErrorTracker,
  i18n: VueI18n
): Module<State, RootState> {
  const authApi = authApiFactory(fetchService)
  return {
    namespaced: true,
    actions: actionsFactory(authApi, router, errorTracker, i18n),
    mutations,
    state,
    getters
  }
}
