import { State } from './state'

export const GET_IS_OPEN_GETTER = 'GET_IS_OPEN_GETTER'
export const GET_CLOSING_DAYS_GETTER = 'GET_CLOSING_DAYS_GETTER'
export const GET_CLOSED_STORE_ADDRESS_IDS_GETTER =
  'GET_CLOSED_STORE_ADDRESS_IDS_GETTER'
export const GET_IS_CHECK_IN_PENDING_GETTER = 'GET_IS_CHECK_IN_PENDING_GETTER'
export const GET_IS_CHECK_IN_CONFIRMATION_LOADING_GETTER =
  'GET_IS_CHECK_IN_CONFIRMATION_LOADING_GETTER'
export const GET_IS_ALLOWED_TO_CHOOSE_CLOSING_TYPE_GETTER =
  'GET_IS_ALLOWED_TO_CHOOSE_CLOSING_TYPE_GETTER'
export const GET_SHOULD_PLAY_CHECKIN_NOTIFICATION_GETTER =
  'GET_SHOULD_PLAY_CHECKIN_NOTIFICATION_GETTER'
export const GET_HAS_DISMISSING_CHECK_IN_FAILED_GETTER =
  'GET_HAS_DISMISSING_CHECK_IN_FAILED_GETTER'
export const GET_SHOULD_SHOW_STORE_CLOSING_MODAL_GETTER =
  'GET_SHOULD_SHOW_STORE_CLOSING_MODAL_GETTER'
export const GET_IS_START_OF_SHIFT_GETTER = 'GET_IS_START_OF_SHIFT_GETTER'

export const getters = {
  [GET_IS_OPEN_GETTER](state: State) {
    return state.isOpen
  },
  [GET_CLOSING_DAYS_GETTER](state: State) {
    return state.closingDays
  },
  [GET_CLOSED_STORE_ADDRESS_IDS_GETTER](state: State) {
    return state.closedStoreAddressIds
  },
  [GET_IS_CHECK_IN_CONFIRMATION_LOADING_GETTER]({ uiState }: State) {
    return uiState.isCheckinConfirmationLoading
  },
  [GET_IS_CHECK_IN_PENDING_GETTER]({ dataState }: State) {
    return dataState.checkIn.isCheckInPending
  },
  [GET_SHOULD_PLAY_CHECKIN_NOTIFICATION_GETTER]({ dataState }: State) {
    return dataState.checkIn.shouldPlayNotification
  },
  [GET_IS_ALLOWED_TO_CHOOSE_CLOSING_TYPE_GETTER]({ dataState }: State) {
    return dataState.checkIn.isAllowedToChooseClosingType
  },
  [GET_IS_START_OF_SHIFT_GETTER]({ dataState }: State) {
    return dataState.checkIn.isStartOfShift
  },
  [GET_HAS_DISMISSING_CHECK_IN_FAILED_GETTER]({ uiState }: State) {
    return uiState.hasDismissingCheckInFailed
  },
  [GET_SHOULD_SHOW_STORE_CLOSING_MODAL_GETTER]({ uiState }: State) {
    return uiState.shouldShowStoreClosingModal
  }
}
