import { plainToClass } from 'class-transformer'
import { City } from '@/modules/store/data/resources'

export default class User {
  static parse(userData = {}) {
    return plainToClass(User, userData)
  }
  name: string
  email: string
  id: number
  urn: string
  store: Record<string, any>
  restrictedViews: string[]
  supervisorPassword: string
  allowedToManageOrders: boolean
  csPhoneNumber: string
  city: City

  constructor() {
    this.name = ''
    this.email = ''
    this.id = -1
    this.urn = ''
    this.store = {}
    this.restrictedViews = []
    this.supervisorPassword = ''
    this.allowedToManageOrders = false
    this.csPhoneNumber = ''
    this.city = {
      code: '',
      name: '',
      currency: '',
      currencyPattern: '',
      countryCode: '',
      languageCode: '',
      timeZone: '',
      stores: [],
      storeIds: []
    }
  }

  isAllowedToManageOrders() {
    return this.allowedToManageOrders
  }

  isSuperGlovo() {
    return this.store.superGlovo
  }

  exists() {
    return this.id !== -1
  }

  storeName() {
    return this.store.name
  }

  storeAddress() {
    return this.store.storeAddresses[0]
  }

  getSAId() {
    return this.store.storeAddresses && this.store.storeAddresses.length > 0
      ? this.store.storeAddresses[0].id
      : -1
  }

  isMCD() {
    return this.store.storeAddresses && this.store.storeAddresses.length > 0
      ? this.store.storeAddresses[0].mcd
      : false
  }

  supportsDelays() {
    return this.store.storeAddresses && this.store.storeAddresses.length > 0
      ? this.store.storeAddresses[0].delayingOrdersSupported
      : false
  }

  cityCode() {
    return this.city && this.city.code
  }

  schedule() {
    if (!this.store.storeAddresses) {
      return []
    }
    // We need to shift the times 1 day and fill empty days
    const oldSchedule = this.store.storeAddresses[0].openingTimes
    const newSchedule = []
    for (let i = 1; i < 8; i++) {
      if (i === 7) {
        newSchedule.push(Object.assign([], oldSchedule['1']))
      } else {
        newSchedule.push(Object.assign([], oldSchedule[`${i + 1}`]))
      }
    }

    // Normalize data
    const emptyDay = { opening: null, closing: null }
    newSchedule.forEach(day => {
      if (day.length === 0) {
        day.push(emptyDay)
      }

      if (day.length === 1) {
        day.push(emptyDay)
      }
    })
    return newSchedule
  }
}
