import moment from 'moment-timezone'
import { safeCall } from '@/modules/shared'

class CurrencyService {
  locale: string
  currency: string
  formatter: Intl.NumberFormat
  constructor() {
    this.currency = 'EUR'
    this.locale = navigator.language || 'es-ES'
    this.formatter = new Intl.NumberFormat(this.locale, {
      style: 'currency',
      currency: this.currency
    })
  }

  setCurrency(currency = 'EUR', locale: string | null, timeZone: string) {
    moment.tz.setDefault(timeZone)
    if (this.currency === currency) {
      return
    }
    this.locale = locale || this.locale
    this.currency = currency
    this.formatter = new Intl.NumberFormat(this.locale, {
      style: 'currency',
      currency
    })
  }

  format(value: string | number) {
    if (typeof value === 'string') {
      return value
    } else {
      return this.formatter.format(value)
    }
  }

  getCurrencySymbol(): string {
    return (
      safeCall(() => {
        const parts = this.formatter.formatToParts(0)
        return parts.find(({ type }) => type === 'currency')!.value
      }) || ''
    )
  }
}

export default new CurrencyService()
