
import Component from 'vue-class-component'
import { Mixins, Prop, Watch } from 'vue-property-decorator'

import Icon from 'common/Icon.vue'
import { WatchedModel } from '@/components/mixins'
import {
  DISABLE_SCROLL,
  ENABLE_SCROLL
} from '@/modules/core/store/action-types'
import { core } from '@/modules/core/store/namespace'
import { StoreAction } from '@/modules/store/types/store'
import { Alignment } from '@/components/types'

@Component({
  name: 'GlovoModal',
  components: {
    Icon
  }
})
export default class GlovoModal extends Mixins(WatchedModel) {
  @Prop({ default: true }) closeOnEsc!: boolean
  @Prop({ default: true }) closeOnClickOutside!: boolean
  @Prop({ default: true }) showCloseButton!: boolean
  @Prop({ default: null }) maxWidth!: string | null
  @Prop({ default: false }) preloadContent!: boolean
  @Prop({ default: 'stretch' }) alignButtons!: Alignment

  @core.Action(ENABLE_SCROLL)
  enableScroll!: StoreAction<void>

  @core.Action(DISABLE_SCROLL)
  disableScroll!: StoreAction<void>

  beforeDestroy() {
    window.removeEventListener('keyup', this.onKeyUp)
    this.enableScroll()
  }

  @Watch('internalValue')
  handleEventListeners() {
    if (this.internalValue) {
      window.addEventListener('keyup', this.onKeyUp)
      this.disableScroll()
    } else {
      window.removeEventListener('keyup', this.onKeyUp)
      this.enableScroll()
    }
  }

  onMaskClick() {
    if (this.closeOnClickOutside) {
      this.closeModal()
    }
  }

  onCloseButtonClick() {
    this.closeModal()
  }

  onKeyUp(event: KeyboardEvent) {
    const ESC_KEYS = ['Esc', 'Escape']

    if (this.closeOnEsc && ESC_KEYS.includes(event.key)) {
      this.closeModal()
    }
  }

  private closeModal() {
    this.$emit('close')
    this.internalValue = false
  }

  // Note: testing CSS custom properties is not possible because the wrapper's API gets rid of them.
  // So, beware this is especially sensitive code when refactoring
  get cssVariables() {
    return this.maxWidth ? { '--modal-max-width': this.maxWidth } : {}
  }
}
