import { root, host } from '@/api/localConfig'
import { MAX_RETRIES, MAX_RETRIES_INCREASED } from '@/api/networkConfig'
import { AbstractFetchService } from '@/modules/http'
import { TokenInformation } from '@/modules/authentication/store/state'
import { StoreAdmin } from '@/modules/store/data/resources'
import { AxiosError } from 'axios'
import { Store } from 'vuex'
import _Store, { State } from '@/modules/store'
import { GET_TOGGLES_GETTER } from '@/modules/feature-toggles'
import { DEFAULT_TIMEOUT } from '@/modules/http/services/fetch/network-fetch-service/network-fetch-service'
import { ServerStaticErrorCodes } from '@/modules/http/services/fetch/network-fetch-service/constants'
import { ONE_MINUTE_IN_MILLIS } from '@/modules/core/helpers'

export const REFRESH_TOKEN_TIMEOUT = ONE_MINUTE_IN_MILLIS

export interface RefreshTokenPayload {
  grantType: 'REFRESH_TOKEN'
  refreshToken: string
}
export interface GetTokenPayload {
  grantType: 'PASSWORD'
  userType: 'STORE_ADMIN'
  username: string
  password: string
}

export function isNetworkError(e: AxiosError) {
  return Boolean(e.message && e.message.includes('Network Error'))
}

export function isInvalidTokenError(e: AxiosError) {
  return Boolean(
    e.response &&
      e.response.data &&
      e.response.data.error &&
      e.response.data.error.staticCode ===
        ServerStaticErrorCodes.INVALID_ACCESS_TOKEN
  )
}

const REFRESH_ENDPOINT = `${root}/oauth/refresh`

export default function (
  fetch: AbstractFetchService,
  store: Store<State> = _Store.store
) {
  return {
    token: (credentials: GetTokenPayload) =>
      fetch.post<TokenInformation>(
        `${root}/oauth/token`,
        null,
        credentials,
        REFRESH_TOKEN_TIMEOUT,
        MAX_RETRIES
      ),
    refreshToken: (credentials: RefreshTokenPayload) => {
      const isLogoutMitigationEnabled =
        store.getters[GET_TOGGLES_GETTER].FE_PARTNERS_LOG_OUT_MITIGATION_ENABLED

      if (isLogoutMitigationEnabled) {
        return refreshTokenWithExponentialBackoff(fetch, credentials)
      }
      return performOldRefreshTokenLogic(fetch, store, credentials)
    },
    me: () =>
      fetch.get<StoreAdmin>(
        `${host}/me`,
        undefined,
        undefined,
        undefined,
        MAX_RETRIES
      )
  }
}

function refreshTokenWithExponentialBackoff(
  fetch: AbstractFetchService,
  credentials: RefreshTokenPayload
) {
  const RETRIES_LIMIT = 100

  return fetch.post<TokenInformation>(
    REFRESH_ENDPOINT,
    null,
    credentials,
    DEFAULT_TIMEOUT,
    RETRIES_LIMIT,
    {},
    {},
    (error: AxiosError) => !isInvalidTokenError(error), // retry if it's not an invalid token error
    true, // enable exponential backoff
    true // omit cancel tokens
  )
}

function performOldRefreshTokenLogic(
  fetch: AbstractFetchService,
  store: Store<State>,
  credentials: RefreshTokenPayload
) {
  return fetch.post<TokenInformation>(
    REFRESH_ENDPOINT,
    null,
    credentials,
    REFRESH_TOKEN_TIMEOUT,
    MAX_RETRIES_INCREASED,
    {},
    {},
    (error: AxiosError) => isNetworkError(error)
  )
}
