
import Vue from 'vue'
import {
  IS_AUTHENTICATED_GETTER,
  LOG_OUT,
  authentication
} from '@/modules/authentication'
import {
  GET_IS_OPEN_GETTER,
  GET_IS_CHECK_IN_PENDING_GETTER,
  storeManagement
} from '@/modules/store-management/store'
import CheckInOverlay from '@/modules/orders/components/check-in/CheckInOverlay.vue'
import {
  GET_IS_OFFLINE_BANNER_VISIBLE_GETTER,
  GET_IS_POOR_CONNECTIVITY_BANNER_VISIBLE_GETTER
} from '@/modules/core/store/getters'
import AudioService, { Sounds } from '@/services/audio/AudioService'
import Component from 'vue-class-component'
import { Action, Getter } from 'vuex-class'
import { ToBeMigrated } from 'types/any'
import { FeatureToggle } from '@/modules/feature-toggles/store/state'
import { core } from '@/modules/core/store/namespace'
import { Watch } from 'vue-property-decorator'
import { DisplayType } from '@/modules/store/services/display-types'
import { StoreAddressId } from '@/modules/store/data/resources'
import TopBar from './top/TopBar.vue'
import Categories from './navigation/Categories.vue'
import { StoreAction } from '@/modules/store/types/store'
import { arePromotionsEnabled } from '@/modules/promotions'
import {
  GET_TOGGLES_GETTER,
  TOGGLES_LOADED_GETTER
} from '@/modules/feature-toggles'
import {
  GET_ENABLED_STORE_ADDRESS_IDS_GETTER,
  IS_MULTIVIEW_GETTER
} from '@/modules/authentication/store/getters'
import { GET_SHOULD_PLAY_CHECKIN_NOTIFICATION_GETTER } from '@/modules/store-management/store/getters'
import CancelledOrderDialog from '@/modules/orders/components/list/CancelledOrderDialog.vue'

@Component({
  name: 'MainPage',
  components: {
    TopBar,
    Categories,
    CancelledOrderDialog,
    CheckInOverlay
  }
})
export default class MainPage extends Vue {
  statusTimeout: NodeJS.Timeout | null = null

  @Action('initCategories') initCategories: ToBeMigrated
  @Action('enableCategory') enableCategory: ToBeMigrated
  @authentication.Action(LOG_OUT)
  logout!: StoreAction<void>

  @Getter(GET_TOGGLES_GETTER) toggles!: Record<FeatureToggle, boolean>
  @Getter(TOGGLES_LOADED_GETTER) areTogglesLoaded!: boolean

  @Getter('display') display!: DisplayType
  @storeManagement.Getter(GET_IS_OPEN_GETTER) isOpen!: boolean
  @storeManagement.Getter(GET_IS_CHECK_IN_PENDING_GETTER)
  isPendingCheckIn!: boolean
  @storeManagement.Getter(GET_SHOULD_PLAY_CHECKIN_NOTIFICATION_GETTER)
  shouldPlayCheckinNotification!: boolean
  @core.Getter(GET_IS_OFFLINE_BANNER_VISIBLE_GETTER)
  isOfflineBannerVisible!: boolean
  @core.Getter(GET_IS_POOR_CONNECTIVITY_BANNER_VISIBLE_GETTER)
  isPoorConnectionBannerVisible!: boolean
  @authentication.Getter(IS_AUTHENTICATED_GETTER) isAuthenticated!: boolean
  @authentication.Getter(GET_ENABLED_STORE_ADDRESS_IDS_GETTER)
  enabledStoreAddressIds!: StoreAddressId[]
  @authentication.Getter(IS_MULTIVIEW_GETTER) isMultiview!: boolean

  @Watch('checkinPending') checkinPendingSoundHandler() {
    if (!this.toggles.FE_PARTNERS_WEB_CHECK_IN_ENABLED) return false

    if (this.shouldPlayCheckinNotificationSound) {
      AudioService.playSound(Sounds.checkin)
    } else {
      AudioService.stopSound(Sounds.checkin)
    }
  }

  @Watch('areTogglesLoaded') onAreTogglesLoadedChange() {
    this.enablePromotionsCategory()
  }

  beforeMount() {
    this.init()
  }

  async init() {
    if (this.isAuthenticated) {
      this.initCategories()
      this.enablePromotionsCategory()
    } else {
      this.logout()
      this.$errorMessage(this.$t('login.auth.error'))
    }
    this.checkinPendingSoundHandler()
  }

  enablePromotionsCategory() {
    if (arePromotionsEnabled(this.toggles)) {
      this.enableCategory('PROMOTIONS')
    }
  }

  get storeAddressId() {
    return this.enabledStoreAddressIds[0]
  }

  get shouldShowConnectivityBanner() {
    return this.isOfflineBannerVisible || this.isPoorConnectionBannerVisible
  }

  get isInOrdersPage() {
    return this.$route.path === '/orders'
  }

  get checkinPending() {
    return (
      this.isPendingCheckIn &&
      !this.isMultiview &&
      this.toggles.FE_PARTNERS_WEB_CHECK_IN_ENABLED
    )
  }

  get shouldShowCheckinOverlay() {
    return this.checkinPending && this.isInOrdersPage
  }

  get shouldPlayCheckinNotificationSound() {
    return this.checkinPending && this.shouldPlayCheckinNotification
  }
}
