import { Store } from 'vuex'
import { State } from '@/modules/store'
import telemetry from '@/modules/telemetry/telemetry'
import mParticle from '@/modules/mparticle'
import { getDeviceId } from '@/modules/shared'
import { GET_CURRENT_STORE_ADMIN_GETTER } from '@/modules/authentication'
import { BusinessEventNames } from 'models/BusinessEvents'

export type EventSeverity = 'minor' | 'moderate' | 'major' | 'severe'

export enum EventSeverityThreshold {
  minor = 2000,
  moderate = 5000,
  major = 10000,
  severe = 20000
}

function getEventSeverity(eventLength: number): EventSeverity {
  if (eventLength > EventSeverityThreshold.severe) return 'severe'
  else if (eventLength > EventSeverityThreshold.major) return 'major'
  else if (eventLength > EventSeverityThreshold.moderate) return 'moderate'
  else return 'minor'
}

/**
 * **Responsiveness Monitoring Service** : Detects anomalies in the browser
 * rendering framerate values using the `requestAnimationFrame` method.
 * When the interval between two calls exceeds the set thresholds, it determines
 * the severity of the event and emits metrics. The monitoring is disabled
 * when app losses focus (minimized, background...) to prevent false positives,
 * when focus is set back.
 */
export default class ResponsivenessMonitoringService {
  constructor(store: Store<State>) {
    this.store = store
    this.tick = this.tick.bind(this)
    this.onVisibilityChange = this.onVisibilityChange.bind(this)
  }

  private store: Store<State>
  private isRunning = false
  private lastTickTimestamp: number | null = null
  private timer: number | null = null

  public start(): void {
    if (this.isRunning) return
    this.isRunning = true
    this.lastTickTimestamp = null
    document.addEventListener(
      'visibilitychange',
      this.onVisibilityChange,
      false
    )
    this.nextTickSchedule()
  }

  public stop(): void {
    if (!this.isRunning) return
    this.isRunning = false
    document.removeEventListener(
      'visibilitychange',
      this.onVisibilityChange,
      false
    )
    this.nextTickUnschedule()
  }

  private tick(now: number = Date.now()): void {
    const isFirstIteration = this.lastTickTimestamp === null
    const elapsedTime = now - (this.lastTickTimestamp || 0)
    this.lastTickTimestamp = now
    if (!isFirstIteration && elapsedTime >= EventSeverityThreshold.minor) {
      this.logUnresponsiveEvent(elapsedTime)
    }
    this.nextTickSchedule()
  }

  private nextTickSchedule() {
    this.nextTickUnschedule()
    this.timer = window.requestAnimationFrame(this.tick)
  }

  private nextTickUnschedule() {
    if (!this.timer) return
    window.cancelAnimationFrame(this.timer)
    this.timer = null
  }

  private onVisibilityChange(): void {
    if (document.hidden) this.nextTickUnschedule()
    else {
      this.lastTickTimestamp = null
      this.nextTickSchedule()
    }
  }

  private getStoreAdminId() {
    const admin = this.store.getters[
      `authentication/${GET_CURRENT_STORE_ADMIN_GETTER}`
    ]
    return (admin && admin.id) || ''
  }

  private logUnresponsiveEvent(elapsedTime: number) {
    const severity = getEventSeverity(elapsedTime)
    const tags = { severity }
    const storeAdminId = this.getStoreAdminId()
    telemetry.Metric.count('app_unresponsive_event', 1, { tags })
    telemetry.Metric.gauge('app_unresponsive_length', elapsedTime, { tags })
    telemetry.Log.info(
      'app_unresponsive_event',
      `App unresponsive (severity=${severity})`,
      {
        attr: {
          'event-severity': severity,
          'elapsed-time': elapsedTime,
          'device-id': getDeviceId()
        }
      }
    )

    mParticle.logEvent(
      BusinessEventNames.appUnresponsiveEvent,
      mParticle.EventType.UserContent,
      {
        storeAdminId,
        deviceId: getDeviceId(),
        eventSeverity: severity,
        elapsedTime: elapsedTime
      }
    )
  }
}
