import { State } from './state'

export const GET_STORE_PRODUCTS_BY_ID = 'GET_STORE_PRODUCTS_BY_ID'
export const GET_STORE_PRODUCTS_BY_STORE_ID = 'GET_STORE_PRODUCTS_BY_STORE_ID'
export const GET_STORE_PRODUCTS_ERROR_GETTER = 'GET_STORE_PRODUCTS_ERROR_GETTER'
export const GET_STORE_PRODUCTS_LOADING_GETTER =
  'GET_STORE_PRODUCTS_LOADING_GETTER'

export const getters = {
  [GET_STORE_PRODUCTS_BY_ID](state: State) {
    return state.dataState.products.byId
  },
  [GET_STORE_PRODUCTS_BY_STORE_ID](state: State) {
    return state.dataState.products.byStoreId
  },
  [GET_STORE_PRODUCTS_ERROR_GETTER](state: State) {
    return state.uiState.getStoreProductsApiError
  },
  [GET_STORE_PRODUCTS_LOADING_GETTER](state: State) {
    return state.uiState.storeProductsLoading
  }
}
