import { analyticsEvents } from '../analytics-library'
import {
  StoreAddressId,
  ProductId,
  AttributeId,
  StoreAdminId
} from '@/modules/store/data/resources'

// TD Task added to fix ts-ignore - https://glovoapp.atlassian.net/browse/PAR-2254

export const stockOutEvents = {
  // PRODUCTS
  onProductCancelPressed(storeAddressId: StoreAddressId): void {
    const { StockoutProductCancelPressed } = analyticsEvents
    // @ts-ignore
    this.trackMParticleEvent(new StockoutProductCancelPressed(storeAddressId))
  },
  onProductPriceSavePressed(
    productId: ProductId,
    storeAddressId: StoreAddressId,
    storeAdminId: StoreAdminId,
    oldPrice: number,
    newPrice: string
  ): void {
    const { AcceptProductPriceChangePressed } = analyticsEvents
    // @ts-ignore
    this.trackMParticleEvent(
      new AcceptProductPriceChangePressed(
        productId,
        storeAddressId,
        storeAdminId,
        oldPrice,
        parseFloat(newPrice)
      )
    )
  },

  // ATTRIBUTES
  onAttributePriceSavePressed(
    attributeId: AttributeId,
    storeAddressId: StoreAddressId,
    storeAdminId: StoreAdminId,
    oldPrice: number,
    newPrice: string
  ): void {
    const { AcceptAttributePriceChangePressed } = analyticsEvents
    // @ts-ignore
    this.trackMParticleEvent(
      new AcceptAttributePriceChangePressed(
        attributeId,
        storeAddressId,
        storeAdminId,
        oldPrice,
        parseFloat(newPrice)
      )
    )
  }
}
