<template>
  <div></div>
</template>
<script>
import { mapGetters } from 'vuex'
export default {
  name: 'Blank',
  computed: {
    ...mapGetters(['defaultCategory'])
  },
  watch: {
    defaultCategory() {
      this.init()
    }
  },
  mounted() {
    this.init()
  },
  methods: {
    init() {
      if (this.defaultCategory) {
        this.$router.push({ name: this.defaultCategory.route })
      }
    }
  }
}
</script>
<style lang="scss"></style>
