import { Store } from 'npm/vuex'
import { State } from '@/modules/store'
import mParticleInstance, { EventTracker } from '@/modules/mparticle'
import Storage from 'models/Storage'
import { GET_CURRENT_STORE_ADMIN_GETTER } from '@/modules/authentication'
import { BusinessEventNames } from 'models/BusinessEvents'
import telemetryInstance, { Telemetry } from '@/modules/telemetry/telemetry'

class VisibilityService {
  private store: Store<State>
  private readonly mParticle: EventTracker
  private telemetry: Telemetry

  constructor(
    store: Store<State>,
    mParticle: EventTracker = mParticleInstance,
    telemetry: Telemetry = telemetryInstance
  ) {
    this.store = store
    this.mParticle = mParticle
    this.telemetry = telemetry
    this.logVisibilityEvent = this.logVisibilityEvent.bind(this)
  }

  start() {
    window.addEventListener('visibilitychange', this.logVisibilityEvent)
  }

  stop() {
    window.removeEventListener('visibilitychange', this.logVisibilityEvent)
  }

  // Potentially add Application Quit event https://glovoapp.atlassian.net/browse/PAR-1059
  private logVisibilityEvent() {
    const event: BusinessEventNames = document.hidden
      ? BusinessEventNames.applicationHidden
      : BusinessEventNames.applicationShown

    const { logEvent, EventType } = this.mParticle
    logEvent(event, EventType.UserContent, {
      deviceId: this.getDeviceId(),
      storeAdminId: this.getStoreAdminId()
    })
    const tags = {
      'visibility-status':
        event === BusinessEventNames.applicationShown ? 'SHOWN' : 'HIDDEN'
    }
    this.telemetry.Metric.count(`visibility_status_change`, 1, { tags })
  }

  private getDeviceId() {
    const device = Storage.get(Storage.items.DEVICE)
    return (device || {}).id || ''
  }

  private getStoreAdminId() {
    const admin = this.store.getters[
      `authentication/${GET_CURRENT_STORE_ADMIN_GETTER}`
    ]
    return (admin && admin.id) || ''
  }
}

export default VisibilityService
