import { Product } from '@/modules/store'
import { Attribute, RequestPriceUpdate } from '@/modules/store/data/resources'
import { State } from './state'

export const TEMPORARY_PRODUCTS_GETTER = 'TEMPORARY_PRODUCTS_GETTER'
export const TEMPORARY_ATTRIBUTES_GETTER = 'TEMPORARY_ATTRIBUTES_GETTER'
export const TEMPORARY_REQUEST_PRICE_UPDATE_GETTER =
  'TEMPORARY_REQUEST_PRICE_UPDATES_GETTER'
export const IS_GET_CONTENT_LOADING_GETTER = 'IS_GET_CONTENT_LOADING_GETTER'

export const getters = {
  [TEMPORARY_PRODUCTS_GETTER]: ({ dataState }: State) =>
    dataState.temporaryProducts,
  [TEMPORARY_ATTRIBUTES_GETTER]: ({ dataState }: State) =>
    dataState.temporaryAttributes,
  [TEMPORARY_REQUEST_PRICE_UPDATE_GETTER]: ({ dataState }: State) =>
    dataState.temporaryRequestPriceUpdate,
  [IS_GET_CONTENT_LOADING_GETTER]: ({ uiState }: State) =>
    uiState.isGetContentLoading
}

export interface Getters {
  [TEMPORARY_PRODUCTS_GETTER]: Product[]
  [TEMPORARY_ATTRIBUTES_GETTER]: Attribute[]
  [TEMPORARY_REQUEST_PRICE_UPDATE_GETTER]: RequestPriceUpdate | null
}
