import 'babel-polyfill'
import Vue from 'vue'
import I18n from '@/modules/i18n'
import { CoreModule } from '@/modules/core'
import Observability from '@/modules/observability'
import Router from '@/modules/router'
import Store from '@/modules/store'
import { StockModule } from '@/modules/stock'
import { OrdersModule } from '@/modules/orders'
import { AuthenticationModule } from '@/modules/authentication'
import { HttpModule } from '@/modules/http'
import { FeatureTogglesModule } from '@/modules/feature-toggles'
import {
  sentryUrl,
  gtmId,
  datadogRumKey,
  datadogRumAppId
} from '@/api/localConfig'
import { StoreManagementModule } from '@/modules/store-management'
import { TelemetryModule } from '@/modules/telemetry/telemetry-module'
import { AnalyticsModule } from '@/modules/analytics/analytics-module'
import { InvoicesModule } from '@/modules/invoices'
import { ProfileModule } from '@/modules/profile'
import { BenchmarkingModule } from '@/modules/benchmarking'
import { datadogRum, Datacenter } from '@datadog/browser-rum'
import { version } from '@/../version'
import { PromotionsModule } from '@/modules/promotions'
import { CmsModule } from '@/modules/cms'
import { LegalModule } from '@/modules/legal'
import { StoreCatalogsModule } from '@/modules/store-catalogs'

function setupMain() {
  Vue.config.productionTip = false

  HttpModule.install()
  const { fetchService } = HttpModule

  Observability.install(Vue, { sentryDSN: sentryUrl })

  Store.install(Vue, { deps: { fetchService } })
  const { store } = Store

  I18n.install(Vue)
  const { i18n } = I18n

  Router.install(Vue)
  const { router } = Router

  const featureTogglesModule = new FeatureTogglesModule(store, fetchService)
  featureTogglesModule.install()

  const authenticationModule = new AuthenticationModule(
    router,
    store,
    fetchService,
    i18n
  )
  authenticationModule.install(Vue)

  const cmsModule = new CmsModule()
  cmsModule.install()

  const legalModule = new LegalModule()
  legalModule.install()

  const storeCatalogsModule = new StoreCatalogsModule(store, fetchService)
  storeCatalogsModule.install()

  const ordersModule = new OrdersModule(store, fetchService)
  ordersModule.install()

  const stockModule = new StockModule(store, fetchService)
  stockModule.install()

  const storeManagementModule = new StoreManagementModule(store, fetchService)
  storeManagementModule.install()

  const invoicesModule = new InvoicesModule(store, fetchService)
  invoicesModule.install()

  const benchmarkingModule = new BenchmarkingModule(store)
  benchmarkingModule.install()

  const profileModule = new ProfileModule()
  profileModule.install()

  const promotionsModule = new PromotionsModule(store, fetchService)
  promotionsModule.install()

  const coreModule = new CoreModule(store)
  coreModule.install(Vue, {
    gtmId,
    deps: {
      router,
      store,
      i18n
    }
  })

  const telemetryModule = new TelemetryModule(Vue)
  telemetryModule.install()

  const analyticsModule = new AnalyticsModule(Vue)
  analyticsModule.install()

  datadogRum.init({
    applicationId: datadogRumAppId,
    clientToken: datadogRumKey,
    env: process.env.VUE_APP_MODE === 'production' ? 'production' : 'stage',
    service: 'partners-webapp',
    version,
    datacenter: Datacenter.US,
    resourceSampleRate: 5,
    sampleRate: 5
  })

  const { App } = coreModule.components
  // tslint:disable-next-line: no-unused-expression
  new Vue({
    el: '#app',
    router,
    i18n,
    store,
    components: { App },
    template: '<App/>'
  })
}

setupMain()
