import ErrorTracker from './error-tracker'
import NullTracker from './null-tracker'
import AbstractErrorTracker from './abstract-error-tracker'
import AbstractErrorTrackerOptions from './abstract-error-tracker-options'

const DISABLED_ENVIRONMENTS = ['development']

export default class ErrorTrackerFactory {
  static createErrorTracker(options: AbstractErrorTrackerOptions) {
    let ErrorTrackerClass: typeof AbstractErrorTracker
    const env = process.env.VUE_APP_MODE || 'development'

    if (DISABLED_ENVIRONMENTS.includes(env)) {
      const warning = `ErrorTracker not initialized in ${process.env.VUE_APP_MODE} mode. Run in test or production environment to enable
      the error tracking or modify ErrorTrackerPlugin file directly`
      // tslint:disable-next-line: no-console
      console.warn(warning)

      ErrorTrackerClass = NullTracker
    } else {
      ErrorTrackerClass = ErrorTracker
    }

    return new ErrorTrackerClass(options)
  }
}
