
import Component from 'vue-class-component'
import Vue from 'vue'
import { Action } from 'vuex-class'
import moment from 'moment'
import { DISPLAY_REPORTS_INVOICES } from '@/modules/store/services/display-types'
import { ToBeMigrated } from 'types/any'
import { invoices } from '@/modules/invoices/store/namespace'
import {
  GET_INVOICES,
  GetInvoicesPayload
} from '@/modules/invoices/store/action-types'
import { StoreAction } from '@/modules/store/types/store'
import {
  GET_INVOICES_GETTER,
  GET_INVOICES_LOADING_GETTER
} from '@/modules/invoices/store/getters'
import { GET_SELECTED_STORE_ADDRESS_IDS_GETTER } from '@/modules/authentication'
import { authentication } from '@/modules/authentication/store/namespace'
import { Invoice } from '@/modules/invoices/types'
import GlovoTable from 'common/ui/GlovoTable.vue'
import GlovoMonthSelector from 'common/ui/GlovoMonthSelector.vue'
import GlovoBtn from '@/components/common/GlovoBtn.vue'
import GlovoTableColumn from 'common/ui/GlovoTableColumn'
import { INVOICES_COLUMNS } from '@/modules/invoices/components/constants/invoices-columns'
import DateTimeService from '@/services/datetime/DateTimeService'
import { StoreAddressId } from '../../store/data/resources'
import { Watch } from 'vue-property-decorator'
import Icon from 'common/Icon.vue'
import { GET_CURRENT_STORE_ADMIN_GETTER } from '@/modules/authentication'
import { StoreAdmin } from '@/modules/store/data/resources'

@Component({
  name: 'Invoices',
  components: {
    Icon,
    GlovoBtn,
    GlovoTable,
    GlovoMonthSelector
  }
})
export default class Invoices extends Vue {
  currentMonth: string = moment().format('YYYY-MM')
  selectedMonth: string = moment().format('YYYY-MM')

  @Action('displayState') displayState!: ToBeMigrated

  @authentication.Getter(GET_SELECTED_STORE_ADDRESS_IDS_GETTER)
  getSelectedStoreAddressIds!: StoreAddressId[]

  @invoices.Action(GET_INVOICES) getInvoices!: StoreAction<GetInvoicesPayload>

  @invoices.Getter(GET_INVOICES_GETTER) invoices!: Invoice[]

  @invoices.Getter(GET_INVOICES_LOADING_GETTER) areInvoicesLoading!: boolean

  @authentication.Getter(GET_CURRENT_STORE_ADMIN_GETTER)
  storeAdmin!: StoreAdmin

  private loadInvoices() {
    this.getInvoices({
      storeAddressIds: this.getSelectedStoreAddressIds,
      yearMonth: this.selectedMonth
    })
  }

  @Watch('getSelectedStoreAddressIds')
  onSelectedStoreAddressChange() {
    this.loadInvoices()
  }

  mounted() {
    this.loadInvoices()
    this.displayState(DISPLAY_REPORTS_INVOICES)
  }

  get columns(): GlovoTableColumn[] {
    return INVOICES_COLUMNS.map(({ name, translationKey }) => ({
      name,
      label: this.$tc(translationKey).toUpperCase()
    }))
  }

  get hasInvoices(): boolean {
    return this.invoices.length > 0
  }

  updateSelectedMonth(yearMonth: string) {
    this.selectedMonth = yearMonth
    this.getInvoices({
      yearMonth,
      storeAddressIds: this.getSelectedStoreAddressIds
    })
  }

  formatDate(date: string) {
    return DateTimeService.formatAsDate(date)
  }

  logDownloadPdfEvent() {
    this.$telemetry.Metric.count('DOWNLOAD_INVOICE', 1, {
      tags: {
        country: this.storeAdmin.city.countryCode
      }
    })
  }

  downloadAllInvoices() {
    this.invoices.forEach(invoice => {
      this.logDownloadPdfEvent()
      window.open(invoice.url)
    })
  }
}
