/*eslint no-empty-pattern: "off"*/
import * as types from '@/modules/store/services/mutation-types'
import { beforeEnterServiceFactory } from '@/modules/authentication/services/before-enter/before-enter-service-factory'
import Storage from 'models/Storage'
import User from 'models/User'
import usersApiFactory from '@/api/user'
import { GET_STORE_ADDRESSES_GETTER } from '../data/getters'
import { ADD_STORE_ADDRESSES } from '../data/action-types'
import cloneDeep from 'lodash/cloneDeep'
import telemetry from '@/modules/telemetry/telemetry'
import { STORE_MANAGEMENT_MODULE_NAME } from '@/modules/store-management/store-managment-module'
import { GET_STATUS } from '@/modules/store-management/store/action-types'
import {
  KustomerProblem,
  ContentModificationsKustomerProblems
} from '@/modules/profile/types/KustomerProblem'

const state = {
  user: {},
  status: {
    open: false
  },
  closingDays: [],
  featureToggles: new Map(),
  ticketCopies: 1,
  communication: {}
}

const getters = {
  featureToggles: state => state.featureToggles,
  user: state => User.parse(state.user),
  status: state => state.status,
  open: state => state.status.open,
  closingDays: state => state.closingDays,
  ticketCopies: state => state.ticketCopies,
  communication: state => state.communication
}

export function actionsFactory({
  status,
  closingDays,
  getInvoicingContactDetailsByStoreAddressId,
  setInvoicingContactDetailsByStoreAddressId,
  getBusinessContactDetailsByStoreAddressId,
  setBusinessContactDetailsByStoreAddressId,
  getDailyOperationContactDetailsByStoreAddressId,
  setDailyOperationContactDetailsByStoreAddressId,
  setClosingDay,
  setClosingDaysByAddressIds,
  removeClosingDay,
  requestSupport,
  getFeatureToggles,
  updateSchedule,
  communication,
  markCommunicationAsViewed
}) {
  return {
    userLoggedIn({ commit, getters }, user) {
      telemetry.setDefaultAttribute('user-id', user.id)
      telemetry.setDefaultTag(
        'user-country',
        (user.city && user.city.countryCode) || 'unknown'
      )
      commit(types.AUTH_LOGGED_IN, user)
      beforeEnterServiceFactory().setUser(getters.user)
    },
    logout({ commit }) {
      telemetry.removeDefaultAttribute('user-id')
      telemetry.removeDefaultTag('user-country')
      commit(types.AUTH_LOG_ERROR)
    },
    syncTickets({ commit }) {
      if (Storage.get(Storage.items.COPIES)) {
        commit(types.SET_TICKET_COPIES, Storage.get(Storage.items.COPIES))
      } else {
        Storage.set(Storage.items.COPIES, 1)
        commit(types.SET_TICKET_COPIES, 1)
      }
    },
    async getStatus({ commit, getters }, storeAddressId) {
      const user = getters.user
      if (user && user.getSAId() === -1) {
        return
      }
      if (!storeAddressId) {
        storeAddressId = user.getSAId()
      }

      // Temporary fix because api.user.status does not support arrays
      const singleStoreAddressId = Array.isArray(storeAddressId)
        ? storeAddressId[0]
        : storeAddressId
      const statusResponse = await status(singleStoreAddressId)
      commit('GET_STATUS', statusResponse)
    },
    async getClosingDays({ commit }, storeAddressId) {
      if (
        !storeAddressId ||
        (Array.isArray(storeAddressId) && storeAddressId.length === 0)
      ) {
        return
      }
      // Temporary fix because api.user.closingDays does not support arrays
      const singleStoreAddressId = Array.isArray(storeAddressId)
        ? storeAddressId[0]
        : storeAddressId
      const intervals = await closingDays(singleStoreAddressId)
      commit('GET_CLOSING_DAYS', intervals.days)
    },
    async getInvoicingContactDetails(_, storeAddressId) {
      if (!storeAddressId) {
        return
      }
      return await getInvoicingContactDetailsByStoreAddressId(storeAddressId)
    },
    async setInvoicingContactDetails(_, { storeAddressId, contact }) {
      if (!storeAddressId) {
        return
      }
      await setInvoicingContactDetailsByStoreAddressId(storeAddressId, contact)
    },
    async getBusinessContactDetails(_, storeAddressId) {
      if (!storeAddressId) {
        return
      }
      return getBusinessContactDetailsByStoreAddressId(storeAddressId)
    },
    async setBusinessContactDetails(_, { storeAddressId, contact }) {
      if (!storeAddressId) {
        return
      }
      await setBusinessContactDetailsByStoreAddressId(storeAddressId, contact)
    },
    async getDailyOperationContactDetails(_, storeAddressId) {
      if (!storeAddressId) {
        return
      }
      return getDailyOperationContactDetailsByStoreAddressId(storeAddressId)
    },
    async setDailyOperationContactDetails(_, { storeAddressId, contact }) {
      if (!storeAddressId) {
        return
      }
      await setDailyOperationContactDetailsByStoreAddressId(
        storeAddressId,
        contact
      )
    },
    async setClosingDay(
      { dispatch, getters },
      { interval, storeAddressIds } = {}
    ) {
      const user = getters.user
      if (!user || user.getSAId() === -1) {
        return
      }
      if (storeAddressIds.length > 0) {
        await setClosingDaysByAddressIds(storeAddressIds, interval)
      } else {
        await setClosingDay(user.getSAId(), interval)
        await Promise.all([
          dispatch('getStatus', user.getSAId()),
          dispatch('getClosingDays', user.getSAId())
        ])
      }
    },
    async removeClosingDay(
      { dispatch, getters },
      { interval, storeAddressId } = {}
    ) {
      const user = getters.user
      if (user && user.getSAId() === -1) {
        return
      }
      await removeClosingDay(storeAddressId, interval)
      await Promise.all([
        dispatch('getStatus', storeAddressId),
        dispatch('getClosingDays', storeAddressId)
      ])
    },
    async requestSupport({ getters }, { id, form }) {
      const formToSend = mapSupportFormValues(form)
      if (id !== null) {
        await requestSupport(id, formToSend)
      } else {
        await requestSupport(getters.user.getSAId(), formToSend)
      }
    },
    async getToggles({ commit }) {
      // TODO delete in https://glovoapp.atlassian.net/browse/PAR-279
      for (const name of Object.values({})) {
        const response = await getFeatureToggles(name)
        commit(types.GET_TOGGLES, response)
      }
    },
    async updateSchedule({ rootGetters, dispatch }, data) {
      const openingTimes = {}
      for (const i in data.schedule) {
        const day = []
        for (const shift of data.schedule[i]) {
          if (shift.opening && shift.closing) {
            day.push(shift)
          }
        }
        openingTimes[i] = day
      }
      const storeAddressId = data.storeAddressId
      await updateSchedule(storeAddressId, openingTimes)
      const storeAddress = cloneDeep(
        rootGetters[GET_STORE_ADDRESSES_GETTER][storeAddressId]
      )
      storeAddress.openingTimes = cloneDeep(openingTimes)
      dispatch(ADD_STORE_ADDRESSES, [storeAddress], { root: true })
      dispatch(
        `${STORE_MANAGEMENT_MODULE_NAME}/${GET_STATUS}`,
        storeAddressId,
        { root: true }
      )
      return true
    },
    setTicketCopies({ commit }, number) {
      Storage.set(Storage.items.COPIES, number)
      commit('SET_TICKET_COPIES', number)
    },
    async getUnviewedCommunication({ commit }, storeAddressId) {
      if (!storeAddressId) {
        return
      }
      const response = await communication(storeAddressId)
      commit('GET_UNVIEWED_COMMUNICATION', response)
    },
    async markCommunicationAsViewed(_, { storeAddressId, communicationId }) {
      if (!storeAddressId || !communicationId) {
        return
      }
      await markCommunicationAsViewed(storeAddressId, {
        communicationId
      })
    }
  }
}

const mapSupportFormValues = form => {
  const changes = parseInt(form.changes)
  if (form.problem !== KustomerProblem.CONTENT_MODIFICATIONS) {
    return form
  }
  if (changes <= 5) {
    return {
      ...form,
      problem: ContentModificationsKustomerProblems.SMALL_CHANGE
    }
  } else if (changes >= 6) {
    return {
      ...form,
      problem: ContentModificationsKustomerProblems.BIG_CHANGE
    }
  } else {
    return form
  }
}

const mutations = {
  [types.AUTH_LOGGED_IN](state, user) {
    state.user = user
  },

  [types.AUTH_LOG_ERROR](state) {
    state.user = {}
    beforeEnterServiceFactory().reset()
  },
  [types.UPDATE_STATUS](state, status) {
    state.status = status
  },
  [types.GET_STATUS](state, status) {
    state.status = status
  },
  [types.GET_CLOSING_DAYS](state, days) {
    state.closingDays = days
  },
  [types.GET_TOGGLES](state, data) {
    for (const key of Object.keys(data)) {
      state.featureToggles.set(key, data[key])
    }
  },
  [types.SET_TICKET_COPIES](state, data) {
    state.ticketCopies = data
  },
  [types.GET_UNVIEWED_COMMUNICATION](state, data) {
    state.communication = data
  }
}

export default function (fetchService) {
  const usersApi = usersApiFactory(fetchService)
  return {
    state,
    getters,
    actions: actionsFactory(usersApi),
    mutations
  }
}
