import { User } from './user'
import { Customer } from './customer'
import { StoreAddressId } from '@/modules/store/data/resources/store-address'
import { ProductSummary } from '@/modules/store/data/resources/product-summary'
import { OrderStatus } from '@/modules/store/data/resources/order-status'
import { PreparationStage } from '@/modules/orders/constants'
import { HHmm } from 'types/dates'
import { zero } from 'types/zero'

export type OrderId = number

export interface HistoryOrder {
  acceptanceTime: number | null
  allergyInfo: string | null
  badRatingReasons: BadRatingReason[] | null
  bundledOrders?: number[]
  canBeMarkedReadyForPickup: boolean
  cancelReason: CancelReason | null
  cancellationTime?: number | null
  cityName: string
  code: string
  companyAddressCity: string | null
  companyAddressLine1: string | null
  companyAddressLine2: string | null
  companyName: string | null
  companyPostalCode: string | null
  courier: User | null
  currentStatus: OrderStatus
  customer: Customer
  customerCashPaymentAmount: number | null
  deliveryAddress: { label: string; latitude: number; longitude: number }
  deliveryFee: number | null
  description: string
  originalDescription?: string
  discountsTotal: number
  dispatchingTime: number
  id: OrderId
  incidents: {
    reason: string
    resolution: string
    productToCharge: number
    productRefund: number
  } | null
  isCourierInPickingZone: boolean
  isCourierNearby: boolean
  isFirstOrder: boolean
  isNotificationPixelViewed?: boolean
  isNotificationPixelLoaded?: boolean
  isPickedUp: boolean
  isStacked: boolean
  marketplace: boolean
  mcd: boolean
  minBasketSurcharge: number
  minutesUntilPickup?: number | null
  newPickupEstimatedArrival: string | null
  partnerProductDiscounts: number | zero // discounts on the products + attributes, defaults to 0, comes from /orders/completed/filtered
  partnerStrategy: string | null
  paymentMethod: string
  pickupEstimatedArrival: string
  pickupEstimatedArrivalDiff: number | null
  pickupTime: number
  pickupTimeRange?: TimeRange
  pickupCode: string | null
  preparationStage: PreparationStage
  products: ProductSummary | null // the backend sends these as null at first, we add the products later, fix here - https://glovoapp.atlassian.net/browse/PAR-106
  productsTotal: number
  readyForPickupTime: number | null
  refundIncident: RefundIncident | null
  scheduledTime: string
  size: number // number of products in total
  storeAddress: string
  storeAddressDetails: string
  storeAddressId: StoreAddressId
  storeName: string
  storeTrackingCode: string | null // kitchen code
  storeTrackingCodeMaxLength: number
  taxId: string | null
  timeZone: string
  totalPrice: number //  Total price of order with discounts included. comes from /orders/completed/filtered endpoint.
  cutleryDetails: CutleryDetails | null
  servingTime?: number | null
  isCancellationAcknowledged: boolean
  notifications?: {
    hasPlayedCourierNearby?: boolean
  }
  isPickedUpByCustomer?: boolean
  isReady: boolean // i.e. has been prepared by the partner
  isOrderReadyButtonViewed?: boolean
}

export interface RefundIncident {
  reason: string
  partnerResolution: string
  affectedProducts: string
  chargedProducts: string
  additionalDetails: string
  chargedToPartner: number
  refundedToCustomer: number
}

interface CutleryDetails {
  cutleryRequested: boolean
}

interface TimeRange {
  start: HHmm
  end: HHmm
}

export const enum BadRatingReason {
  WRONG_OR_MISSING_PRODUCT = 'WRONG_OR_MISSING_PRODUCT',
  PRICING_PROBLEM = 'PRICING_PROBLEM',
  BAD_PRODUCT_QUALITY = 'BAD_PRODUCT_QUALITY',
  TASTED_BAD = 'TASTED_BAD',
  PACKAGING_ISSUES = 'PACKAGING_ISSUES',
  WRONG_PRODUCTS = 'WRONG_PRODUCTS',
  MISSING_PRODUCTS = 'MISSING_PRODUCTS',
  POOR_QUALITY = 'POOR_QUALITY',
  ALLERGY_NOT_CONSIDERED = 'ALLERGY_NOT_CONSIDERED',
  CHARGED_WRONG_AMOUNT = 'CHARGED_WRONG_AMOUNT',
  SPECIAL_STORE_PROMOS = 'SPECIAL_STORE_PROMOS'
  // cannot be exhaustive from the backend, since it can also be any string
}

export const enum CancelReason {
  PARTNER_PRINTER_ISSUE = 'PARTNER_PRINTER_ISSUE',
  STORE_CAN_NOT_DELIVER = 'STORE_CAN_NOT_DELIVER',
  PRODUCTS_NOT_AVAILABLE = 'PRODUCTS_NOT_AVAILABLE',
  STORE_CLOSED = 'STORE_CLOSED'
}
