
import Component from 'vue-class-component'
import Vue from 'vue'
import { Prop } from 'vue-property-decorator'
import { OrderDetails } from '@/modules/store/data/resources/order-details'
import { currency } from '@/modules/core/filters'
import Order from '@/models/Order'

@Component({ name: 'NormalOrderBreakdownSummary' })
export default class NormalOrderBreakdownSummary extends Vue {
  @Prop() details!: OrderDetails

  get totalPriceOfProducts() {
    return currency(this.paymentDetails.totalProductsPrice)
  }

  get productDiscounts() {
    return currency(this.paymentDetails.partnerProductDiscounts)
  }

  get totalPrice() {
    return currency(this.paymentDetails.orderTotalPrice)
  }

  get isCashPayment() {
    return Order.paymentMethodIsCash(this.details)
  }

  get hasProductDiscounts() {
    return this.paymentDetails.partnerProductDiscounts !== 0
  }

  get paymentDetails() {
    return this.details.paymentDetails
  }
}
