
import Component from 'vue-class-component'
import Vue from 'vue'
import { Prop } from 'vue-property-decorator'

@Component({ name: 'Pair' })
export default class Pair extends Vue {
  @Prop({ default: '' })
  left!: string | number

  @Prop({ default: '' })
  right!: string | number

  @Prop({ default: '' })
  color!: string
}
