
import Vue from 'vue'
import Component from 'vue-class-component'
import { Prop, Watch } from 'vue-property-decorator'
import QuantitySelectorModal from 'common/QuantitySelectorModal.vue'
import { OrderDetails } from '@/modules/store/data/resources/order-details'
import { orderManagement } from '@/modules/orders/store/namespace'
import { GET_ORDER_PRODUCT_QUANTITY_MODAL_GETTER } from '@/modules/orders/store/getters'
import {
  CLOSE_ORDER_PRODUCT_QUANTITY_MODAL,
  EDIT_PRODUCT_QUANTITY,
  EditProductQuantityPayload,
  RESTORE_EDIT_PRODUCT_QUANTITY
} from '@/modules/orders/store/action-types'
import { StoreAction } from '@/modules/store/types/store'
import type { BoughtProductId } from '@/modules/store/data/resources/order-details'

@Component({
  name: 'OrderProductQuantityModal',
  components: {
    QuantitySelectorModal
  }
})
export default class OrderProductQuantityModal extends Vue {
  @Prop() order!: OrderDetails

  newQuantity = this.quantity

  @orderManagement.Getter(GET_ORDER_PRODUCT_QUANTITY_MODAL_GETTER)
  orderProductQuantityModal!: {
    isDisplayed: boolean
    productId: BoughtProductId
  }

  @orderManagement.Action(CLOSE_ORDER_PRODUCT_QUANTITY_MODAL)
  closeOrderProductQuantityModal!: StoreAction<void>

  @orderManagement.Action(EDIT_PRODUCT_QUANTITY)
  editProduct!: StoreAction<EditProductQuantityPayload>

  @orderManagement.Action(RESTORE_EDIT_PRODUCT_QUANTITY)
  restoreEditProductQuantity!: StoreAction<void>

  get isVisible() {
    return (
      this.orderProductQuantityModal?.productId &&
      this.orderProductQuantityModal?.isDisplayed
    )
  }

  get min() {
    return 1
  }

  get max() {
    return 99
  }

  get productDetails() {
    const productId = this.orderProductQuantityModal?.productId
    return this.order.products.find(product => product.id === productId)
  }

  get productName() {
    return this.productDetails?.name
  }

  get quantity() {
    return this.productDetails?.quantity || 0
  }

  get isDisplayed() {
    return this.orderProductQuantityModal.isDisplayed
  }

  set isDisplayed(value: boolean) {
    if (this.isDisplayed && value === false) {
      this.closeOrderProductQuantityModal()
    }
  }

  @Watch('quantity')
  handleValue() {
    this.newQuantity = this.quantity
  }

  onDecreased() {
    this.newQuantity--
  }

  onIncrease() {
    this.newQuantity++
  }

  showUndoBanner() {
    this.$toast.show({
      message: this.$t('product.edited.quantity').toString(),
      actionLabel: this.$t('common.undo').toString(),
      onActionClick: () => this.restoreEditProductQuantity()
    })
  }

  async onSave() {
    if (this.productDetails) {
      await this.editProduct({
        orderId: this.order.id,
        storeAddressId: this.order.storeDetails.storeAddressId,
        product: this.productDetails,
        quantity: this.newQuantity
      })
      this.showUndoBanner()
    }
    this.closeOrderProductQuantityModal()
  }

  closeModal() {
    this.closeOrderProductQuantityModal()
  }
}
