import { State } from './state'
import { MutationTree } from 'vuex'
import {
  ADD_CANCELLED_ORDERS,
  AddCancelledOrdersPayload,
  ADD_PENDING_ORDERS,
  AddPendingOrdersPayload,
  ADD_ACCEPTED_ORDERS,
  AddActiveOrdersPayload,
  ADD_SERVED_ORDERS,
  AddServedOrdersPayload,
  GET_ORDER_DETAILS_SUCCESS,
  GetOrderDetailsSuccessPayload,
  UPDATE_ORDER_DETAILS_LOADING,
  UpdateOrderDetailsLoadingPayload,
  UPDATE_ORDER_DETAILS_FAILURE,
  UpdateOrderDetailsFailurePayload,
  SET_ORDER_DETAILS_STORE_TRACKING_CODE,
  SetStoreTrackingCodePayload,
  SET_ORDER_DETAILS_PRODUCTS,
  SetProductsPayload,
  SET_ORDER_DETAILS_DESCRIPTION,
  SetDescriptionPayload,
  MARK_ORDER_AS_READY,
  MarkOrderAsReadyPayload,
  DISPATCH_ORDER_SUCCESS,
  DispatchOrderSuccessPayload,
  SET_ORDER_DETAILS_AS_ACCEPTED,
  SET_IS_GROCERIES_CHECKBOX_CHECKED,
  SetIsGroceriesCheckboxCheckedPayload,
  SET_ORDER_DETAILS_TOTAL_NUMBER_OF_PRODUCTS,
  SetOrderDetailsTotalNumberOfProductsPayload,
  UPDATE_ORDER_PRODUCT_EDIT_MODAL,
  UPDATE_ORDER_PRODUCT_QUANTITY_MODAL,
  UPDATE_ORDER_PRODUCT_ATTRIBUTES_MODAL,
  UpdateOrderProductEditModalPayload,
  UpdateOrderProductQuantityModalPayload,
  UpdateOrderProductAttributesModalPayload,
  ADD_TEMPORARY_EDIT_PRODUCT_QUANTITY,
  AddTemporaryEditProductQuantityPayload,
  CLEAR_TEMPORARY_EDIT_PRODUCT_QUANTITY
} from './action-types'
import { PreparationStage } from '@/modules/orders/constants'
import isEqual from 'lodash/isEqual'
import { ProductDetails } from '@/modules/store/data/resources/order-details'

export const mutations: MutationTree<State> = {
  [ADD_ACCEPTED_ORDERS]({ dataState }: State, payload: AddActiveOrdersPayload) {
    if (payload) {
      const idList = payload.map(({ id }) => id)
      if (isEqual(dataState.acceptedOrders, idList)) return
      dataState.acceptedOrders = idList
    }
  },
  [ADD_PENDING_ORDERS]({ dataState }: State, payload: AddPendingOrdersPayload) {
    if (payload) {
      const idList = payload.map(({ id }) => id)
      if (isEqual(dataState.pendingAcceptanceOrders, idList)) return
      dataState.pendingAcceptanceOrders = idList
    }
  },
  [ADD_CANCELLED_ORDERS](
    { dataState }: State,
    payload: AddCancelledOrdersPayload
  ) {
    if (payload) {
      const idList = payload.map(({ id }) => id)
      if (isEqual(dataState.cancelledOrders, idList)) return
      dataState.cancelledOrders = idList
    }
  },
  [ADD_SERVED_ORDERS]({ dataState }: State, payload: AddServedOrdersPayload) {
    if (payload) {
      const idList = payload.map(({ id }) => id)
      if (isEqual(dataState.servedOrders, idList)) return
      dataState.servedOrders = idList
    }
  },
  [DISPATCH_ORDER_SUCCESS](
    { dataState }: State,
    payload: DispatchOrderSuccessPayload
  ) {
    dataState.acceptedOrders = dataState.acceptedOrders.filter(
      orderId => orderId !== payload.id
    )
    dataState.servedOrders = [payload.id, ...dataState.servedOrders]
  },
  [GET_ORDER_DETAILS_SUCCESS](
    { dataState }: State,
    details: GetOrderDetailsSuccessPayload
  ) {
    dataState.orderDetails = details
  },
  [UPDATE_ORDER_DETAILS_LOADING](
    { uiState }: State,
    isLoading: UpdateOrderDetailsLoadingPayload
  ) {
    uiState.isLoadingOrderDetails = isLoading
  },
  [UPDATE_ORDER_DETAILS_FAILURE](
    { uiState }: State,
    hasLoadingFailed: UpdateOrderDetailsFailurePayload
  ) {
    uiState.hasLoadingOrderDetailsFailed = hasLoadingFailed
  },
  [SET_ORDER_DETAILS_STORE_TRACKING_CODE](
    { dataState }: State,
    storeTrackingCode: SetStoreTrackingCodePayload
  ) {
    dataState.orderDetails!.orderInformation.storeTrackingCode = storeTrackingCode
  },
  [SET_ORDER_DETAILS_PRODUCTS](
    { dataState }: State,
    products: SetProductsPayload
  ) {
    dataState.orderDetails!.products = products
  },
  [SET_ORDER_DETAILS_TOTAL_NUMBER_OF_PRODUCTS](
    { dataState }: State,
    products: SetOrderDetailsTotalNumberOfProductsPayload
  ) {
    const sum = products.reduce((acc: number, product: ProductDetails) => {
      return acc + product.quantity
    }, 0)
    dataState.orderDetails!.orderInformation.totalNumberOfProducts = sum
  },
  [SET_ORDER_DETAILS_DESCRIPTION](
    { dataState }: State,
    description: SetDescriptionPayload
  ) {
    dataState.orderDetails!.orderInformation.description = description
  },
  [SET_ORDER_DETAILS_AS_ACCEPTED]({ dataState }: State) {
    dataState.orderDetails!.statusDetails.preparationStage =
      PreparationStage.ACCEPTED_BY_PARTNER
  },
  [MARK_ORDER_AS_READY](
    { dataState: { orderDetails } }: State,
    { storeAddressId, orderId }: MarkOrderAsReadyPayload
  ) {
    if (orderDetails) {
      const isTheSameOrder =
        orderDetails.id === orderId &&
        orderDetails.storeDetails.storeAddressId === storeAddressId
      if (isTheSameOrder) {
        orderDetails.statusDetails.isReady = true
      }
    }
  },
  [SET_IS_GROCERIES_CHECKBOX_CHECKED](
    { dataState }: State,
    { id, isChecked }: SetIsGroceriesCheckboxCheckedPayload
  ) {
    dataState.checkedGroceriesCheckboxesById[id] = isChecked
  },
  [UPDATE_ORDER_PRODUCT_EDIT_MODAL](
    { uiState }: State,
    { isDisplayed, productId }: UpdateOrderProductEditModalPayload
  ) {
    uiState.orderProductEditModal = { isDisplayed, productId }
  },
  [UPDATE_ORDER_PRODUCT_QUANTITY_MODAL](
    { uiState }: State,
    { isDisplayed, productId }: UpdateOrderProductQuantityModalPayload
  ) {
    uiState.orderProductQuantityModal = {
      isDisplayed,
      productId
    }
  },
  [UPDATE_ORDER_PRODUCT_ATTRIBUTES_MODAL](
    { uiState }: State,
    { isDisplayed, productId }: UpdateOrderProductAttributesModalPayload
  ) {
    uiState.orderProductAttributesModal = {
      isDisplayed,
      productId
    }
  },
  [ADD_TEMPORARY_EDIT_PRODUCT_QUANTITY](
    { dataState }: State,
    payload: AddTemporaryEditProductQuantityPayload
  ) {
    dataState.temporaryEditProductQuantity = { ...payload }
  },
  [CLEAR_TEMPORARY_EDIT_PRODUCT_QUANTITY]({ dataState }: State) {
    dataState.temporaryEditProductQuantity = null
  }
}
