import { Vue as _Vue } from 'vue/types/vue'
import { GlvModule, GlvModuleOptions } from 'glovo'
import { installVuexStore } from './plugins'
import { vuexStoreFactory } from './services'
import { Store } from 'vuex'
import { State } from './state'
import { DataGetters } from './data'
import { AbstractFetchService } from '@/modules/http'

interface StoreModuleOptions extends GlvModuleOptions {
  deps: {
    fetchService: AbstractFetchService
  }
}
class StoreModule implements GlvModule<StoreModuleOptions> {
  store!: Store<State>
  install(Vue: typeof _Vue, options?: StoreModuleOptions) {
    installVuexStore(Vue)
    const { fetchService } = options!.deps
    this.store = vuexStoreFactory(fetchService)
  }
}
const glvModule = new StoreModule()

// tslint:disable-next-line: no-empty-interface
export type Getters = DataGetters

export default glvModule
export { State }
export {
  HistoryOrder,
  OrderStatus,
  Product,
  OrderId,
  GET_STORE_ADMINS_GETTER
} from './data'
