import { State as RootState } from '@/modules/store'
import { Store, ActionPayload, ActionSubscribersObject } from 'vuex'
import {
  PAUSE_LIVE_ORDERS,
  ACCEPT_ORDER,
  RESUME_LIVE_ORDERS,
  MARK_ORDER_AS_READY,
  START_LIVE_ORDERS,
  STOP_LIVE_ORDERS
} from '../../store'
import {
  MARK_CANCELLED_ORDER_AS_ACKNOWLEDGED,
  MARK_COURIER_NEARBY_AS_NOTIFIED
} from '@/modules/store/data/action-types'
import {
  LOG_OUT,
  RESTORE_SELECTED_STORE_ADDRESS_IDS
} from '@/modules/authentication'

export function liveOrdersServiceHandler(
  moduleName: string,
  store: Store<RootState>
): ActionSubscribersObject<ActionPayload, RootState> {
  return {
    before: action => {
      switch (action.type) {
        case MARK_ORDER_AS_READY:
        case MARK_CANCELLED_ORDER_AS_ACKNOWLEDGED: {
          store.dispatch(`${moduleName}/${PAUSE_LIVE_ORDERS}`)
          break
        }
        case `orderManagement/${ACCEPT_ORDER}`: {
          store.dispatch(`${moduleName}/${PAUSE_LIVE_ORDERS}`)
          store.dispatch(
            MARK_COURIER_NEARBY_AS_NOTIFIED,
            action.payload.orderId
          )
          break
        }
      }
    },
    after: action => {
      switch (action.type) {
        case MARK_ORDER_AS_READY:
        case MARK_CANCELLED_ORDER_AS_ACKNOWLEDGED:
        case `orderManagement/${ACCEPT_ORDER}`: {
          store.dispatch(`${moduleName}/${RESUME_LIVE_ORDERS}`)
          break
        }
        case `authentication/${RESTORE_SELECTED_STORE_ADDRESS_IDS}`: {
          store.dispatch(`${moduleName}/${START_LIVE_ORDERS}`)
          break
        }
        case `authentication/${LOG_OUT}`: {
          store.dispatch(`${moduleName}/${STOP_LIVE_ORDERS}`)
          break
        }
        default: {
          break
        }
      }
    }
  }
}
