import { FIVE_SECONDS_IN_MILLIS } from '@/modules/core/helpers'
import { Observable, concat, timer, defer } from 'rxjs'
import {
  switchMap,
  repeat,
  tap,
  retryWhen,
  map,
  catchError
} from 'rxjs/operators'
import * as uuid from 'uuid'

export interface PollingOptions {
  interval: number
  attempts: number
}

export class PollingService {
  interval: number
  attempts: number

  private numberOfFailures = 0

  constructor(
    options: PollingOptions = {
      interval: FIVE_SECONDS_IN_MILLIS,
      attempts: 0
    }
  ) {
    this.interval = options.interval
    this.attempts = options.attempts
  }

  createPolling<T, E>(
    requestFn: () => Observable<T> | Promise<T>,
    beforeFn: (correlationId: string) => void = () => {},
    afterFn: (correlationId: string, pollingResult: T) => void = () => {},
    errorFn: (correlationId: string, error: E) => void = () => {}
  ): Observable<T> {
    const polling$ = timer(this.interval).pipe(
      map(() => {
        return uuid.v4()
      }),
      tap(correlationId => {
        beforeFn(correlationId)
      }),
      switchMap(correlationId =>
        defer(() => requestFn()).pipe(
          tap((pollingResult: T) => {
            afterFn(correlationId, pollingResult)
          }),
          catchError(error => {
            errorFn(correlationId, error)
            throw error
          })
        )
      ),
      tap(() => {
        this.numberOfFailures = 0
      }),
      repeat()
    )
    const firstRequest$ = defer(() => requestFn())
    return concat(firstRequest$, polling$).pipe(
      retryWhen(errors$ =>
        errors$.pipe(
          switchMap(error => {
            ++this.numberOfFailures
            if (this.attempts < this.numberOfFailures) {
              throw error
            }
            const delay = this.interval
            return timer(delay)
          })
        )
      )
    )
  }
}
