import { GetterTree } from 'vuex'
import { State } from './state'
import { Getters as RootGetters, State as RootState } from '@/modules/store'

import { GET_ORDERS_GETTER } from '@/modules/store/data/getters'
import { StoreAddressId } from '@/modules/store/data/resources'
import {
  AcceptedOrder,
  CancelledOrder,
  PendingOrder,
  ServedOrder
} from '@/modules/store/data/resources/orders-list/order-types'
import { BoughtProductId } from '@/modules/store/data/resources/order-details'
import { EditProductQuantity } from '@/modules/store/data/resources/edit-product-quantity'

export const GET_ACCEPTED_ORDERS_GETTER = 'GET_ACCEPTED_ORDERS_GETTER'
export const GET_PENDING_ORDERS_GETTER = 'GET_PENDING_ORDERS_GETTER'
export const GET_CANCELLED_ORDERS_GETTER = 'GET_CANCELLED_ORDERS_GETTER'
export const GET_SERVED_ORDERS_GETTER = 'GET_SERVED_ORDERS_GETTER'

export const HAS_PENDING_ORDERS_GETTER = 'HAS_PENDING_ORDERS_GETTER'

export const GET_IS_GROCERIES_STORE_ADDRESS_GETTER =
  'GET_IS_GROCERIES_STORE_ADDRESS_GETTER'
export const GET_IS_KITCHEN_CODE_STORE_ADDRESS_GETTER =
  'GET_IS_KITCHEN_CODE_STORE_ADDRESS_GETTER'
export const GET_ORDER_DETAILS_GETTER = 'GET_ORDER_DETAILS_GETTER'
export const GET_IS_ORDER_DETAILS_LOADING_GETTER =
  'GET_IS_ORDER_DETAILS_LOADING_GETTER'
export const GET_IS_ORDER_DETAILS_LOADING_FAILURE_GETTER =
  'GET_IS_ORDER_DETAILS_LOADING_FAILURE_GETTER'
export const GET_ORDER_PRODUCT_EDIT_MODAL_GETTER =
  'GET_ORDER_PRODUCT_EDIT_MODAL_GETTER'
export const GET_ORDER_PRODUCT_QUANTITY_MODAL_GETTER =
  'GET_ORDER_PRODUCT_QUANTITY_MODAL_GETTER'
export const GET_ORDER_PRODUCT_ATTRIBUTES_MODAL_GETTER =
  'GET_ORDER_PRODUCT_ATTRIBUTES_MODAL_GETTER'
export const GET_IS_GROCERIES_PRODUCT_CHECKED =
  'GET_IS_GROCERIES_PRODUCT_CHECKED'
export const TEMPORARY_EDIT_PRODUCT_QUANTITY_GETTER =
  'TEMPORARY_EDIT_PRODUCT_QUANTITY_GETTER'

export const getters: GetterTree<State, RootState> = {
  [GET_ACCEPTED_ORDERS_GETTER]: (
    { dataState }: State,
    _: Getters,
    __: RootState,
    rootGetters: RootGetters
  ): AcceptedOrder[] => {
    const orderIds = dataState.acceptedOrders
    const allOrders = rootGetters[GET_ORDERS_GETTER]
    return orderIds
      .map(id => allOrders[id])
      .filter(order => !!order) as AcceptedOrder[]
  },
  [GET_CANCELLED_ORDERS_GETTER]: (
    { dataState }: State,
    _: Getters,
    __: RootState,
    rootGetters: RootGetters
  ): CancelledOrder[] => {
    const orderIds = dataState.cancelledOrders
    const allOrders = rootGetters[GET_ORDERS_GETTER]
    return orderIds
      .map(id => allOrders[id])
      .filter(order => !!order) as CancelledOrder[]
  },
  [GET_PENDING_ORDERS_GETTER]: (
    { dataState }: State,
    _: Getters,
    __: RootState,
    rootGetters: RootGetters
  ): PendingOrder[] => {
    const orderIds = dataState.pendingAcceptanceOrders
    const allOrders = rootGetters[GET_ORDERS_GETTER]
    return orderIds
      .map(id => allOrders[id])
      .filter(order => !!order) as PendingOrder[]
  },
  [GET_SERVED_ORDERS_GETTER]: (
    { dataState }: State,
    _: Getters,
    __: RootState,
    rootGetters: RootGetters
  ): ServedOrder[] => {
    const orderIds = dataState.servedOrders
    const allOrders = rootGetters[GET_ORDERS_GETTER]
    return orderIds
      .map(id => allOrders[id])
      .filter(order => !!order) as ServedOrder[]
  },
  [HAS_PENDING_ORDERS_GETTER]: (
    state: State,
    localGetters: Getters
  ): boolean => {
    return localGetters[GET_PENDING_ORDERS_GETTER].length > 0
  },
  [GET_IS_GROCERIES_STORE_ADDRESS_GETTER]: (
    _: State,
    __: Getters,
    { data: { stores, storeAddresses } }: RootState
  ) => (id: StoreAddressId): boolean => {
    return Boolean(stores[storeAddresses[id].parentStoreId].isGroceriesStore)
  },
  [GET_IS_GROCERIES_PRODUCT_CHECKED]: ({
    dataState: { checkedGroceriesCheckboxesById }
  }: State) => (id: BoughtProductId): boolean => {
    return Boolean(checkedGroceriesCheckboxesById[id])
  },
  [GET_IS_KITCHEN_CODE_STORE_ADDRESS_GETTER]: (
    _: State,
    __: Getters,
    { data: { storeAddresses } }: RootState
  ) => (id: StoreAddressId): boolean => {
    if (storeAddresses[id]) {
      return storeAddresses[id].useKitchenCode
    }
    return false
  },
  [GET_ORDER_DETAILS_GETTER]: ({ dataState }: State) => {
    return dataState.orderDetails
  },
  [GET_IS_ORDER_DETAILS_LOADING_GETTER]: ({ uiState }: State) => {
    return uiState.isLoadingOrderDetails
  },
  [GET_IS_ORDER_DETAILS_LOADING_FAILURE_GETTER]: ({ uiState }: State) => {
    return uiState.hasLoadingOrderDetailsFailed
  },
  [GET_ORDER_PRODUCT_EDIT_MODAL_GETTER]: ({ uiState }: State) => {
    return uiState.orderProductEditModal
  },
  [GET_ORDER_PRODUCT_QUANTITY_MODAL_GETTER]: ({ uiState }: State) => {
    return uiState.orderProductQuantityModal
  },
  [GET_ORDER_PRODUCT_ATTRIBUTES_MODAL_GETTER]: ({ uiState }: State) => {
    return uiState.orderProductAttributesModal
  },
  [TEMPORARY_EDIT_PRODUCT_QUANTITY_GETTER]: ({ dataState }: State) => {
    return dataState.temporaryEditProductQuantity
  }
}

export interface Getters {
  [GET_PENDING_ORDERS_GETTER]: PendingOrder[]
  [TEMPORARY_EDIT_PRODUCT_QUANTITY_GETTER]: EditProductQuantity | null
}
