
import Component from 'vue-class-component'
import Vue from 'vue'
import { Prop } from 'vue-property-decorator'
import { DetailsListItem } from './types'

@Component({ name: 'DetailsList' })
export default class DetailsList extends Vue {
  @Prop({ default: false }) bulleted!: boolean
  @Prop({ default: false }) bold!: boolean
  @Prop({ required: true }) items!: DetailsListItem[]

  get itemsLength() {
    return (this.items || []).length
  }

  get hasItems() {
    return this.itemsLength > 0
  }

  itemIsString(item: DetailsListItem): item is string {
    return typeof item === 'string'
  }

  itemHasMessage(item: DetailsListItem): boolean {
    return !this.itemIsString(item) && !!item.message
  }

  parseItemTitle(item: DetailsListItem): string {
    return this.itemIsString(item) ? item : item?.title ?? ''
  }
}
