import { Vue as _Vue } from 'vue/types/vue'
import { ErrorMessageConfig, Message } from '@/modules/core/plugins/message'
import { FIVE_SECONDS_IN_MILLIS } from '../helpers'

const defaultDuration = FIVE_SECONDS_IN_MILLIS

export default function install(Vue: typeof _Vue) {
  Vue.mixin({
    methods: {
      $errorMessage(message: Message, config: ErrorMessageConfig = {}) {
        message = message ? message : this.$t('generic.error.wentwrong')
        return this.$message.error({
          message,
          duration: defaultDuration,
          customClass: 'errorMessage',
          ...config
        })
      }
    }
  })
}
